import Controller from './space.controller';
import Template from './space.template.html';
import Messages from '../../messages';

let SpaceComponent = {
    bindings: {
        asset: '=',
        listMode: '=',
        space: '='
    },
    controller: Controller,
    template: Template,
    Messages: Messages
};

export default SpaceComponent;


