export default class Space {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
     return $resource(ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/floors/:floorId/spaces/:id', 
                                                            { assetId: '@assetId', assetObjectId: '@assetObjectId', 'floorId': '@floorId', id: '@id' }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/floors/:floorId/spaces',
            isArray: true
        },
        create: {
            method: 'POST',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/floors/:floorId/spaces',
        },
        update: {
            method: 'PUT'
        },
        delete: {
            method: 'DELETE'
        },
        getFull: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/floors/:floorId/spaces/:id/full',
        },
        getByAssetId: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/spaces',
            isArray: true
        },
        getByAssetObjectId: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/spaces',
            isArray: true
        }
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    Space.instance = new Space(ConfigurationService, $resource);
    return Space.instance;
  }
}


