export default class SecurityService {
  /*@ngInject*/
  constructor($http, $q, $state, NotificationService, ConfigurationService, localStorageService, ActivityMonitor, Role) {
    this.$http = $http;
    this.$q = $q;
    this.$state = $state;
    this.notificationService = NotificationService;
    this.configurationService = ConfigurationService;
    this.localStorageService = localStorageService;
    this.ActivityMonitor = ActivityMonitor;
    this.Role = Role;

    this.authentication = new Authentication();
    this.activities = null;
    this.userInfo = null;
  }

  fillAuthData() {
    let authData = this.localStorageService.get('authenticationData');
    if (authData) {
      this.authentication.set(authData.userName);
    }
  }

  signOut() {
    this.localStorageService.remove('authenticationData');
    this.authentication.clear();
    this.reset();

    this.ActivityMonitor.off('inactive');
  }

  signIn(userName, password) {
    let self = this;

    let data = "grant_type=password&username=" + userName + "&password=" + password + "&client_id=" + this.configurationService.getClientId();

    let deferred = this.$q.defer();

    this.$http.post(
      this.configurationService.getApiBaseUrl() + 'api/token',
      data,
      {
        headers:
        {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((response) => {

        this.localStorageService.set('authenticationData', {
          access_token: response.data.access_token,
          userName: userName
        });

        this.authentication.set(userName);

        self.ActivityMonitor.on('inactive', () => {
          self.signOut();
          self.$state.go('login', { automatic: true }, { reload: true });
        });

        deferred.resolve(response);

      }, (err, status) => {
        this.signOut();
        deferred.reject(err, status);
      });

    return deferred.promise;
  }

  canDo(activityId, failedState, notLoggedInState) {
    let deferred = this.$q.defer();

    this.getActivities().then((data) => {
      if (data.indexOf(activityId.toUpperCase()) >= 0) {
        deferred.resolve(true);
      }
      else {
        if (failedState && notLoggedInState) {
          if (data.length > 0) { // means authenticated
            this.$state.go(failedState);
          } else {
            this.$state.go(notLoggedInState);
          }
        } else {
          deferred.reject('Forbidden');
        }
      }
    });

    return deferred.promise;
  }

  getActivities() {
    let deferred = this.$q.defer();
    let promise = deferred.promise;

    if (this.activities != null) {
      deferred.resolve(this.activities);
    }
    else {
      this.$http({
        method: 'GET', url: this.configurationService.getApiBaseUrl() + ('api/account/activities')
        // headers: {
        //   'Authorization': 'Bearer ' + this.getToken()
        // }
      }).then(
        (result) => {
          this.activities = result.data;
          deferred.resolve(this.activities);
        });
    }

    return promise;
  }

  reset() {
    this.activities = null;
    this.userInfo = null;
  }

  getToken() {
    let authData = this.localStorageService.get('authenticationData');
    if (authData == null) {
      return null;
    }
    else {
      return authData.access_token;
    }
  }

  getUserInfo() {
    let self = this;

    let deferred = self.$q.defer();
    let promise = deferred.promise;

    if (self.userInfo !== null) {
      deferred.resolve(self.userInfo);
    }
    else {
      self.$http.get(self.configurationService.getApiBaseUrl() + 'api/account/userinfo').then(
        (result) => {
          self.Role.get(
            { id: result.data.roleId},
            (role) => {
              self.userInfo = new UserInfo(
                result.data.id,
                result.data.email,
                result.data.displayName,
                result.data.roleId,
                role.name,
                result.data.brokerId,
                result.data.state,
                result.data.localeId);
              deferred.resolve(self.userInfo);
          })
        });
    }

    return promise;
  }
}


class Authentication {
  constructor() {
    this.authenticated = false;
    this.username = null;
  }

  set(userName) {
    this.authenticated = true;
    this.username = userName;
  }

  clear() {
    this.authenticated = false;
    this.username = null;
  }
}

class UserInfo {
  constructor(id, email, displayName, roleId, roleName, brokerId, state, localeId) {
    this.id = id;
    this.email = email;
    this.displayName = displayName;
    this.roleId = roleId;
    this.roleName = roleName;
    this.brokerId = brokerId;
    this.state = state;
    this.localeId = localeId;
  }
}


