export default class UtilsService {
    constructor() {
    }

    generateGuid() {
        function s4() {
            return Math.floor((1 + Math.random()) * 0x10000)
                       .toString(16)
                       .substring(1);
        }

        return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
    }

    generateAddress(data) {
        let city = data.city,
            street = data.street,
            building = data.streetAddress;
        return ((city && `${city}, `) + (street && `${street}, `) + (building && `${building}`)) || '';
    }

    alignStatusLabelsLength(value) {
        let integerPart = Math.floor(value);

        if (integerPart < 10) {
            return value.toFixed(2);
        } else if (integerPart < 100) {
            return value.toFixed(1);
        } else {
            return Math.round(value);
        }
    }

    formatBadgeValue(value){
        if (value > 0 && value < 1000) {
            value = this.alignStatusLabelsLength(value);
        } else if (value >= 1000 && value < 1000000) {
            value = this.alignStatusLabelsLength(value / 1000) + 'K';
        } else if (value >= 1000000 && value < 1000000000) {
            value = this.alignStatusLabelsLength(value / 1000000) + 'M';
        } else if (value >= 1000000000) {
            value = this.alignStatusLabelsLength(value / 1000000000) + 'B';
        }
        return value;
    }
}


