import MainHeaderComponent from './mainheader/mainheader.component';
import Sidebar from './sidebar/sidebar.component';
import RightSidebar from './right-sidebar/right-sidebar.component';
import NavigationCreateItemsComponent from './navigation/navigation-create-items.component';
import ImageGalleryComponent from './imagegallery/imagegallery.component';
import ImageCropperComponent from './imagegallery/imagecropper.component';
import AttachementsComponent from './attachements/attachements.component';
import 'ng-file-upload';
import BreadcrumbPanelComponent from './breadcrumbpanel/breadcrumbpanel.component'
import SpaceDescription from './space-description/space-description.component';
import TenantDescription from './tenant-description/tenant-description.component';
import BodyBlocker from './body-blocker/body-blocker.component';
import UserMenu from './usermenu/usermenu.component';
import ActionMenu from './actionmenu/actionmenu.component';
import Footer from './footer/footer.component';

var module = angular.module('tower360.components', ['ngFileUpload'])
    .component('mainheader', MainHeaderComponent)
    .component('sidebar', Sidebar)
    .component('rightSidebar', RightSidebar)
    .component('navigationCreateItems', NavigationCreateItemsComponent)
    .component('imagegallery', ImageGalleryComponent)
    .component('imagecropper', ImageCropperComponent)
    .component('attachements', AttachementsComponent)
    .component('breadcrumbPanel', BreadcrumbPanelComponent)
    .component('spaceDescription', SpaceDescription)
    .component('tenantDescription', TenantDescription)
    .component('bodyBlocker', BodyBlocker)
    .component('userMenu', UserMenu)
    .component('actionMenu', ActionMenu)
    .component('footer', Footer);

export default module;



