import ValidationEngine from 'devextreme/ui/validation_engine';
import Messages from '../messages';
import Constants from '../constants';

export default class ClientEditController {
    /*@ngInject*/
    constructor($scope, $state, $timeout, Client, clientId, NotificationService, $uibModalInstance, UtilsService) {
        this.$scope = $scope;
        this.$state = $state;
        this.$timeout = $timeout;
        this.Client = Client;
        this.clientId = clientId;
        this.notificationService = NotificationService;
        this.$uibModalInstance = $uibModalInstance;
        this.utils = UtilsService;
        this.validationGroup = 'client-edit';
        this.addressValidationGroup = 'client-edit-address';

        this.init();
        this.initForm();
    }

    init() {
        let self = this;

        if (this.clientId) {
            this.$scope.getDataInProgress = true;
            this.clientObject = {};
            this.Client.getFull(
                {id: this.clientId},
                result => {
                    this.$scope.client = result;
                    this.$scope.getDataInProgress = false;
                    this.$timeout(() => {
                        this.initAutocomplete();
                    });
                },
                error => {
                    this.notificationService.error(error.data.message ? error.data.message : error.data);
                }
            );
        } else {
            this.$timeout(() => {
                this.initAutocomplete();
            });
        }

        this.$scope.clearAddress = function(){
            self.$scope.client.address  = null;
        }
    }

    initForm() {
        let self = this;

        this.$scope.editMode = !!this.clientObject;

        if (this.$scope.editMode) {
            this.$scope.client = self.clientObject;
        } else {
            this.$scope.client = {
                name: '',
                countryId: 1,
                city: '',
                street: '',
                streetAddress: '',
                postCode: '',
                notes: '',
                brokerId: null,
                representatives: []
            };
        }

        this.$scope.nameSettings = {
            placeholder: Messages('clients.placeholder.name'),
            bindingOptions: {
                value: 'client.name'
            }
        };
        this.$scope.nameValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'stringLength', max: 255 },
            ],
            validationGroup: this.validationGroup
        };

        this.countryStore = Constants.countryStore();
        this.$scope.countrySettings = {
            dataSource: this.countryStore,
            displayExpr: 'display',
            valueExpr: 'value',
            bindingOptions: {
                value: 'client.countryId'
            }
        };

        this.$scope.countryValidationRules = {
            validationRules: [],
            validationGroup: this.validationGroup,
            onOptionChanged: function() {
                self.setAutocompleteCountry();
            }
        };

        this.$scope.client.address = '';
        this.$scope.addressSettings = {
            bindingOptions: {
                value: 'client.address',
                disabled: '!client.countryId'
            }
        };
        this.$scope.addressValidationRules = {
            validationRules: [{
                type: 'custom',
                validationCallback: function(options) {
                    if (!self.$scope.client.countryId) {
                        options.rule.message = Messages('address.validation.countryRequired');
                        return false;
                    }

                    return true;
                }
            }],
            validationGroup: this.addressValidationGroup
        };

        this.$scope.citySettings = {
            placeholder: Messages('address.placeholder.city'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'client.city'
            }
        };
        this.$scope.cityValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 80 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.streetSettings = {
            placeholder: Messages('address.placeholder.street'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'client.street'
            }
        };
        this.$scope.streetValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 100 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.streetAddressSettings = {
            placeholder: Messages('address.placeholder.streetAddress'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'client.streetAddress'
            }
        };
        this.$scope.streetAddressValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 10 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.postCodeSettings = {
            placeholder: Messages('address.placeholder.postCode'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'client.postCode'
            }
        };
        this.$scope.postCodeValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 10 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.notesSettings = {
            placeholder: Messages('clients.placeholder.notes'),
            height: 120,
            maxLength: 500,
            bindingOptions: {
                value: 'client.notes'
            }
        };
        this.$scope.notesValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 500 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.shortNameSettings = {
            placeholder: Messages('clients.placeholder.shortName'),
            bindingOptions: {
                value: 'client.shortName'
            }
        };
        this.$scope.shortNameValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 100 },
            ],
            validationGroup: this.validationGroup
        };

        this.industryStore = Constants.clientsIndustryStore();
        this.$scope.industrySettings = {
            dataSource: this.industryStore,
            displayExpr: 'display',
            valueExpr: 'value',
            bindingOptions: {
                value: 'client.industryId'
            }
        };

        this.$scope.industryValidationRules = {
            validationRules: [],
            validationGroup: this.validationGroup,
        };

        this.$scope.validateAndSave = () => {
            let validation = ValidationEngine.getGroupConfig(this.validationGroup).validate(),
                addressValidation = ValidationEngine.getGroupConfig(self.addressValidationGroup).validate();

            if (validation.isValid && addressValidation.isValid) {
                this.saveInProgress = true;
                this.isSaveOperationDisable = true;

                if (!self.$scope.editMode) {
                    self.Client.save(this.$scope.client,
                        (result) => {
                            self.saveInProgress = false;
                            this.$uibModalInstance.close(this.$scope.client);
                            this.$state.go('client', {id : result.id});
                            this.notificationService.success(Messages('clients.message.created'));
                        },
                        (error) => {
                            self.notificationService.error(error.data);
                            self.saveInProgress = false;
                        }
                    );
                } else {
                    self.Client.update({ id: this.$scope.client.id }, this.$scope.client,
                        (result) => {
                            self.notificationService.success(Messages('clients.message.updated'));
                            self.saveInProgress = false;
                            if (self.$scope.client.industryId != null){
                                Constants.clientsIndustryStore().byKey(self.$scope.client.industryId).done(function(dataItem) {
                                        self.$scope.client.industry = dataItem.display;
                                })
                            }

                            this.$uibModalInstance.close(this.$scope.client);
                        },
                        (error) => {
                            self.notificationService.error(error.data);
                            self.saveInProgress = false;
                        }
                    );
                }
            }
        };

        this.$scope.cancel = () => {
            ValidationEngine.removeGroup(this.validationGroup);
            this.$uibModalInstance.dismiss('cancel');
        };
    }

    initAutocomplete() {
        if (!window.google) {
            return;
        }

        this.autocomplete = new google.maps.places.Autocomplete($('#client-address-field input')[0]);
        google.maps.event.addListener(this.autocomplete, 'place_changed', () => {
            let place = this.autocomplete.getPlace(),
                citySet, streetSet, buildingSet, postCodeSet;

            if (place.address_components instanceof Array) {
                place.address_components.forEach((item) => {
                    if (item.types.indexOf('locality') !== -1) {
                        this.$scope.client.city = item.long_name;
                        citySet = true;
                    }
                    if (item.types.indexOf('route') !== -1) {
                        this.$scope.client.street = item.long_name;
                        streetSet = true;
                    }
                    if (item.types.indexOf('street_number') !== -1) {
                        this.$scope.client.streetAddress = item.long_name;
                        buildingSet = true;
                    }
                    if (item.types.indexOf('postal_code') !== -1) {
                        this.$scope.client.postCode = item.long_name;
                        postCodeSet = true;
                    }
                });
            }

            if (!citySet) {
                this.$scope.client.city = null;
            }
            if (!streetSet) {
                this.$scope.client.street = null;
            }
            if (!buildingSet) {
                this.$scope.client.streetAddress = null;
            }
            if (!postCodeSet) {
                this.$scope.client.postCode = null;
            }

            ValidationEngine.getGroupConfig(this.addressValidationGroup).validate();
        });

        if (this.$scope.client.countryId) {
            this.setAutocompleteCountry();
        }
    }

    setAutocompleteCountry() {
        let countries = Constants.countryStorePlain(),
            curCountryCode;

        countries.forEach((country) => {
            if (country.value === this.$scope.client.countryId) {
                curCountryCode = country.isoCode;
            }
        });

        this.autocomplete.setComponentRestrictions({
            'country': curCountryCode
        });
    }

    
}


