import { LocaleId, updateLocale } from '../../app.localization';
import Messages from '../../messages';

export default class MainHeaderController {
    /*@ngInject*/
    constructor($scope, $state, User, Broker, Locale, SecurityService, Activities) {
        this.$scope = $scope;
        this.$state = $state;
        this.securityService = SecurityService;
        this.activities = Activities;
        this.Locale = Locale;
        this.User = User;
        this.Broker = Broker;
        this.messages = Messages;

        this.loadUserInfo();
        this.applyActivities();
        this.init();
    }

    init() {
        var self = this;

        self.Locale.query(
            {},
            (result) => {
                self.$scope.languages = result;
            });
    }

    setLanguage(item) {
        let self = this;

        var currentLocaleId = LocaleId;
        if (currentLocaleId != item.id) {
            self.updateUserLocale(this.userInfo.id, item.id);
        }
    }

    minimalize () {
        if ($(window).outerWidth() < 769) {
            $("body").toggleClass("show-sidebar");
        } 
        else {
            $("body").toggleClass("hide-sidebar");
        }
    } 

    signOut() {
        this.securityService.signOut();
        this.$state.go('login', null, { reload: true });
    }

    loadUserInfo() {
        let self = this;

        this.securityService.getUserInfo().then(result => {
            self.userInfo = result;
            if (result.roleId === 2) {
                this.loadBrokerInfo();
            }
        })
    }

    loadBrokerInfo() {
        this.Broker.get({ id: this.userInfo.brokerId }).$promise.then(result => {
            this.brokerInfo = result;
        });
    }

    updateUserLocale(userId, localeId) {
        var self = this;

        self.userInfo.localeId = localeId;
        self.User.update({ id: userId }, self.userInfo,
            (result) => {
                self.$state.reload();
            }
        );
    }

    applyActivities() {
        this.securityService.getActivities().then(() => {
            this.securityService.canDo(this.activities.HomeView)
                .then(
                (result) => this.homeAvailable = result,
                () => this.homeAvailable = false);
            this.securityService.canDo(this.activities.BrokersView)
                .then(
                (result) => this.brokersAvailable = result,
                () => this.brokersAvailable = false);
            this.securityService.canDo(this.activities.UsersView)
                .then(
                (result) => this.usersAvailable = result,
                () => this.usersAvailable = false);
            this.securityService.canDo(this.activities.AssetsManage)
                .then(
                (result) => this.assetsAvailable = result,
                () => this.assetsAvailable = false);
        });
    }
}



