import ClientEditModalController from './client-edit-modal.controller';
import ClientEditModalTemplate from './client-edit-modal.template.html';
import Constants from '../constants';

export default class ClientController {
    /*@ngInject*/
    constructor($scope, $state, Client, clientObject, NotificationService, $filter, $uibModal, NgMap, UtilsService) {
        this.$scope = $scope;
        this.$state = $state;
        this.Client = Client;
        this.clientObject = clientObject;
        this.notificationService = NotificationService;
        this.$filter = $filter;
        this.$uibModal = $uibModal;
        this.NgMap = NgMap;
        this.utils = UtilsService;

        this.init();
    }

    init() {
        let self = this;
        this.clientObject.address = this.utils.generateAddress(this.clientObject);
        if (this.clientObject.industryId != null){
            Constants.clientsIndustryStore().byKey(this.clientObject.industryId).done(function(dataItem) {
                    self.clientObject.industry = dataItem.display;
             })
        }

        this.NgMap.getMap().then(map => {
            this.map = map;
            if (!this.map.marker) 
                this.map.marker = new google.maps.Marker();

            this.map.marker.setMap(null);

            let stylez = [{
                featureType: 'all',
                elementType: 'all',
                stylers: [{
                    saturation: -100  // made the map as grayscale
                }]
            }];
            let mapType = new google.maps.StyledMapType(stylez, {
                name: 'Grayscale'
            });

            map.mapTypes.set('tehgrayz', mapType);
            map.setMapTypeId('tehgrayz');

            this.setMapCoordinates();
        });
    }

    editClient() {
        let modal = this.$uibModal.open({
            controller: ClientEditModalController,
            template: ClientEditModalTemplate,
            resolve: {
                clientId: this.clientObject.id
            }
        });
        modal.result.then((result) => {
            let newAddress = this.utils.generateAddress(result),
                newCountry = result.countryId,
                needToUpdateMap;

            if (newAddress !== this.clientObject.address || newCountry !== this.clientObject.countryId) {
                needToUpdateMap = true;
            }

            jQuery.extend(this.clientObject, result);
            this.clientObject.address = newAddress;

            if (needToUpdateMap) {
                this.setMapCoordinates();
            }
        });
    }

    setMapCoordinates() {
        this.map.marker.setMap(null);

        if (this.clientObject.address) {
            let geocoder = new google.maps.Geocoder(),
                countries = Constants.countryStorePlain(),
                country = this.$filter('filter')(countries, {
                    value: this.clientObject.countryId
                });

            if (country && country[0]) {
                country = country[0].text + ', ';
            } else {
                country = '';
            }

            geocoder.geocode({
                address: country + this.clientObject.address
            }, (result, status) => {
                if (status === 'OK') {
                    this.map.setCenter(result[0].geometry.location);

                    this.map.marker.setPosition(result[0].geometry.location);
                    this.map.marker.setMap(this.map)
                }
            });
        }
    }
}


