export default class Asset {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
     return $resource(ConfigurationService.getApiBaseUrl() + 'api/assets/:id', { id: '@id' }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets',
            isArray: true
        },
        getFull: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:id/full',
        },
        setImage: {
            method: 'POST',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:id/image',
            transformRequest: angular.identity,
            headers: { 'Content-Type': undefined }
        },
        resetImage: {
            method: 'DELETE',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:id/image',
        },
        create: {
            method: 'POST'
        },
        update: {
            method: 'PUT'
        },
        archive: {
            method: 'DELETE'
        }
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    Asset.instance = new Asset(ConfigurationService, $resource);
    return Asset.instance;
  }
}


