import Messages from '../../messages';
import Constants from '../../constants';
import ArrayStore from 'devextreme/data/array_store';

export default class RepresentativesController {
    /*@ngInject*/
    constructor($scope, NotificationService, UtilsService, Representative, DialogService) {
        // from component
        this.client = this.client;

        // injected
        this.$scope = $scope;
        this.NotificationService = NotificationService;
        this.utils = UtilsService;
        this.messages = Messages;
        this.dialogService = DialogService;
        this.Representative = Representative;
        // initialization
        this.init();
        this.initGrid();
    }

    init() {
        let self = this;

        self.client.representatives.forEach(function (rep) {
            if(!rep.uniqueId){
                rep.uniqueId = self.utils.generateGuid();
            }
        })
    }

    initGrid() {
        let self = this;

        self.errorHandler = {
            requestFailed: function (response) {
                self.NotificationService.error(response.data);
            }
        }

        self.store = new ArrayStore({
            data: self.client.representatives,
            key: "uniqueId",
            errorHandler: self.errorHandler.requestFailed
        });

        self.gridDataSource = { store: self.store };

        self.gridSettings = $.extend(Constants.defaultGridSettings(), {
            dataSource: self.gridDataSource,
            onInitialized: function (e) {
                self.gridInstance = e.component;
            },
            filterRow: { visible: false },
            groupPanel: { visible: false },
            editing: {
                allowUpdate: true,
                allowDelete: true
            },
            columns: [
                {
                    dataField: 'name',
                    caption: Messages('clients.representatives.name'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    sortOrder: 'asc',
                    alignment: 'left',
                    validationRules: [
                        { type: 'required', message: Messages('validation.required') },
                        { type: 'stringLength', max: 255 },
                    ],
                },
                {
                    dataField: 'email',
                    caption: Messages('clients.representatives.email'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    alignment: 'left',
                    validationRules: [
                        { type: 'email' },
                    ],
                },
                {
                    dataField: 'phone',
                    caption: Messages('clients.representatives.phone'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    validationRules: [
                        {
                            type: "pattern",
                            message: Messages('validation.phone'),
                            pattern: Constants.phonePattern() 
                        }
                    ],
                },
                {
                    dataField: 'position',
                    caption: Messages('clients.representatives.position'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    alignment: 'right',
                    validationRules: [
                        { type: 'stringLength', max: 100 },
                    ],
                },
                {
                    dataField: "id",
                    caption: Messages('actions'),
                    width: 100,
                    alignment: 'right',
                    cellTemplate: function (container, options) {
                        container.addClass("actions-cell");

                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-pencil"></div>',
                            onClick: function (e) {
                                self.gridInstance.editRow(options.rowIndex);
                            }
                        }).appendTo(container);

                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-trash-o"></div>',
                            onClick: function (e) {
                                self.gridInstance.deleteRow(options.rowIndex);
                            }
                        }).appendTo(container);
                    },
                    editCellTemplate: function (container, options) {
                        container.addClass("actions-cell");
                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-floppy-o"></div>',
                            onClick: function (e) {
                                 self.gridInstance.saveEditData(options.rowIndex);
                            }
                        }).appendTo(container);
                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-trash-o"></div>',
                            onClick: function (e) {
                                self.gridInstance.cancelEditData(options.rowIndex);
                            }
                        }).appendTo(container);
                    }
                }
            ],
            onRowInserting: function(e){
                self.create(e);
            },
            onRowUpdating: function(e){
                self.update(e);
            },
            onRowRemoving: function(e) {
                self.delete(e);
            }
        });
    }

    addRow() {
        this.gridInstance.addRow();
    }

    create(e){
        let self = this;

        var d = $.Deferred();
        self.Representative.create({ clientId: self.client.id }, e.data,
                (result) => {
                    self.NotificationService.success(Messages('clients.representatives.message.created'));
                    e.data.id = result.id;
                    d.resolve();
                },
                (error) => {
                    self.NotificationService.error(error.data);
                    d.resolve(true);
                }
            );

            e.cancel = d.promise();
    }

    update(e) {
        var data = e.oldData;
        if (e.newData.name != null) data.name == e.newData.name;
        if (e.newData.phone != null) data.phone == e.newData.phone;
        if (e.newData.position != null) data.position == e.newData.position;
        if (e.newData.email != null) data.email == e.newData.email;

        let self = this;

        var d = $.Deferred();
        self.Representative.update({ clientId: self.client.id, id:data.id }, data,
                (result) => {
                    self.NotificationService.success(Messages('clients.representatives.message.updated'));
                    d.resolve();
                },
                (error) => {
                    self.NotificationService.error(error.data);
                    d.resolve(true);
                }
            );

            e.cancel = d.promise();
    }

    delete(e){
        let self = this;
        var d = $.Deferred();
        self.Representative.delete({ clientId: self.client.id, id:e.data.id }, 
                (result) => {
                    self.NotificationService.success(Messages('clients.representatives.message.deleted'));
                    d.resolve();
                },
                (error) => {
                    self.NotificationService.error(error.data);
                    d.resolve(true);
                }
            );

            e.cancel = d.promise();
    }
}


