export default class AuthInterceptorService {
    /*@ngInject*/
    constructor($q, $location, localStorageService) {
        this.$q = $q;
        this.$location = $location;
        this.localStorageService = localStorageService;

        this.request = config => {
            if (config.url.includes('/api/')) {
                let token = this.localStorageService.get('authenticationData');

                if (token != null && token.access_token) {
                    config.headers.Authorization = 'Bearer ' + token.access_token;
                }
            }

            return config;
        }

        this.requestError = rejection => {
            return this.$q.reject(rejection);
        }

        this.response = response => {
            if (response.status == "401") {
                this.localStorageService.remove('authenticationData');
                if (window.location.pathname != '/login') { 
                    window.location.href = "/login";
                }
            }
            //the same response/modified/or a new one need to be returned.  
            return response;
        }

        this.responseError = rejection => {
            if (rejection.status == "401") {
                this.localStorageService.remove('authenticationData');
                if (window.location.pathname != '/login') { 
                    window.location.href = "/login";
                }
            }
            return this.$q.reject(rejection);
        }
    }
  
}


