import UserEditModalController from './user-edit-modal.controller';
import UserChangePasswordModalController from './user-change-password-modal.controller';
import UserEditModalTemplate from './user-edit-modal.template.html';
import UserChangePasswordModalTemplate from './user-change-password-modal.template.html';
import Messages from '../messages';

export default class UserController {
    /*@ngInject*/
    constructor($scope, $state, User, userObject, currentUser, StoreAdapter, Role, Locale, NotificationService, sweetAlert, $uibModal) {
        this.$scope = $scope;
        this.$state = $state;
        this.User = User;
        this.userObject = userObject;
        this.currentUser = currentUser;
        this.storeAdapter = StoreAdapter;
        this.Role = Role;
        this.Locale = Locale;
        this.notificationService = NotificationService;
        this.sweetAlert = sweetAlert;
        this.$uibModal = $uibModal;

        this.init();
    }

    init() {
        this.$scope.isArchived = this.userObject.state === 3;

        let rolesStore = this.storeAdapter.createStore(this.Role, {}, {
            requestFailed: function (response) {
                self.NotificationService.error(response.data);
            }
        });

        rolesStore.load().done((result) => {
            this.roles = result;
            this.setRole();
        });

        let localesStore = this.storeAdapter.createStore(this.Locale, {}, {
            requestFailed: function (response) {
                self.NotificationService.error(response.data);
            }
        });

        localesStore.load().done((result) => {
            this.locales = result;
            this.setLocale();
        });
    }

    setRole() {
        let role = this.roles.find((item) => {
            return item.id === this.userObject.roleId;
        });
        role = role || {};
        this.$scope.role = role;
    }

    setLocale() {
        let locale = this.locales.find((item) => {
            return item.id === this.userObject.localeId;
        });
        locale = locale || {};
        this.$scope.locale = locale;
    }

    editUser() {
        let modal = this.$uibModal.open({
            controller: UserEditModalController,
            template: UserEditModalTemplate,
            resolve: {
                userObject: Object.assign({}, this.userObject)
            }
        });
        modal.result.then((result) => {
            jQuery.extend(this.userObject, result);
            this.$scope.isArchived = result.state === 3;
            this.setRole();
            this.setLocale();
        });
    }

    archiveTrigger() {
        if (this.$scope.isArchived) {
            this.activate();
        } else {
            this.archive();
        }
    }

    archive() {
        let self = this;

        this.sweetAlert.swal(
            {
                title: Messages('users.archive.title'),
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('users.archive.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    let user = Object.assign({}, self.userObject);
                    user.state = 3;
                    self.User.update({ id: user.id }, user,
                        function() {
                            self.userObject.state = 3;
                            self.$scope.isArchived = true;
                            self.notificationService.success(Messages('users.message.archived'));
                        },
                        function(result) {
                            console.error(result);
                            self.notificationService.error(result.data.message ? result.data.message : result.data);
                        });
                }
            }
        );
    }

    activate() {
        let self = this;
        this.sweetAlert.swal(
            {
                title: Messages('users.activate.title'),
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('users.activate.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    let user = Object.assign({}, self.userObject);
                    user.state = 2;
                    self.User.update({ id: user.id }, user,
                        function() {
                            self.userObject.state = 1;
                            self.$scope.isArchived = false;
                            self.notificationService.success(Messages('users.message.activated'));
                        },
                        function(result) {
                            self.notificationService.error(result.data.message ? result.data.message : result.data);
                        });
                }
            }
        );
    }

    changePassword(){
        let modal = this.$uibModal.open({
            controller: UserChangePasswordModalController,
            template: UserChangePasswordModalTemplate,
            resolve: {
                userId: this.userObject.id
            }
        });
    }
}


