// import template and controller from individual component directory
import Controller from './imagegallery.controller'
import Template from './imagegallery.template.html'

let ImageGalleryComponent = {
    bindings: {
        images: '=',
        maxImageWidth: '=?',
        maxImageHeight: '=?',
        maxImageCount: '=?',
        readonly: '=?',
        imageAddCallback: '=?',
        imageDeleteCallback: '=?',
        sortingChangeCallback: '=?'
    },
    controller: Controller,
    template: Template
};

export default ImageGalleryComponent;


