export default class User {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
     return $resource(ConfigurationService.getApiBaseUrl() + 'api/users/:id', { id: '@id' }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/users',
            isArray: true
        },
        update: {
            method: 'PUT'
        },
         changePassword: {
            method: 'PUT',
            url: ConfigurationService.getApiBaseUrl() + 'api/users/:id/password',
        },
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    User.instance = new User(ConfigurationService, $resource);
    return User.instance;
  }
}


