'use strict';

import Messages from '../../messages';

export default class Sidebar {
    /*@ngInject*/
    constructor($scope, $rootScope, $state, $stateParams, SecurityService, UtilsService, Activities, Asset, Broker) {
        this.$scope = $scope;
        this.$rootScope = $rootScope;
        this.$state = $state;
        this.$stateParams = $stateParams;
        this.securityService = SecurityService;
        this.utils = UtilsService;
        this.activities = Activities;
        this.Asset = Asset;
        this.Broker = Broker;
        this.messages = Messages;

        this.loadUserInfo();
        this.init();
        this.applyActivities();
    }

    init() {
        let self = this;
        this.$rootScope.$on('$stateChangeSuccess', 
            function(event, toState, toParams, fromState, fromParams){ 
                if(toState.name != fromState.name){
                    self.resetSettings();
                    self.updateMenu(toState, toParams);
                    $('.nav li a').blur();
                }
            });

        self.updateMenu(self.$state.current, self.$stateParams);
    }

    loadUserInfo() {
        this.securityService.getUserInfo().then(result => {
            this.userInfo = result;
            if (result.roleId === 2) {
                this.loadBrokerInfo();
            }
        });
    }

    loadBrokerInfo() {
        this.Broker.get({ id: this.userInfo.brokerId }).$promise.then(result => {
            this.brokerInfo = result;
        });
    }

    applyActivities() {
        this.securityService.getActivities().then(() => {
            this.securityService.canDo(this.activities.HomeView)
                .then(
                    (result) => this.homeAvailable = result,
                    () => this.homeAvailable = false);
            this.securityService.canDo(this.activities.BrokersView)
                .then(
                    (result) => this.brokersAvailable = result,
                    () => this.brokersAvailable = false);
            this.securityService.canDo(this.activities.UsersView)
                .then(
                    (result) => this.usersAvailable = result,
                    () => this.usersAvailable = false);
            this.securityService.canDo(this.activities.AssetsManage)
                .then(
                    (result) => this.assetsAvailable = result,
                    () => this.assetsAvailable = false);
            this.securityService.canDo(this.activities.AssetsView)
                .then(
                    (result) => this.assetsViewAvailable = result,
                    () => this.assetsViewAvailable = false);
            this.securityService.canDo(this.activities.ClientsManage)
                .then(
                    (result) => this.clientsAvailable = result,
                    () => this.clientsAvailable = false);
        });
    }

    updateMenu(state, stateParams) {
        let self = this;

        self.assetsClientMenu = self.isAssetsClientMenu(state, stateParams);
        self.globalClientMenu = !self.assetsClientMenu;
    }

    isActive(name) {
        let states = [];
        switch (name) {
            case 'users':
                states = ['users', 'user'];
                break;
            case 'brokers':
                states = ['brokers', 'broker'];
                break;
            case 'assets':
                states = ['assets', 'edit-asset', 'asset-stats'];
                break;
            case 'asset-stats-spaces':
                states = ['asset-stats-spaces'];
                break;
            case 'asset-stats-tenants':
                states = ['asset-stats-tenants'];
                break;
            case 'asset-stats-stacking-plan':
                states = ['asset-stats-stacking-plan'];
                break;
            case 'clients':
                states = ['clients', 'create-client', 'edit-client'];
                break;
        }

        for (let i = 0, l = states.length; i < l; i++) {
            if (this.$state.includes(states[i])) {
                return true;
            }
        }
        return false;
    }

    resetSettings() {

        //reset settings for asset        
        //this.showAssetPanel = false;
    }

    isAssetsClientMenu(state, stateParams){
        let self = this;
        if(state.name === 'asset-stats-spaces' || state.name === 'asset-stats-stacking-plan' || state.name === 'asset-stats-tenants') {
            self.$scope.assetId = state.resolve.assetId[1](stateParams);
            self.Asset.get({id: this.$scope.assetId}).$promise.then(asset => {
                this.asset = asset;
                if(this.asset){
                    this.assetAddress = this.utils.generateAddress(this.asset)
                    this.showAssetPanel = true;
                }
            })
            return true;
        } else {
            this.showAssetPanel = false;
        }

        return false;
    }

    signOut() {
        this.securityService.signOut();
        this.$state.go('login', null, { reload: true });
    }
}



