/*@ngInject*/
export default function routing($urlRouterProvider, $locationProvider, $stateProvider, Activities) {
    $locationProvider.html5Mode({
        enabled: true,
        requireBase: false
    }).hashPrefix('!');

    $urlRouterProvider.otherwise('/');

    $stateProvider
        // private part states
        .state('private', {
            template: require('./private.html'),
            abstract: true
        })
        .state('home', {
            url: '/',
            parent: 'private',
            template: require('../app/home/home-template.html'),
            controller: 'HomeController',
            resolve: {
                 canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.HomeView, 'forbidden', 'login');
                }]
            }
        })
        .state('brokers', {
            url: '/brokers',
            parent: 'private',
            template: require('../app/brokers/brokers.template.html'),
            controller: 'BrokersController',
            controllerAs: 'ctrl',
            resolve: {
                canDo: ['SecurityService', function (SecurityService) {
                   return SecurityService.canDo(Activities.BrokersView, 'forbidden', 'login');
               }]
            }
        })
        .state('broker', {
            url: '/brokers/{id:int}',
            parent: 'private',
            template: require('../app/brokers/broker.template.html'),
            controller: 'BrokerController',
            controllerAs: '$ctrl',
            resolve: {
                brokerObject: ['$stateParams', 'Broker', function ($stateParams, Broker) {
                    return Broker.get({ id: $stateParams.id }).$promise;
                }],
                canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.BrokerManage, 'forbidden', 'login');
                }],
            }
        })
        .state('asset-stats', {
            url: '/asset-stats',
            parent: 'private',
            template: require('../app/asset-stats/asset-stats.template.html'),
            controller: 'AssetStatsController',
            controllerAs: '$ctrl',
            resolve: {
                canDo: ['SecurityService', function (SecurityService) {
                   return SecurityService.canDo(Activities.AssetsView, 'forbidden', 'login');
               }]
            }
        })
        .state('asset-stats-2', {
            url: '/asset-stats2',
            parent: 'private',
            template: require('../app/asset-stats/asset-stats-2.template.html'),
            controller: 'AssetStatsController',
            controllerAs: '$ctrl',
            resolve: {
                canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.AssetsView, 'forbidden', 'login');
                }]
            }
        })
        .state('asset-stats-spaces', {
            url: '/asset-stats/{id:int}/spaces',
            parent: 'private',
            template: require('../app/asset-stats/asset-stat-spaces.template.html'),
            controller: 'AssetStatSpacesController',
            controllerAs: '$ctrl',
            resolve: {
                asset: ['$stateParams', 'Asset', function ($stateParams, Asset) {
                    return Asset.get({ id: $stateParams.id }).$promise;
                }],
                canDo: ['SecurityService', function(SecurityService) {
                    return SecurityService.canDo(Activities.AssetsView, 'forbidden', 'login');
                }],
                assetId: ['$stateParams', function($stateParams) {
                    return $stateParams.id;
                }]
            }
        })
        .state('asset-stats-tenants', {
            url: '/asset-stats/{id:int}/tenants',
            parent: 'private',
            template: require('../app/asset-stats/tenants/asset-stat-tenants.template.html'),
            controller: 'AssetStatTenantsController',
            controllerAs: '$ctrl',
            resolve: {
                asset: ['$stateParams', 'Asset', function ($stateParams, Asset) {
                    return Asset.get({ id: $stateParams.id }).$promise;
                }],
                canDo: ['SecurityService', function(SecurityService) {
                    return SecurityService.canDo(Activities.AssetsView, 'forbidden', 'login');
                }],
                assetId: ['$stateParams', function($stateParams) {
                    return $stateParams.id;
                }]
            }
        })
        .state('asset-stats-stacking-plan', {
            url: '/asset-stats/{id:int}/stacking-plan',
            parent: 'private',
            template: require('../app/asset-stats/asset-stat-stacking-plan.template.html'),
            controller: 'AssetStatStackingPlanController',
            controllerAs: '$ctrl',
            resolve: {
                asset: ['$stateParams', 'Asset', function ($stateParams, Asset) {
                    return Asset.get({ id: $stateParams.id }).$promise;
                }],
                canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.AssetsView, 'forbidden', 'login');
                }],
                assetId: ['$stateParams', function($stateParams) {
                    return $stateParams.id;
                }]
            }
        })
        .state('users', {
            url: '/users',
            parent: 'private',
            template: require('../app/users/users.template.html'),
            controller: 'UsersController',
            controllerAs: '$ctrl',
            resolve: {
                canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.UsersView, 'forbidden', 'login');
                }],
                currentUser: ['SecurityService', function (SecurityService) {
                    return SecurityService.getUserInfo();
                }]
            }
        })
        .state('user', {
            url: '/users/{id:int}',
            parent: 'private',
            template: require('../app/users/user.template.html'),
            controller: 'UserController',
            controllerAs: '$ctrl',
            resolve: {
                userObject: ['$stateParams', 'User', function ($stateParams, User) {
                    return User.get({ id: $stateParams.id }).$promise;
                }],
                canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.UsersView, 'forbidden', 'login');
                }],
                currentUser: ['SecurityService', function (SecurityService) {
                    return SecurityService.getUserInfo();
                }]
            }
        })
        .state('assets', {
            url: '/assets',
            parent: 'private',
            template: require('../app/assets/assets.template.html'),
            controller: 'AssetsController',
            controllerAs: '$ctrl',
            resolve: {
                canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.AssetsManage, 'forbidden', 'login');
                }],
                currentUser: ['SecurityService', function (SecurityService) {
                    return SecurityService.getUserInfo();
                }]
            }
        })
         .state('asset', {
            url: '/assets/{id:int}',
            parent: 'private',
            template: require('../app/assets/asset.template.html'),
            controller: 'AssetController',
            controllerAs: '$ctrl',
            resolve: {
                assetObject: ['$stateParams', 'Asset', function ($stateParams, Asset) {
                    return Asset.getFull({ id: $stateParams.id }).$promise;
                }],
                canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.AssetsManage, 'forbidden', 'login');
                }],
                currentUser: ['SecurityService', function (SecurityService) {
                    return SecurityService.getUserInfo();
                }]
            }
        })
        .state('clients', {
            url: '/clients',
            parent: 'private',
            template: require('../app/clients/clients.template.html'),
            controller: 'ClientsController',
            controllerAs: '$ctrl',
            resolve: {
                canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.ClientsManage, 'forbidden', 'login');
                }],
                currentUser: ['SecurityService', function (SecurityService) {
                    return SecurityService.getUserInfo();
                }]
            }
        })
        .state('client', {
            url: '/clients/{id:int}',
            parent: 'private',
            template: require('../app/clients/client.template.html'),
            controller: 'ClientController',
            controllerAs: '$ctrl',
            resolve: {
                clientObject: ['$stateParams', 'Client', function ($stateParams, Client) {
                    return Client.getFull({ id: $stateParams.id }).$promise;
                }],
                canDo: ['SecurityService', function (SecurityService) {
                    return SecurityService.canDo(Activities.ClientsManage, 'forbidden', 'login');
                }],
                currentUser: ['SecurityService', function (SecurityService) {
                    return SecurityService.getUserInfo();
                }]
            }
        })
        // public part states
        .state('public', {
            template: require('./public.html'),
            abstract: true
        })
        .state('login', {
            url: '/login',
            parent: 'public',
            params: {
                automatic: false
            },
            template: require('../app/login/login-template.html'),
            controller: 'LoginController',
            controllerAs: '$ctrl'
        })
        .state('registration', {
            url: '/registration/{token}',
            template: require('../app/registration/registration.template.html'),
            controller: 'RegistrationController',
            controllerAs: '$ctrl',
            resolve: {
                registrationObject: ['$stateParams', 'Registration', function ($stateParams, Registration) {
                    return Registration.get({ token: $stateParams.token }).$promise;
                }],
            }
        })
        // error pages
        .state('forbidden', {
            url: '/forbidden',
            template: require('../app/errors/forbidden.template.html')
        })
        .state('notfound', {
            url: '/notfound',
            template: require('../app/errors/notfound.template.html')
        });
}



