export default class Floor {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
     return $resource(ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/floors/:id', { assetId: '@assetId', assetObjectId: '@assetObjectId', id: '@id', count: '@count', autonumerate:'@autonumerate'  }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/floors',
            isArray: true
        },
        createRange: {
            method: 'POST',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/floors?count=:count&autonumerate=:autonumerate',
            isArray: true
        },
        update: {
            method: 'PUT'
        },
        delete: {
            method: 'DELETE'
        },
        getFull: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/floors/:id/full',
        },
        getByAssetId: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/floors',
            isArray: true
        },
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    Floor.instance = new Floor(ConfigurationService, $resource);
    return Floor.instance;
  }
}


