// import template and controller from individual component directory
import Controller from './floor.controller'
import Template from './floor.template.html'

let FloorComponent = {
    bindings: {
        asset: '=',
        saveUpdates: '=?',
        needUpdateDataSources: '&'
    },
    controller: Controller,
    template: Template
};

export default FloorComponent;


