export default class Client {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
    return $resource(ConfigurationService.getApiBaseUrl() + 'api/clients/:id', { id: '@id' }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/clients',
            isArray: true
        },
        update: {
            method: 'PUT'
        },
        create: {
            method: 'POST',
            url: ConfigurationService.getApiBaseUrl() + 'api/clients',
        },
        getFull: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/clients/:id/full',
        },
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    Client.instance = new Client(ConfigurationService, $resource);
    return Client.instance;
  }
}


