export default class CompareObjectService {
    constructor() {
    }

    compareSimpleAssets(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        return obj1.id == obj2.id &&
            obj1.state == obj2.state &&
            obj1.name == obj2.name &&
            obj1.countryId == obj2.countryId &&
            obj1.city == obj2.city &&
            obj1.street == obj2.street &&
            obj1.building == obj2.building &&
            obj1.brokerDatabaseRecordId == obj2.brokerDatabaseRecordId &&
            obj1.description == obj2.description &&
            obj1.notes == obj2.notes &&
            obj1.floorCount == obj2.floorCount &&
            obj1.brokerId == obj2.brokerId;
    }

    compareAssets(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        var res = this.compareSimpleAssets(obj1, obj2);

        if (res && obj1.images != undefined && obj2.images != undefined) {
            if (obj1.images.length != obj2.images.length)
                return false;

            for (var i = 0; i < obj1.images.length && res; i++) {
                res = this.compareImages(obj1.images[i], obj2.images[i]);
            }
        }

        if (res && obj1.documents != undefined && obj2.documents != undefined) {
            if (obj1.documents.length != obj2.documents.length)
                return false;

            for (var i = 0; i < obj1.documents.length && res; i++) {
                res = this.compareDocuments(obj1.documents[i], obj2.documents[i]);
            }
        }

        if (res && obj1.floors != undefined && obj2.floors != undefined) {
            if (obj1.floors.length != obj2.floors.length)
                return false;

            for (var i = 0; i < obj1.floors.length && res; i++) {
                res = this.compareFloors(obj1.floors[i], obj2.floors[i]);
            }
        }

        if (res && obj1.spaces != undefined && obj2.spaces != undefined) {
            if (obj1.spaces.length != obj2.spaces.length)
                return false;

            for (var i = 0; i < obj1.spaces.length && res; i++) {
                res = this.compareSpaces(obj1.spaces[i], obj2.spaces[i]);
            }
        }

        return res;
    }

    compareImages(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        return obj1.id == obj2.id &&
            obj1.assetId == obj2.assetId &&
            obj1.filePath == obj2.filePath &&
            obj1.order == obj2.order &&
            obj1.originalFileName == obj2.originalFileName &&
            obj1.isMain == obj2.isMain &&
            obj1.contentType == obj2.contentType &&
            obj1.imageFile == obj2.imageFile;
    }

    compareDocuments(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        return obj1.id == obj2.id &&
            obj1.assetId == obj2.assetId &&
            obj1.filePath == obj2.filePath &&
            obj1.originalFileName == obj2.originalFileName &&
            obj1.contentType == obj2.contentType &&
            obj1.documentFile == obj2.documentFile &&
            obj1.notes == obj2.notes;
    }

    compareSimpleSpaces(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        return obj1.id == obj2.id &&
            obj1.assetId == obj2.assetId &&
            obj1.floorId == obj2.floorId &&
            obj1.brokerId == obj2.brokerId &&
            obj1.number == obj2.number &&
            obj1.type == obj2.type &&
            obj1.rentableArea == obj2.rentableArea &&
            obj1.state == obj2.state &&
            obj1.marketingDescription == obj2.marketingDescription &&
            obj1.notes == obj2.notes;
    }

    compareSpaces(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        var res = this.compareSimpleSpaces(obj1, obj2);

        if (res && obj1.images != undefined && obj2.images != undefined) {
            if (obj1.images.length != obj2.images.length)
                return false;

            for (var i = 0; i < obj1.images.length && res; i++) {
                res = this.compareImages(obj1.images[i], obj2.images[i]);
            }
        }

        if (res && obj1.documents != undefined && obj2.documents != undefined) {
            if (obj1.documents.length != obj2.documents.length)
                return false;

            for (var i = 0; i < obj1.documents.length && res; i++) {
                res = this.compareDocuments(obj1.documents[i], obj2.documents[i]);
            }
        }

        return res;
    }

    compareSimpleFloors(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        return obj1.id == obj2.id &&
            obj1.number == obj2.number &&
            obj1.description == obj2.description &&
            obj1.assetId == obj2.assetId &&
            obj1.brokerId == obj2.brokerId;
    }

    compareFloors(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        var res = this.compareSimpleFloors(obj1, obj2);

        if (res && obj1.spaces != undefined && obj2.spaces != undefined) {
            if (obj1.spaces.length != obj2.spaces.length)
                return false;

            for (var i = 0; i < obj1.spaces.length && res; i++) {
                res = this.compareSpaces(obj1.spaces[i], obj2.spaces[i]);
            }
        }

        return res;
    }

    compareClients(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        var res = this.compareSimpleClients(obj1, obj2);

        if (res && obj1.representatives != undefined && obj2.representatives != undefined) {
            if (obj1.representatives.length != obj2.representatives.length)
                return false;

            for (var i = 0; i < obj1.representatives.length && res; i++) {
                res = this.compareRepresentatives(obj1.representatives[i], obj2.representatives[i]);
            }
        }

        return res;
    }

    compareSimpleClients(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        return obj1.id == obj2.id &&
            obj1.name == obj2.name &&
            obj1.state == obj2.state &&
            obj1.countryId == obj2.countryId &&
            obj1.city == obj2.city &&
            obj1.street == obj2.street &&
            obj1.building == obj2.building &&
            obj1.notes == obj2.notes;
    }

    compareRepresentatives(obj1, obj2) {
        if (obj1 == undefined || obj2 == undefined)
            return false;

        return obj1.id == obj2.id &&
            obj1.name == obj2.name &&
            obj1.mobileNumber == obj2.mobileNumber &&
            obj1.officeNumber == obj2.officeNumber &&
            obj1.email == obj2.email;
    }
}


