import Controller from './spaces.controller';
import Template from './spaces.template.html';

let SpacesComponent = {
    bindings: {
        asset: '=',
        saveUpdates: '=?',
        needUpdateDataSources: '&'
    },
    controller: Controller,
    template: Template
};

export default SpacesComponent;


