import AssetStatsController from './asset-stats.controller';
import AssetStatSpacesController from './asset-stat-spaces.controller';
import AssetStatTenantsController from './tenants/asset-stat-tenants.controller';
import AssetStatStackingPlan from './asset-stat-stacking-plan.resource';
import AssetStatStackingPlanController from './asset-stat-stacking-plan.controller';
import AssetStats from './asset-stats.resource';
import AssetStatSpaces from './asset-stat-spaces.resource';
import AssetStatTenants from './tenants/asset-stat-tenants.resource';

var module = angular.module('tower360.assetStats', [])
  .controller('AssetStatsController', AssetStatsController)
  .controller('AssetStatSpacesController', AssetStatSpacesController)
  .controller('AssetStatTenantsController', AssetStatTenantsController)
  .controller('AssetStatStackingPlanController', AssetStatStackingPlanController)
  .factory('AssetStats', AssetStats.activate)
  .factory('AssetStatSpaces', AssetStatSpaces.activate)
  .factory('AssetStatTenants', AssetStatTenants.activate)
  .factory('AssetStatStackingPlan', AssetStatStackingPlan.activate)
  .directive('resize', ['$window', function ($window) {
    return function (scope, element) {
        var w = angular.element($window);
        var changeHeight = function () {
          element.css('height', (element.width()));
        }
        w.bind('resize', function () {
            changeHeight();
        });
        changeHeight();
    }
  }]);

export default module;


