import UsersController from './users.controller';
import UserController from './user.controller';
import User from './user.resource';
import Role from './role.resource';
import Broker from './broker.resource';
import Locale from './locale.resource';

var module = angular.module('tower360.users', [])
  .controller('UsersController', UsersController)
  .controller('UserController', UserController)
  .factory('User', User.activate)
  .factory('Role', Role.activate)
  .factory('Broker', Broker.activate)
  .factory('Locale', Locale.activate);

export default module;


