import Messages from '../messages';
import Constants from '../constants';

export default class SpacesFilterController {
    /*@ngInject*/
    constructor($scope, $state, filter, towers, floors, $uibModalInstance, uibDateParser) {
        this.$scope = $scope;
        this.$state = $state;
        this.filter = filter.data;
        this.towers = towers.data;
        this.floors = floors;
        this.$uibModalInstance = $uibModalInstance;
        this.uibDateParser = uibDateParser;

        this.init();
        this.initForm();
    }

    init() {
        let spaceTypes = Constants.spaceTypeStorePlain(),
            piecesPerRow = Math.round(spaceTypes.length / 3),
            typesRows = [];

        for (let i = 0, l = 3; i < l; i++) {
            typesRows[i] = [];
            let j = 0;
            for (let jl = piecesPerRow; j < jl; j++) {
                let item = spaceTypes.shift();
                if (item) {
                    typesRows[i].push(item);
                } else {
                    break;
                }
            }
        }

        this.$scope.typesRows = typesRows;
    }

    initForm() {
        this.$scope.fromValueChanged = (from, to) => {
            if (this.$scope[from] && this.$scope[to] && this.$scope[from] > this.$scope[to]) {
                this.$scope[to] = this.$scope[from];
            }
        };
        this.$scope.toValueChanged = (from, to) => {
            if (this.$scope[from] && this.$scope[to] && this.$scope[to] < this.$scope[from]) {
                this.$scope[from] = this.$scope[to];
            }
        };

        this.$scope.fromSelectorChanged = (from, to) => {
            if (this.$scope[from] && this.$scope[to] && Number(this.$scope[from]) > Number(this.$scope[to])) {
                this.$scope[to] = this.$scope[from];
            }
        };
        this.$scope.toSelectorChanged = (from, to) => {
            if (this.$scope[from] && this.$scope[to] && Number(this.$scope[to]) < Number(this.$scope[from])) {
                this.$scope[from] = this.$scope[to];
            }
        };

        // price
        let price_from = this.filter.find((item) => {
            return item.id === 'price_from';
        });
        this.$scope.price_from = price_from && price_from.value;
        let price_to = this.filter.find((item) => {
            return item.id === 'price_to';
        });
        this.$scope.price_to = price_to && price_to.value;

        // area
        let area_from = this.filter.find((item) => {
            return item.id === 'area_from';
        });
        this.$scope.area_from = area_from && area_from.value;
        let area_to = this.filter.find((item) => {
            return item.id === 'area_to';
        });
        this.$scope.area_to = area_to && area_to.value;

        // floor
        let floor_from = this.filter.find((item) => {
            return item.id === 'floor_from';
        });
        this.$scope.floor_from = floor_from && floor_from.value;
        let floor_to = this.filter.find((item) => {
            return item.id === 'floor_to';
        });
        this.$scope.floor_to = floor_to && floor_to.value;
        this.$scope.floors = [];
        for (let i = this.floors.min, l = this.floors.max; i <= l; i++) {
            let name;
            if (i < 0) {
                name = `${i} UG`;
            } else if (i > 0) {
                name = `${i} OG`;
            } else {
                name = `EG`
            }
            this.$scope.floors.push({
                id: i,
                name
            });
        }

        // asking price
        let in_place_rent_from = this.filter.find((item) => {
            return item.id === 'in_place_rent_from';
        });
        this.$scope.in_place_rent_from = in_place_rent_from && in_place_rent_from.value;
        let in_place_rent_to = this.filter.find((item) => {
            return item.id === 'in_place_rent_to';
        });
        this.$scope.in_place_rent_to = in_place_rent_to && in_place_rent_to.value;

        // towers
        this.$scope.towers = {
            available: this.towers,
            selected: []
        };
        this.filter.forEach((item) => {
            if (item.id.indexOf('tower_') === 0) {
                this.$scope.towers.selected.push(item.value);
            }
        });

        // office type
        this.$scope.office_types = [];
        this.$scope.office_types_ids = [];
        this.filter.forEach((item) => {
            if (item.id.indexOf('office_type_') === 0) {
                this.$scope.office_types.push(item.value);
                this.$scope.office_types_ids.push(item.value.id);
            }
        });
        this.$scope.officeTypeToggleSelection = (type, valueString) => {
            let id = this.$scope.office_types.findIndex((item) => {
                return item.id === type;
            });

            if (id > -1) {
                this.$scope.office_types.splice(id, 1);
                this.$scope.office_types_ids.splice(id, 1);
            } else {
                this.$scope.office_types.push({
                    id: type,
                    name: valueString
                });
                this.$scope.office_types_ids.push(type);
            }
        };


        this.$scope.cancel = () => {
            this.$uibModalInstance.dismiss('cancel');
        };

        this.$scope.save = () => {
            let result = [];
            if (this.$scope.price_from) {
                result.push({
                    id: 'price_from',
                    name: Messages('assets.spaces.filter.asking_price_from'),
                    value: this.$scope.price_from,
                    text: `${this.$scope.price_from} [€/${Messages('square.meter')}]`
                });
            }
            if (this.$scope.price_to) {
                result.push({
                    id: 'price_to',
                    name: Messages('assets.spaces.filter.asking_price_to'),
                    value: this.$scope.price_to,
                    text: `${this.$scope.price_to} [€/${Messages('square.meter')}]`
                });
            }
            if (this.$scope.area_from) {
                result.push({
                    id: 'area_from',
                    name: Messages('assets.spaces.filter.rentable_area_from'),
                    value: this.$scope.area_from,
                    text: `${this.$scope.area_from} ${Messages('square.meter')}`
                });
            }
            if (this.$scope.area_to) {
                result.push({
                    id: 'area_to',
                    name: Messages('assets.spaces.filter.rentable_area_to'),
                    value: this.$scope.area_to,
                    text: `${this.$scope.area_to} ${Messages('square.meter')}`
                });
            }
            if (this.$scope.floor_from) {
                let florFrom = this.$scope.floors.find((item) => {
                    return item.id === Number(this.$scope.floor_from);
                });
                result.push({
                    id: 'floor_from',
                    name: Messages('assets.spaces.filter.floor_from'),
                    value: this.$scope.floor_from,
                    text: `${florFrom.name}`
                });
            }
            if (this.$scope.floor_to) {
                let florTo = this.$scope.floors.find((item) => {
                    return item.id === Number(this.$scope.floor_to);
                });
                result.push({
                    id: 'floor_to',
                    name: Messages('assets.spaces.filter.floor_to'),
                    value: this.$scope.floor_to,
                    text: `${florTo.name}`
                });
            }
            if (this.$scope.in_place_rent_from) {
                result.push({
                    id: 'in_place_rent_from',
                    name: Messages('assets.spaces.filter.in_place_rent_from'),
                    value: this.$scope.in_place_rent_from,
                    text: `${this.$scope.in_place_rent_from} [€/${Messages('square.meter')}]`
                });
            }
            if (this.$scope.in_place_rent_to) {
                result.push({
                    id: 'in_place_rent_to',
                    name: Messages('assets.spaces.filter.in_place_rent_to'),
                    value: this.$scope.in_place_rent_to,
                    text: `${this.$scope.in_place_rent_to} [€/${Messages('square.meter')}]`
                });
            }
            if (this.$scope.towers.selected.length) {
                this.$scope.towers.selected.forEach(function(item) {
                    result.push({
                        id: `tower_${item.name}`,
                        value: item,
                        text: item.name
                    });
                });
            }
            if (this.$scope.office_types.length) {
                this.$scope.office_types.forEach((item) => {
                    result.push({
                        id: `office_type_${item.id}`,
                        value: item,
                        text: item.name
                    });
                });
            }
            this.$uibModalInstance.close(result);
        }
    }
}


