import AssetEditModalController from './asset-edit-modal.controller';
import AssetEditModalTemplate from './asset-edit-modal.template.html';
import 'devextreme/ui/text_area';
import Messages from '../messages';
import Constants from '../constants';

export default class AssetController {
    /*@ngInject*/
    constructor($scope, $state, Broker, Asset, assetObject, currentUser, ImageService, StoreAdapter, NotificationService, $filter, NgMap, $uibModal, sweetAlert, UtilsService) {
        this.$scope = $scope;
        this.$state = $state;
        this.$filter = $filter;
        this.Broker = Broker;
        this.Asset = Asset;
        this.assetObject = assetObject;
        this.currentUser = currentUser;
        this.storeAdapter = StoreAdapter;
        this.notificationService = NotificationService;
        this.NgMap = NgMap;
        this.$uibModal = $uibModal;
        this.sweetAlert = sweetAlert;
        this.utils = UtilsService;
        this.imageService = ImageService;

        this.init();
        this.initForm();
    }

    init() {
        let self = this;

        self.asset = self.assetObject;
        self.asset.address = self.utils.generateAddress(self.asset);
        self.asset.images = [];

        if(self.assetObject.mainImageUrl){
            self.asset.images = [{filePath: self.assetObject.mainImageUrl}];
        }

        self.$scope.isArchived = self.asset.state === 3;

        self.errorHandler = {
            requestFailed: function (response) {
                this.NotificationService.error(response.data);
            }
        };

        self.updateDataSources = function(initiator) {
            if(initiator == 'objects') {
                self.$scope.$broadcast('updateAssetObjectDataSource');
            } else if(initiator == 'floors'){
                self.$scope.$broadcast('updateFloorDataSource');
            }
        };

        self.NgMap.getMap().then(function(map) {
            self.map = map;

            if (!self.map.marker) 
                self.map.marker = new google.maps.Marker();

            self.map.marker.setMap(null);

            let stylez = [{
                featureType: 'all',
                elementType: 'all',
                stylers: [{
                    saturation: -100  // made the map as grayscale
                }]
            }];
            let mapType = new google.maps.StyledMapType(stylez, {
                name: 'Grayscale'
            });

            map.mapTypes.set('tehgrayz', mapType);
            map.setMapTypeId('tehgrayz');

            self.setMapCoordinates();
        });

        self.$scope.saveUpdates = function() {
            self.saveUpdates();
        };

        self.$scope.updateImage = function() {
            self.updateImage();
        }
    }

    initForm() {
        let self = this;

        this.stateStore = Constants.assetStateStore();
        this.countryStore = Constants.countryStore();

        let brokersStore = this.storeAdapter.createStore(this.Broker, {}, this.errorHandler);

        brokersStore.load().done((result) => {
            result.forEach(function(value) {
                self.brokers.push({
                    value: value.id,
                    text: value.name
                });
            });
        });

        this.brokers = [];
        this.brokerId = this.asset.brokerId;
        this.showBroker = function() {
            let selected = self.$filter('filter')(self.brokers, {
                value: self.brokerId
            });
            return (typeof self.brokerId === 'number' && selected.length) ? selected[0].text : 'Not set';
        };

        this.brokerDatabaseRecordId = this.asset.brokerDatabaseRecordId || '';
        this.brokerDatabaseRecordIdValidation = function(value) {
            if (value.length > 10) {
                return Messages('assets.brokerDatabaseRecordId.errorLength');
            }
        };

        this.notes = this.asset.notes || '';
        this.notesValidation = function(value) {
            if (value.length > 500) {
                return Messages('assets.notes.errorLength');
            }
        };
    }

    setMapCoordinates() {
        this.map.marker.setMap(null);
        
        if (this.assetObject.address) {
            var geocoder = new google.maps.Geocoder(),
                countries = Constants.countryStorePlain(),
                country = this.$filter('filter')(countries, {
                    value: this.asset.countryId
                });

            if (country && country[0]) {
                country = country[0].text + ', ';
            } else {
                country = '';
            }

            geocoder.geocode({
                address: country + this.assetObject.address
            }, (result, status) => {
                if (status === 'OK') {
                    this.map.setCenter(result[0].geometry.location);

                    this.map.marker.setPosition(result[0].geometry.location);
                    this.map.marker.setMap(this.map)
                }
            });
        }
    }

    updateImage(){
        let self = this;

        if(self.asset.images.length == 0){
            self.Asset.resetImage(
                { id: this.asset.id },
                (result) => {
                    self.notificationService.success(Messages('assets.gallery.message.deleted'));
                    self.saveInProgress = false;
                },
                (error) => {
                    this.notificationService.error(error.data);
                    self.saveInProgress = false;
                }
            );
        }
        else {
            var blob = self.imageService.dataURItoBlob(self.asset.images[0].imageFile.imageData);
            var fd = new FormData(document.forms[0]);
            fd.append("data", blob, self.asset.images[0].originalFileName);

            self.Asset.setImage(
                { id: this.asset.id },
                fd ,
                (result) => {
                    self.notificationService.success(Messages('assets.gallery.message.added'));
                    self.saveInProgress = false;
                },
                (error) => {
                    this.notificationService.error(error.data);
                    self.saveInProgress = false;
                }
            );
        }
    }

    saveUpdates() {
        for (let i in this.asset) {
            if (this.asset.hasOwnProperty(i)) {
                if (this[i] !== undefined) {
                    this.asset[i] = this[i];
                } else if (this.$scope[i] !== undefined) {
                    this.asset[i] = this.$scope[i];
                }
            }
        }

        return this.Asset.update(
            { id: this.asset.id },
            this.asset,
            (result) => {
                this.notificationService.success(Messages('assets.message.updated'));
                this.saveInProgress = false;
            },
            (error) => {
                this.notificationService.error(error.data);
                this.saveInProgress = false;
            }
        );
    }

    tabGridsRefresh($event) {
        $event.preventDefault();
        let tab = $event.currentTarget.getAttribute('aria-controls');
        if (tab === 'floors') {
            $('#floorSettings #gridContainer').dxDataGrid('instance').refresh();
        }

        if (tab === 'spaces') {
            $('#spaceSettings #spacesGridContainer').dxDataGrid('instance').refresh();
        }

        if (tab === 'objects') {
            $('#objectsSettings #objectsGridContainer').dxDataGrid('instance').refresh();
        }
    }

    archiveTrigger() {
        if (this.$scope.isArchived) {
            this.activate();
        } else {
            this.archive();
        }
    }

    editAsset() {
        let modal = this.$uibModal.open({
            controller: AssetEditModalController,
            template: AssetEditModalTemplate,
            resolve: {
                assetObject: Object.assign({}, this.asset)
            }
        });
        modal.result.then(result => {
            let newAddress = this.utils.generateAddress(result),
                newCountry = result.countryId,
                needToUpdateMap;

            if (newAddress !== this.asset.address || newCountry !== this.asset.countryId) {
                needToUpdateMap = true;
            }

            $.extend(this.asset, result);
            this.asset.address = newAddress;

            if (needToUpdateMap) {
                this.setMapCoordinates();
            }
        });
    }

    archive() {
        let self = this;
        this.sweetAlert.swal(
            {
                title: Messages('assets.archive.title'),
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('assets.archive.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    self.Asset.archive({ id: self.asset.id },
                        function (result) {
                            self.$scope.isArchived = true;
                            self.notificationService.success(Messages('assets.message.archived'));
                        },
                        function (error) {
                            self.notificationService.error(error.data.message ? error.data.message : error.data);
                        });
                }
            }
        );
    }

    activate() {
        let self = this,
            asset = Object.assign({}, this.asset);

        this.sweetAlert.swal(
            {
                title: Messages('assets.activate.title'),
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('assets.activate.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    asset.state = 2;
                    self.Asset.update({ id: asset.id }, asset,
                        function(result) {
                            self.$scope.isArchived = false;
                            self.notificationService.success(Messages('assets.message.activated'));
                        },
                        function(error) {
                            self.notificationService.error(error.data.message ? error.data.message : error.data);
                        });
                }
            }
        );
    }
}


