import FilterController from './spaces-filter-modal.controller';
import FilterTemplate from './spaces-filter-modal.template.html';
import Constants from '../constants';
import Messages from '../messages';
import moment from 'moment';

export default class AssetStatSpacesController {
    /*@ngInject*/
    constructor($scope, $rootScope, $state, asset, assetId, AssetStatSpaces, AssetStatStackingPlan, $uibModal, uibDateParser, $timeout, UtilsService) {
        this.$scope = $scope;
        this.$rootScope = $rootScope;
        this.$state = $state;
        this.asset = asset;
        this.assetId = assetId;
        this.AssetStatSpaces = AssetStatSpaces;
        this.AssetStatStackingPlan = AssetStatStackingPlan;
        this.$uibModal = $uibModal;
        this.uibDateParser = uibDateParser;
        this.$timeout = $timeout;
        this.utils = UtilsService;

        this.Messages = Messages;

        this.init();

        $scope.$on('$destroy', () => {
            $rootScope.$broadcast('right-sidebar:close');
        });
        $scope.$on('right-sidebar:closed', () => {
            this.selectSpaceRow([]);
            this.selectedSpace = null;
        });
    }

    init() {
        this.$scope.greedData = [];

        this.$scope.statuses = [{
            id: 'all',
            value: 1,
            name: Messages('assets.spaces.filter.all'),
        }, {
            id: 'vacant',
            value: 3,
            name: Messages('assets.spaces.filter.vacant')
        }, {
            id: 'occupied',
            value: 2,
            name: Messages('assets.spaces.filter.occupied')
        }, {
            id: '1_month',
            value: 1,
            name: Messages('assets.spaces.filter.in_1_month')
        }, {
            id: '3_month',
            value: 3,
            name: Messages('assets.spaces.filter.in_3_month')
        }, {
            id: '6_month',
            value: 6,
            name: Messages('assets.spaces.filter.in_6_month')
        }, {
            id: '1_year',
            value: 12,
            name: Messages('assets.spaces.filter.in_1_year')
        }, {
            id: 'custom',
            name: Messages('assets.spaces.filter.custom_dates')
        }];
        this.$scope.selectedStatus = this.$scope.statuses[0];

        this.$timeout(() => {
            this.customDateElement = $('#spaces-filter-daterangepicker');
            $(this.customDateElement).daterangepicker({
                minDate: new Date(),
                autoApply: true
            });
            $(this.customDateElement).on('apply.daterangepicker', (ev, picker) => {
                this.customAvailableFrom = picker.startDate;
                this.customAvailableTo = picker.endDate;
                this.$scope.customAvailableFrom = this.getParsedDate(picker.startDate);
                this.$scope.customAvailableTo = this.getParsedDate(picker.endDate);
                this.gridUpdate(1);
            });
        }, 1);

        this.$scope.extraFilters = [];
        this.$scope.towers = [];
        this.$scope.sortType = 'unit';
        this.$scope.sortDirection = 0;

        this.gridUpdate(1);

        this.AssetStatStackingPlan.getObjects({
            asset: this.assetId
        }, (result) => {
            this.$scope.towers = result;
        }, (err) => {

        });

        this.statsLabelsDisctionary = {
                'all': 'totalSpacesCount',
                'vacant' :'totalVacantSpacesCount',
                'occupied' :'totalOccupiedSpacesCount',
                '1_month': 'expiresInOneMonthCount',
                '3_month': 'expiresInThreeMonthCount',
                '6_month' : 'expiresInSixMonthCount',
                '1_year': 'expiresInOneYearCount'
        };
    }

    gridUpdate(page) {
        this.$scope.update = true;
        this.page = page;

        let extraFilters = this.$scope.extraFilters,
            status = null,
            availableFrom = null,
            availableTo = null,
            inPlacePriceFrom = extraFilters.find((item) => {
                return item.id === 'in_place_rent_from';
            }),
            inPlacePriceTo = extraFilters.find((item) => {
                return item.id === 'in_place_rent_to';
            }),
            priceFrom = extraFilters.find((item) => {
                return item.id === 'price_from';
            }),
            priceTo = extraFilters.find((item) => {
                return item.id === 'price_to';
            }),
            areaFrom = extraFilters.find((item) => {
                return item.id === 'area_from';
            }),
            areaTo = extraFilters.find((item) => {
                return item.id === 'area_to';
            }),
            floorFrom = extraFilters.find((item) => {
                return item.id === 'floor_from';
            }),
            floorTo = extraFilters.find((item) => {
                return item.id === 'floor_to';
            }),
            towers = [],
            spaceTypes = [];

        extraFilters.forEach((item) => {
            if (item.id.indexOf('tower_') === 0) {
                towers.push(item.value.id);
            }
            if (item.id.indexOf('office_type_') === 0) {
                spaceTypes.push(item.value.id);
            }
        });

        switch (this.$scope.selectedStatus.id) {
            case 'all':
            case 'vacant':
            case 'occupied':
                status = this.$scope.selectedStatus.value;
                break;
            case '1_month':
            case '3_month':
            case '6_month':
            case '1_year':
                availableFrom = moment().format();
                availableTo = moment().add(this.$scope.selectedStatus.value, 'M').format();
                break;
            case 'custom':
                availableFrom = moment(this.customAvailableFrom).format();
                availableTo = moment(this.customAvailableTo).format();
                break;
        }

        this.AssetStatSpaces.search({
            id: this.assetId,
            st: status,
            agte: areaFrom && areaFrom.value,
            alte: areaTo && areaTo.value,
            apgte: priceFrom && priceFrom.value,
            aplte: priceTo && priceTo.value,
            ipgte: inPlacePriceFrom && inPlacePriceFrom.value,
            iplte: inPlacePriceTo && inPlacePriceTo.value,
            expgte: availableFrom,
            explte: availableTo,
            fgte: floorFrom && floorFrom.value,
            flte: floorTo && floorTo.value,
            twr: towers,
            ot: spaceTypes,
            srt: this.$scope.sortType,
            ord: this.$scope.sortDirection,
            pg: page || 1,
            ps: 20
        }, (result) => {
            // console.log(result);
            this.$scope.update = false;
            this.floorsMax = result.maxFloor;
            this.floorsMin = result.minFloor;

            if (page === 1) {
                this.$scope.greedData = result.items;
                this.$scope.disable_infinite_scrolling = false;
                this.updateStatusLabels(result.stats);
            } else {
                if (result.items.length === 0) {
                    this.$scope.disable_infinite_scrolling = true;
                } else {
                    this.$scope.disable_infinite_scrolling = false;
                    this.$scope.greedData = this.$scope.greedData.concat(result.items);
                }
            }

            

            this.$timeout(() => {
                this.$scope.load_more = false;
            }, 1);
        }, (error) => {
            console.log(error);
            this.$scope.update = false;
            this.$scope.error = error;
        });

        this.$rootScope.$broadcast('right-sidebar:close');
    }
    loadMoreItems() {
        if (!this.$scope.greedData.length) {
            return;
        }

        this.$scope.load_more = true;
        this.gridUpdate(++this.page);
    }
    sortGrid(sortType) {
        this.$scope.sortType = sortType;
        this.$scope.sortDirection = this.$scope.sortDirection === 1 ? 0 : 1;
        this.gridUpdate(1);
    }

    getOfficeType(type) {
        let officeType = Constants.spaceTypeStorePlain().find((item) => {
            return item.value === type;
        });
        return officeType.text;
    }

    getParsedDate(date) {
        return this.uibDateParser.filter(new Date(date), 'dd/MM/yyyy');
    }

    selectStatus(e, status) {
        e.preventDefault();
        this.$scope.selectedStatus = status;

        if (status.id !== 'custom') {
            this.gridUpdate(1);
            this.$scope.customAvailableFrom = null;
            this.$scope.customAvailableTo = null;
            $(this.customDateElement).data('daterangepicker').setStartDate(new Date());
            $(this.customDateElement).data('daterangepicker').setEndDate(new Date());
        } else {
            this.showDateRangeSelector();
        }
    }

    showDateRangeSelector() {
        $(this.customDateElement).focus();
    }

    showExtraFilters() {
        let modal = this.$uibModal.open({
            controller: FilterController,
            template: FilterTemplate,
            resolve: {
                filter: {
                    data: this.$scope.extraFilters
                },
                towers: {
                    data: this.$scope.towers
                },
                floors: {
                    min: this.floorsMin,
                    max: this.floorsMax
                }
            },
            size: 'lg'
        });
        modal.result.then((result) => {
            this.$scope.extraFilters = result;
            if (result.length) {
                this.gridUpdate(1);
            }
        });
    }

    extraFilterRemoveItem(item) {
        let id = item.id,
            itemIndex = this.$scope.extraFilters.findIndex(function(item) {
              return item.id === id;
            });

        this.$scope.extraFilters.splice(itemIndex, 1);
        this.gridUpdate(1);
    }

    selectSpaceRow(row) {
        let cl = 'filtered-spaces-row-selected';
        $('#filtered-spaces-grid tr').each(function(i, item) {
            if (item === row[0]) {
                $(item).addClass(cl);
            } else {
                $(item).removeClass(cl);
            }
        });
    }

    showSpaceInfo(event, data) {
        if (this.selectedSpace === data.number) {
            this.$rootScope.$broadcast('right-sidebar:close');
            return;
        }

        this.selectedSpace = data.number;
        let result = Object.assign({}, data),
            contract = data.contract;

        if (contract && contract.industryId != null){
            Constants.clientsIndustryStore().byKey(contract.industryId).done(function(dataItem) {
                result.industryId = contract.industryId;
                result.industry = dataItem.display;
            });
        }

        this.$rootScope.$broadcast('right-sidebar:open', {
            type: 'spaceDescription',
            data: result
        });

        this.selectSpaceRow($(event.target).parent('tr'));
    }

    updateStatusLabels(stats){
            let self = this;
            this.$scope.statuses.forEach(function(element) {
                let value = Number(stats[self.statsLabelsDisctionary[element.id]]);
                element.label = this.utils.formatBadgeValue(value);
            }, this);
    }
}



