import ValidationEngine from 'devextreme/ui/validation_engine';
import Messages from '../messages';
import Constants from '../constants';

export default class UserController {
    /*@ngInject*/
    constructor($scope, $state, $uibModalInstance, User, userObject, Broker, Role, Locale, StoreAdapter, NotificationService) {
        this.$scope = $scope;
        this.$state = $state;
        this.User = User;
        this.userObject = userObject;
        this.Broker = Broker;
        this.Role = Role;
        this.Locale = Locale;
        this.storeAdapter = StoreAdapter;
        this.notificationService = NotificationService;
        this.$uibModalInstance = $uibModalInstance;
        this.validationGroup = 'user-edit';

        this.initForm();
    }

    initForm() {
        let self = this;

        this.errorHandler = {
            requestFailed: function (response) {
                self.NotificationService.error(response.data);
            }
        };

        this.$scope.editMode = this.userObject != null;
        this.isSaveOperationDisable = true;

        if (this.$scope.editMode) {
            this.$scope.user = this.userObject;
        } else {
            this.$scope.user = {
                email: '',
                displayName: '',
                brokerId: null,
                roleId: 1
            }
        }

        this.$scope.emailSettings = {
            placeholder: Messages('users.placeholder.email'),
            bindingOptions: {
                value: 'user.email',
                disabled: 'editMode'
            }
        };
        this.$scope.emailValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'stringLength', max: 256, min: 3, trim: true },
                { type: 'email', message: Messages('validation.email') }
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.displaynameSettings = {
            placeholder: Messages('users.placeholder.displayname'),
            bindingOptions: {
                value: 'user.displayName '
            }
        };
        this.$scope.displaynameValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'stringLength', max: 50, min: 1, trim: true }
            ],
            validationGroup: this.validationGroup
        };

        let rolesStore = this.storeAdapter.createStore(this.Role, {}, this.errorHandler);

        rolesStore.load().done((result) => {
            this.$scope.roleSelectSettings = {
                items: result,
                displayExpr: 'name',
                valueExpr: 'id',
                bindingOptions: {
                    value: 'user.roleId'
                }
            }
        });

        this.$scope.roleValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') }
            ],
            validationGroup: this.validationGroup
        };

        let localesStore = this.storeAdapter.createStore(this.Locale, {}, this.errorHandler);

        localesStore.load().done((result) => {
            console.log(result);
            this.$scope.localeSelectSettings = {
                items: result,
                displayExpr: 'localeName',
                valueExpr: 'id',
                bindingOptions: {
                    value: 'user.localeId'
                }
            }
        });

        this.$scope.localeValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') }
            ],
            validationGroup: this.validationGroup
        };

        let brokersStore = this.storeAdapter.createStore(this.Broker, {}, this.errorHandler);

        brokersStore.load().done((result) => {
            this.$scope.brokerSelectSettings = {
                items: result,
                displayExpr: 'name',
                valueExpr: 'id',
                bindingOptions: {
                    value: 'user.brokerId'
                }
            }
        });

        this.$scope.brokerValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') }
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.validateAndSave = () => {
            let result = ValidationEngine.getGroupConfig(this.validationGroup).validate();
            if (result.isValid) {
                this.saveInProgress = true;
                this.isSaveOperationDisable = true;

                if (!this.$scope.editMode) {
                    this.User.save(this.$scope.user,
                        (result) => {
                            this.notificationService.success(Messages('users.message.created'));
                            this.saveInProgress = false;
                            this.$state.go('user', {id : result.id}, {reload: false});
                            this.$uibModalInstance.close(result);
                        },
                        (error) => {
                            this.notificationService.error(error.data.message ? error.data.message : error.data);
                            this.saveInProgress = false;
                        }
                    );
                } else {
                    this.User.update({ id: this.$scope.user.id }, this.$scope.user,
                        (result) => {
                            this.notificationService.success(Messages('users.message.updated'));
                            self.saveInProgress = false;
                            this.$uibModalInstance.close(result);
                        },
                        (error) => {
                            this.notificationService.error(error.data.message ? error.data.message : error.data);
                            this.saveInProgress = false;
                        }
                    );
                }
            }
        };

        this.$scope.cancel = () => {
            ValidationEngine.removeGroup(this.validationGroup);
            this.$uibModalInstance.dismiss('cancel');
        };
    }
}


