import ValidationEngine from 'devextreme/ui/validation_engine';
import 'devextreme/ui/text_area';
import Messages from '../messages';
import Constants from '../constants';

export default class AssetEditModalController {
    /*@ngInject*/
    constructor($scope, $state, $timeout, Asset, assetObject, $uibModalInstance, NotificationService, StoreAdapter, Broker, UtilsService) {
        this.$scope = $scope;
        this.$state = $state;
        this.Asset = Asset;
        this.assetObject = assetObject;
        this.$uibModalInstance = $uibModalInstance;
        this.notificationService = NotificationService;
        this.storeAdapter = StoreAdapter;
        this.Broker = Broker;
        this.utils = UtilsService;
        this.validationGroup = 'asset-edit';
        this.addressValidationGroup = 'asset-create-address';

        this.init();
        this.initForm();

        $timeout(() => {
            this.initAutocomplete();
        });
    }

    init() {
        let self = this;

        this.$scope.editMode = !!this.assetObject;

        if (this.$scope.editMode) {
            this.$scope.asset = this.assetObject;
            this.$scope.asset.address = this.utils.generateAddress(this.$scope.asset);
        } else {
            this.$scope.asset = {
                name: '',
                brokerId: null,
                countryId: null,
                city: '',
                street: '',
                streetAddress: '',
                description: '',
                brokerDatabaseRecordId: '',
                notes: '',
                images: [],
                documents:[],
                floors: [],
                spaces: [],
                state: 2,
                address: '',
            }
        }

        this.errorHandler = {
            requestFailed: function (response) {
                this.NotificationService.error(response.data);
            }
        };

        this.$scope.clearAddress = function(){
            self.$scope.asset.address = '';
        }
    }

    initForm() {
        let self = this;

        this.$scope.nameSettings = {
            placeholder: Messages('assets.placeholder.name'),
            bindingOptions: {
                value: 'asset.name'
            }
        };
        this.$scope.nameValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'stringLength', max: 100 },
            ],
            validationGroup: this.validationGroup
        };

        let brokersStore = this.storeAdapter.createStore(this.Broker, {}, this.errorHandler);
        brokersStore.load().done((result) => {
            this.$scope.brokerSelectSettings = {
                dataSource: result,
                displayExpr: 'name',
                valueExpr: 'id',
                bindingOptions: {
                    value: 'asset.brokerId'
                }
            }
        });

        this.$scope.brokerValidationRules = {
            validationRules: [
                {type: 'required', message: Messages('validation.required')}
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.description = this.$scope.$parent.description;
        this.$scope.descriptionSettings = {
            placeholder: Messages('assets.placeholder.description'),
            height: 180,
            maxLength: 500,
            bindingOptions: {
                value: 'asset.description'
            }
        };
        this.$scope.descriptionValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 500 },
            ],
            validationGroup: this.validationGroup
        };

        this.countryStore = Constants.countryStore();
        this.$scope.countrySettings = {
            dataSource: this.countryStore,
            displayExpr: 'display',
            valueExpr: 'value',
            bindingOptions: {
                value: 'asset.countryId'
            }
        };

        this.$scope.countryValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 50 },
            ],
            validationGroup: this.validationGroup,
            onOptionChanged: () => {
                this.setAutocompleteCountry();
            }
        };

        this.$scope.addressSettings = {
            bindingOptions: {
                value: 'asset.address',
                disabled: '!asset.countryId'
            }
        };
        this.$scope.addressValidationRules = {
            validationRules: [{
                type: 'custom',
                validationCallback: function(options) {
                    if (!self.$scope.asset.countryId) {
                        options.rule.message = Messages('address.validation.countryRequired');
                        return false;
                    }

                    return true;
                }
            }],
            validationGroup: this.addressValidationGroup
        };

        this.$scope.citySettings = {
            placeholder: Messages('address.placeholder.city'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'asset.city'
            }
        };
        this.$scope.cityValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 80 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.streetSettings = {
            placeholder: Messages('address.placeholder.street'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'asset.street'
            }
        };
        this.$scope.streetValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 100 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.streetAddressSettings = {
            placeholder: Messages('address.placeholder.streetAddress'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'asset.streetAddress'
            }
        };
        this.$scope.streetAddressValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 10 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.postCodeSettings = {
            placeholder: Messages('address.placeholder.postCode'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'asset.postCode'
            }
        };
        this.$scope.postCodeValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 10 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.validateAndSaveSettings = function() {
            let validation = ValidationEngine.getGroupConfig(self.validationGroup).validate(),
                addressValidation = ValidationEngine.getGroupConfig(self.addressValidationGroup).validate();

            if (validation.isValid && addressValidation.isValid) {
                self.$scope.saveInProgress = true;
                self.$scope.isSaveOperationDisable = true;

                if (self.$scope.editMode) {
                    self.Asset.update({id: self.$scope.asset.id}, self.$scope.asset,
                        (result) => {
                            self.notificationService.success(Messages('assets.message.created'));
                            self.$scope.saveInProgress = false;
                            self.$uibModalInstance.close(self.$scope.asset);
                        },
                        (error) => {
                            self.notificationService.error(error.data.message ? error.data.message : error.data);
                            self.$scope.saveInProgress = false;
                        }
                    );
                } else {
                    self.Asset.create({id: self.$scope.asset.id}, self.$scope.asset,
                        (result) => {
                            self.notificationService.success(Messages('assets.message.created'));
                            self.$scope.saveInProgress = false;
                            self.$uibModalInstance.close(self.$scope.asset);
                            self.$state.go('asset', {
                                id: result.id
                            });
                        },
                        (error) => {
                            self.notificationService.error(error.data.message ? error.data.message : error.data);
                            self.$scope.saveInProgress = false;
                        }
                    );
                }
            }
        };

        this.$scope.cancel = function () {
            ValidationEngine.removeGroup(self.validationGroup);
            self.$uibModalInstance.dismiss('cancel');
        };
    }

    initAutocomplete() {
        if (!window.google) {
            return;
        }

        this.autocomplete = new google.maps.places.Autocomplete($('#field-address input')[0]);
        google.maps.event.addListener(this.autocomplete, 'place_changed', () => {
            let place = this.autocomplete.getPlace(),
                citySet, streetSet, buildingSet, postCodeSet;

            if (place.address_components instanceof Array) {
                place.address_components.forEach((item) => {
                    // if (item.types.indexOf('country') !== -1) {
                    //     this.$scope.countryId = item.long_name;
                    // }
                    if (item.types.indexOf('locality') !== -1) {
                        this.$scope.asset.city = item.long_name;
                        citySet = true;
                    }
                    if (item.types.indexOf('route') !== -1) {
                        this.$scope.asset.street = item.long_name;
                        streetSet = true;
                    }
                    if (item.types.indexOf('street_number') !== -1) {
                        this.$scope.asset.streetAddress = item.long_name;
                        buildingSet = true;
                    }
                    if (item.types.indexOf('postal_code') !== -1) {
                        this.$scope.asset.postCode = item.long_name;
                        postCodeSet = true;
                    }
                });
            }

            if (!citySet) {
                this.$scope.asset.city = null;
            }
            if (!streetSet) {
                this.$scope.asset.street = null;
            }
            if (!buildingSet) {
                this.$scope.asset.streetAddress = null;
            }
             if (!postCodeSet) {
                this.$scope.asset.postCode = null;
            }

            ValidationEngine.getGroupConfig(this.addressValidationGroup).validate();
        });

         if (this.$scope.asset.countryId) {
            this.setAutocompleteCountry();
        }
    }

    setAutocompleteCountry() {
        let countries = Constants.countryStorePlain(),
            curCountryCode;

        countries.forEach((country) => {
            if (country.value === this.$scope.asset.countryId) {
                curCountryCode = country.isoCode;
            }
        });

        if (curCountryCode) {
            this.autocomplete.setComponentRestrictions({
                'country': curCountryCode
            });
        }
    }
}



