export default class AssetObject {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
     return $resource(ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:id', { assetId: '@assetId', id: '@id' }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects',
            isArray: true
        },
        create: {
            method: 'POST',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects',
        },
        update: {
            method: 'PUT'
        },
        archive: {
            method: 'DELETE'
        },
        getById: {
            method: 'GET',
        },
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    AssetObject.instance = new AssetObject(ConfigurationService, $resource);
    return AssetObject.instance;
  }
}


