let AssetDocumentsComponent = {
    bindings: {
        documents: '=',
        asset: '=',
        maxAttachementSize: '=?',
        maxAttachementCount: '=?',
        readonly: '=?',
        documentCreateCallback: '=?',
        saveUpdates: '=?'
    },
    controller : function() {
        let self = this;

        self.maxAttachementCount = 10;
        self.maxAttachementSize = 5000000;

        self.documentCreateCallback = function (document) {
            document.assetId = self.asset.id;
            self.saveUpdates();
            //spaceId: self.space.spaceId,
        };
        self.documentDeleteCallback = function (document) {
            self.saveUpdates();
        };
    },
    template: "<attachements documents='$ctrl.documents' " +
    "                        readonly='$ctrl.readonly' " +
    "                        max-attachement-count='$ctrl.maxAttachementCount' " +
    "                        max-attachement-size='$ctrl.maxAttachementSize' " +
    "                        document-create-callback='$ctrl.documentCreateCallback' " +
    "                        document-delete-callback='$ctrl.documentDeleteCallback' " +
    "                        class='asset-documents'></attachements>"
};

export default AssetDocumentsComponent;




