export default class HomeController {
    /*@ngInject*/
    constructor($scope, $state, SecurityService) {
        this.$state = $state;
        this.securityService = SecurityService;
        this.init();
    }

    init() {
        this.securityService.getUserInfo().then(result => {
            let redirectTarget = 'brokers';
            if (result.roleId === 2) {
                redirectTarget = 'asset-stats';
            }
            this.$state.go(redirectTarget);
        });
    }
}


