import ArrayStore from 'devextreme/data/array_store';
import ClientEditModalController from './client-edit-modal.controller';
import ClientEditModalTemplate from './client-edit-modal.template.html';
import Messages from '../messages';
import Constants from '../constants';

export default class ClientsController {
    /*@ngInject*/
    constructor($scope, $state, Client, StoreAdapter, currentUser, NotificationService, DialogService, sweetAlert, $uibModal, UtilsService) {
        this.$scope = $scope;
        this.$state = $state;
        this.Client = Client;
        this.storeAdapter = StoreAdapter;
        this.currentUser = currentUser;
        this.notificationService = NotificationService;
        this.dialogService = DialogService;
        this.sweetAlert = sweetAlert;
        this.$uibModal = $uibModal;
        this.utils = UtilsService;

        this.initGrid();
    }

    initGrid() {
        let self = this;

        self.errorHandler = {
            requestFailed: function (response) {
                this.NotificationService.error(response.data);
            }
        };

        self.stateStore = Constants.clientsStateStore();
        self.countryStore = Constants.countryStore();

        self.clients = [];

        self.Client.query(
            {},
            (result) => {
                self.clients = result;

                self.clients.forEach(item => {
                    item.address = self.utils.generateAddress(item);
                });

                self.store = new ArrayStore({
                    data: self.clients,
                    key: "id",
                    modified: function () {
                        // 'modified' event handler
                    },
                    errorHandler: self.errorHandler.requestFailed
                });

                self.gridDataSource = { store: self.store };

                self.gridSettings = $.extend(Constants.defaultGridSettings(), {
                    dataSource: self.gridDataSource,
                    columns: [{
                        dataField: "id",
                        visible: false
                    }, {
                        dataField: "name",
                        caption: Messages('clients.name'),
                        sortOrder: "asc",
                        filterOperations: ['contains', 'startswith', '='],
                        selectedFilterOperation: 'contains',
                        cellTemplate: function(cellElement, cellInfo) {
                            let element = $('<a>', {
                                'href': '/clients/' + cellInfo.data.id,
                                'ui-sref': 'client'
                            });

                            element.text(cellInfo.displayValue)
                                   .appendTo(cellElement);
                        }
                    }, {
                        dataField: "address",
                        caption: Messages('clients.address'),
                        filterOperations: ['contains', 'startswith', '='],
                        selectedFilterOperation: 'contains',
                    }, {
                        dataField: "countryId",
                        caption: Messages('clients.country'),
                        lookup: {
                            dataSource: self.countryStore,
                            displayExpr: 'display',
                            valueExpr: 'value'
                        },
                    }, {
                        dataField: "state",
                        caption: Messages('clients.state'),
                        lookup: {
                            dataSource: self.stateStore,
                            displayExpr: 'display',
                            valueExpr: 'value'
                        },
                        width: 100
                    },
                   {
                        caption: Messages('actions'),
                        width: 90,
                        alignment: 'right',
                        cellTemplate: function (container, options) {
                            container.addClass("actions-cell");

                            $("<div />").dxButton({
                                type: 'custom',
                                template: `<div class="fa fa-pencil" title="${Messages('clients.edit')}"></div>`,
                                onClick: function (data) {
                                    data.jQueryEvent.stopPropagation();

                                    let modal = self.$uibModal.open({
                                        controller: ClientEditModalController,
                                        template: ClientEditModalTemplate,
                                        resolve: {
                                            clientId: options.data.id
                                        }
                                    });
                                    modal.result.then((result) => {
                                        result.address = self.utils.generateAddress(result);
                                        jQuery.extend(options.data, result);
                                    });
                                }
                            }).appendTo(container);

                            // let isArchived = options.data.state === 2;
                            // $("<div />").dxButton({
                            //     type: 'custom',
                            //     template: `<div class="fa ${isArchived ? 'fa-undo' : 'fa-trash-o'}" title="${isArchived ? Messages('clients.activate') : Messages('clients.archive')}"></div>`,
                            //     disabled: options.data.state === 2,
                            //     onClick: function (data) {
                            //         if (isArchived) {
                            //             self.activateClient(options.data);
                            //         } else {
                            //             self.archiveClient(options.data);
                            //         }
                            //     }
                            // }).appendTo(container);
                        }
                    }],
                    selection: {
                        mode: 'single' // 'multiple'
                    },
                    onSelectionChanged: function(selectedItems){
                        self.selectedClient = selectedItems.selectedRowsData[0];
                    }
                });
            }
        );
    }

    // archiveClient(client) {
    //     client = Object.assign({}, client);
    //     let self = this;
    //     client.state = 2;
    //     this.sweetAlert.swal(
    //         {
    //             title: Messages('clients.archive.title'),
    //             type: 'warning',
    //             showCancelButton: true,
    //             confirmButtonColor: '#DD6B55',
    //             confirmButtonText: Messages('clients.archive.confirmButton'),
    //         },
    //         function (isConfirm) {
    //             if (isConfirm) {
    //                 self.Client.update({id: client.id}, client,
    //                     function(result) {
    //                         self.store.remove(client.id);
    //                         self.store.insert(result);
    //
    //                         $('#gridContainer').dxDataGrid('instance').refresh();
    //                         self.notificationService.success(Messages('clients.message.archived'));
    //                     },
    //                     function(result) {
    //                         self.notificationService.error(result.data.message ? result.data.message : result.data);
    //                     });
    //             }
    //         }
    //     );
    // }
    //
    // activateClient(client) {
    //     client = Object.assign({}, client);
    //
    //     let self = this;
    //     this.sweetAlert.swal(
    //         {
    //             title: Messages('clients.activate.title'),
    //             type: 'warning',
    //             showCancelButton: true,
    //             confirmButtonColor: '#DD6B55',
    //             confirmButtonText: Messages('clients.activate.confirmButton'),
    //         },
    //         function (isConfirm) {
    //             if (isConfirm) {
    //                 clients.state = 1;
    //                 self.Client.update({ id: client.id }, client,
    //                     function(result) {
    //                         self.store.remove(client.id);
    //                         self.store.insert(result);
    //
    //                         $('#gridContainer').dxDataGrid('instance').refresh();
    //                         self.notificationService.success(Messages('clients.message.archived'));
    //                     },
    //                     function(result) {
    //                         self.notificationService.error(result.data.message ? result.data.message : result.data);
    //                     });
    //             }
    //         }
    //     );
    // }
}


