import Messages from '../messages';

export default class LoginController {
  /*@ngInject*/
  constructor($state, $stateParams, SecurityService, NotificationService, localStorageService) {
    this.$state = $state;
    this.securityService = SecurityService;
    this.notificationService = NotificationService;

    this.signInData = new SignInData();

    if ($stateParams.automatic) {
      this.error = Messages('login.automatic.signout');
    }
    else {
      this.error = null;
    }
  }

  signIn() {
    this.securityService.signIn(this.signInData.userName, this.signInData.password)
      .then(
        (response) => {
          this.securityService.reset(); 
          this.$state.go('home');
        },
        (err) => { this.error = err.data.error_description; }
      );
  }
}

class SignInData {
  constructor() {
    this.userName = null;
    this.password = null;
  }
}


