import Controller from './actionmenu.controller'
import Template from './actionmenu.template.html'

let ActionMenuComponent = {
  bindings: {
      userInfo: '=',
      brokerInfo: '=?'
  },
  controller: Controller,
  template: Template
};

export default ActionMenuComponent;


