export default class NotificationService {
  /*@ngInject*/
  constructor(toaster) {
    this.toaster = toaster;
  }

  info(text) {
    this.toaster.pop({
      type: 'info',
      body: text,
      timeout: 3000
    });
  }

  warning(text) {
    this.toaster.pop({
      type: 'warning',
      body: text,
      timeout: 5000
    });
  }

  error(text) {
    this.toaster.pop({
      type: 'error',
      body: text,
      timeout: 10000
    });
  }

  success(text) {
    this.toaster.pop({
      type: 'success',
      body: text,
      timeout: 3000,
      'position-class': 'toast-bottom-center'
    });
  }
}


