import ValidationEngine from 'devextreme/ui/validation_engine';
import Messages from '../messages';
import Constants from '../constants';

export default class BrokerEditController {
    /*@ngInject*/
    constructor($scope, $state, $timeout, $uibModalInstance, brokerObject, Broker, Role, StoreAdapter, NotificationService) {
        this.$scope = $scope;
        this.$state = $state;
        this.brokerObject = brokerObject;
        this.Broker = Broker;
        this.Role = Role;
        this.storeAdapter = StoreAdapter;
        this.notificationService = NotificationService;
        this.$uibModalInstance = $uibModalInstance;
        this.validationGroup = 'broker-edit';
        this.addressValidationGroup = 'broker-edit-address';
        this.$timeout = $timeout;

        this.init();
        this.initForm();
    }

    init() {
        let self = this;

        this.$timeout(() => {
            this.initAutocomplete();
        });

        this.$scope.clearAddress = function(){
            self.brokerObject.address = null;
        }
    }

    initForm() {
        let self = this;

        this.$scope.editMode = this.brokerObject !== null;
        this.isSaveOperationDisable = true;

        if (this.$scope.editMode) {
            this.$scope.broker = this.brokerObject;
        } else {
            this.$scope.broker = {
                name: '',
                countryId: 1,
                city: '',
                street: '',
                streetAddress: '',
                postCode: '',
            }
        }

        this.$scope.nameSettings = {
            placeholder: Messages('brokers.placeholder.name'),
            bindingOptions: {
                value: 'broker.name'
            }
        };
        this.$scope.nameValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'stringLength', max: 255, message: Messages('validation.maxLength') },
            ],
            validationGroup: this.validationGroup
        };

        this.countryStore = Constants.countryStore();
        this.$scope.countrySettings = {
            dataSource: this.countryStore,
            displayExpr: 'display',
            valueExpr: 'value',
            bindingOptions: {
                value: 'broker.countryId'
            }
        };

        this.$scope.countryValidationRules = {
            validationRules: [],
            validationGroup: this.validationGroup,
            onOptionChanged: function() {
                self.setAutocompleteCountry();
            }
        };

        this.$scope.broker.address = '';
        this.$scope.addressSettings = {
            bindingOptions: {
                value: 'broker.address',
                disabled: '!broker.countryId'
            }
        };
        this.$scope.addressValidationRules = {
            validationRules: [{
                type: 'custom',
                validationCallback: function(options) {
                    if (!self.$scope.broker.countryId) {
                        options.rule.message = Messages('address.validation.countryRequired');
                        return false;
                    }

                    return true;
                }
            }],
            validationGroup: this.addressValidationGroup
        };

        this.$scope.citySettings = {
            placeholder: Messages('address.placeholder.city'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'broker.city'
            }
        };
        this.$scope.cityValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 80 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.streetSettings = {
            placeholder: Messages('address.placeholder.street'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'broker.street'
            }
        };
        this.$scope.streetValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 100 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.streetAddressSettings = {
            placeholder: Messages('address.placeholder.streetAddress'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'broker.streetAddress'
            }
        };
        this.$scope.streetAddressValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 10 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.postCodeSettings = {
            placeholder: Messages('address.placeholder.postCode'),
            onChange: self.$scope.clearAddress,
            bindingOptions: {
                value: 'broker.postCode'
            }
        };
        this.$scope.postCodeValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 10 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.validateAndSave = () => {
            let result = ValidationEngine.getGroupConfig(this.validationGroup).validate();
            if (result.isValid) {
                this.saveInProgress = true;
                this.isSaveOperationDisable = true;

                if (!this.$scope.editMode) {
                    this.Broker.save(this.$scope.broker,
                        (result) => {
                            this.saveInProgress = false;
                            this.$state.go('broker', {id : result.id});
                            this.$uibModalInstance.close(this.$scope.broker);
                            this.notificationService.success(Messages('assets.message.created'));
                        },
                        (error) => {
                            this.notificationService.error(error.data.message ? error.data.message : error.data);
                            this.saveInProgress = false;
                        }
                    );
                } else {
                    this.Broker.update({ id: this.$scope.broker.id }, this.$scope.broker,
                        (result) => {
                            this.notificationService.success(Messages('assets.message.updated'));
                            this.saveInProgress = false;
                            this.$uibModalInstance.close(this.$scope.broker);
                        },
                        (error) => {
                            this.notificationService.error(error.data.message ? error.data.message : error.data);
                            this.saveInProgress = false;
                        }
                    );
                }
            }
        };

        this.$scope.cancel = () => {
            ValidationEngine.removeGroup(this.validationGroup);
            this.$uibModalInstance.dismiss('cancel');
        };
    }

    initAutocomplete() {
        if (!window.google) {
            return;
        }

        this.autocomplete = new google.maps.places.Autocomplete($('#broker-address-field input')[0]);
        google.maps.event.addListener(this.autocomplete, 'place_changed', () => {
            let place = this.autocomplete.getPlace(),
                citySet, streetSet, buildingSet, postCodeSet;

            if (place.address_components instanceof Array) {
                place.address_components.forEach((item) => {
                    if (item.types.indexOf('locality') !== -1) {
                        this.$scope.broker.city = item.long_name;
                        citySet = true;
                    }
                    if (item.types.indexOf('route') !== -1) {
                        this.$scope.broker.street = item.long_name;
                        streetSet = true;
                    }
                    if (item.types.indexOf('street_number') !== -1) {
                        this.$scope.broker.streetAddress = item.long_name;
                        buildingSet = true;
                    }
                    if (item.types.indexOf('postal_code') !== -1) {
                        this.$scope.broker.postCode = item.long_name;
                        postCodeSet = true;
                    }
                });
            }

            if (!citySet) {
                this.$scope.broker.city = null;
            }
            if (!streetSet) {
                this.$scope.broker.street = null;
            }
            if (!buildingSet) {
                this.$scope.broker.streetAddress = null;
            }
            if (!postCodeSet) {
                this.$scope.broker.postCode = null;
            }
            ValidationEngine.getGroupConfig(this.addressValidationGroup).validate();
        });

        if (this.$scope.broker.countryId) {
            this.setAutocompleteCountry();
        }
    }


    setAutocompleteCountry() {
        let countries = Constants.countryStorePlain(),
            curCountryCode;

        countries.forEach((country) => {
            if (country.value === this.$scope.broker.countryId) {
                curCountryCode = country.isoCode;
            }
        });

        this.autocomplete.setComponentRestrictions({
            'country': curCountryCode
        });
    }
}


