'use strict';

import Messages from '../../messages';

import UserEditModalController from '../../users/user-edit-modal.controller';
import UserEditModalTemplate from '../../users/user-edit-modal.template.html';
import BrokerEditModalController from '../../brokers/broker-edit-modal.controller';
import BrokerEditModalTemplate from '../../brokers/broker-edit-modal.template.html';
import AssetEditModalController from '../../assets/asset-edit-modal.controller';
import AssetEditModalTemplate from '../../assets/asset-edit-modal.template.html';
import ClientEditModalController from '../../clients/client-edit-modal.controller';
import ClientEditModalTemplate from '../../clients/client-edit-modal.template.html';

export default class NavigationCreateItemsController {
    /*@ngInject*/
    constructor($scope, SecurityService, Activities, $uibModal) {
        this.$scope = $scope;
        this.securityService = SecurityService;
        this.activities = Activities;
        this.$uibModal = $uibModal;
        this.messages = Messages;

        this.init();
        this.applyActivities();
    }

    init() {
        this.$scope.addNewUser = () => {
            this.$uibModal.open({
                controller: UserEditModalController,
                template: UserEditModalTemplate,
                resolve: {
                    userObject: null
                }
            });
        };

        this.$scope.addNewBroker = () => {
            this.$uibModal.open({
                controller: BrokerEditModalController,
                template: BrokerEditModalTemplate,
                resolve: {
                    brokerObject: null
                }
            });
        };

        this.$scope.addNewSet = () => {
            this.$uibModal.open({
                controller: AssetEditModalController,
                template: AssetEditModalTemplate,
                resolve: {
                    assetObject: null
                }
            });
        };

        this.$scope.addNewClient = () => {
            this.$uibModal.open({
                controller: ClientEditModalController,
                template: ClientEditModalTemplate,
                resolve: {
                    clientId: null
                }
            });
        };
    }

    applyActivities() {
        this.securityService.getActivities().then(() => {
            this.securityService.canDo(this.activities.HomeView)
                .then(
                    (result) => this.homeAvailable = result,
                    () => this.homeAvailable = false);
            this.securityService.canDo(this.activities.BrokersView)
                .then(
                    (result) => this.brokersAvailable = result,
                    () => this.brokersAvailable = false);
            this.securityService.canDo(this.activities.UsersView)
                .then(
                    (result) => this.usersAvailable = result,
                    () => this.usersAvailable = false);
            this.securityService.canDo(this.activities.AssetsManage)
                .then(
                    (result) => this.assetsAvailable = result,
                    () => this.assetsAvailable = false);
            this.securityService.canDo(this.activities.AssetsView)
                .then(
                    (result) => this.assetsViewAvailable = result,
                    () => this.assetsViewAvailable = false);
            this.securityService.canDo(this.activities.ClientsManage)
                .then(
                    (result) => this.clientsAvailable = result,
                    () => this.clientsAvailable = false);
        });
    }
}



