export default class AssetStatTenants {
    /*@ngInject*/
    constructor(ConfigurationService, $resource) {
        return $resource(ConfigurationService.getApiBaseUrl() + 'api/assets/:id/contracts/search', {id: '@id'}, {
            query: {
                method: 'GET'
            },
            getSpaceInfo: {
                method: 'GET',
                url: ConfigurationService.getApiBaseUrl() + 'api/assets/:assetId/objects/:assetObjectId/floors/:floorId/spaces/:id/full'
            }
        });
    }

    /*@ngInject*/
    static activate(ConfigurationService, $resource) {
        AssetStatTenants.instance = new AssetStatTenants(ConfigurationService, $resource);
        return AssetStatTenants.instance;
    }
}


