'use strict';

import Messages from '../../messages';
import Constants from '../../constants';

export default class SpaceDescription {
    /*@ngInject*/
    constructor($scope, $rootScope, uibDateParser, UtilsService) {
        this.$scope = $scope;
        this.$rootScope = $rootScope;
        this.messages = Messages;
        this.uibDateParser = uibDateParser;
        this.utils = UtilsService;

        this.init();
    }

    init() {
        this.$scope.data = this.data;
        this.$scope.context = this.context;
    }

    getOfficeType(type) {
        let officeType = Constants.spaceTypeStorePlain().find((item) => {
            return item.value === type;
        });
        return officeType.text;
    }

    getStatus(status) {
        switch (status) {
            case 2:
                return Messages('assets.spaces.filter.occupied');
            case 3:
                return Messages('assets.spaces.filter.vacant');
        }
    }

    getParsedDate(date) {
        return this.uibDateParser.filter(new Date(date), 'dd/MM/yyyy');
    }

    closeSidebar() {
        this.$rootScope.$broadcast('right-sidebar:close');
    }
}



