export default class Broker {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
    return $resource(ConfigurationService.getApiBaseUrl() + 'api/brokers/:id', { id: '@id' }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/brokers',
            isArray: true
        },
        update: {
            method: 'PUT'
        },
        archive: {
            method: 'DELETE'
        }
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    Broker.instance = new Broker(ConfigurationService, $resource);
    return Broker.instance;
  }
}


