import ClientController from './client.controller';
import ClientsController from './clients.controller';
import 'devextreme/integration/angular';
import Client from './client.resource';
import RepresentativesComponent from './representatives/representatives.component';
import Representative from './representatives/representatives.resource';

var module = angular.module('tower360.clients', ['dx'])
  .controller('ClientController', ClientController)
  .controller('ClientsController', ClientsController)
  .factory('Client', Client.activate)
  .component('representatives', RepresentativesComponent)
  .factory('Representative', Representative.activate);

export default module;


