import ArrayStore from 'devextreme/data/array_store';
import Messages from './messages';

const CONSTANTS = {
    devExtremeStoreGenerator: function(values) {
        let store = {
            data: [],
            key: 'value'
        };
        values.forEach(function(item) {
            store.data.push({
                value: item.value,
                display: item.text
            });
        });
        return new ArrayStore(store);
    },

    states: [
        { value: 1, text: Messages('state.pending') },
        { value: 2, text: Messages('state.active') },
        { value: 3, text: Messages('state.archived') }
    ],
    assetStateStore: function() {
        return this.devExtremeStoreGenerator(this.states);
    },
    assetStateStorePlain: function() {
        return this.states.slice(0);
    },

    userStateStore: function() {
        return new ArrayStore({
            data: [
                { value: 2, display: Messages('state.active') },
                { value: 3, display: Messages('state.archived') }
            ],
            key: "value"
        })
    },

    brokerStateStore: function() {
        return new ArrayStore({
            data: [
                { value: 1, display: Messages('state.active') },
                { value: 2, display: Messages('state.archived') }
            ],
            key: "value"
        })
    },

    countries: [{
        value: 1,
        text: Messages('country.germany'),
        isoCode: 'de'
    }],
    countryStore: function() {
        return this.devExtremeStoreGenerator(this.countries);
    },
    countryStorePlain: function() {
        return this.countries.slice(0);
    },

    clientsStateStore: function() {
        return new ArrayStore({
            data: [
                { value: 1, display: Messages('state.active') },
                { value: 2, display: Messages('state.archived') }
            ],
            key: "value"
        })
    },

    industry: [
        { value: 1, text: Messages('industry.technology') },
        { value: 2, text: Messages('industry.media') },
        { value: 3, text: Messages('industry.education') },
        { value: 4, text: Messages('industry.trade') },
        { value: 5, text: Messages('industry.government') },
        { value: 6, text: Messages('industry.healthcare') },
        { value: 7, text: Messages('industry.logistics') },
        { value: 8, text: Messages('industry.consulting') },
        { value: 9, text: Messages('industry.retail') },
        { value: 10, text: Messages('industry.telco') },
        { value: 11, text: Messages('industry.food') },
        { value: 12, text: Messages('industry.finance') },
        { value: 13, text: Messages('industry.residential') },
        { value: 14, text: Messages('industry.travel') },
        { value: 15, text: Messages('industry.wellness') },
        { value: 16, text: Messages('industry.hr') },
        { value: 17, text: Messages('industry.real-estate') },
        { value: 18, text: Messages('industry.industrials') }
    ],
    clientsIndustryStore: function() {
        return this.devExtremeStoreGenerator(this.industry);
    },
    clientsIndustryPlain: function() {
        return this.industry;
    },

    defaultAvailablePageSizes: function() {
        return [10, 20, 50];
    },

    defaultPageSize: function() {
        return 10;
    },

    defaultGridSettings: function() {
        var settings = {
            allowColumnResizing: true,
            showBorders: true,
            showRowLines: true,
            paging: {
                enabled: true,
                pageSize: this.defaultPageSize()
            },
            pager: {
                showPageSizeSelector: true,
                allowedPageSizes: this.defaultAvailablePageSizes(),
                showInfo: true
            },
            filterRow: {
                visible: true
            },
            grouping: {
                contextMenuEnabled: true
            },
            groupPanel: {
                visible: true
            }
        };

        return JSON.parse(JSON.stringify(settings));
    },

    spaceTypes: [
        { value: 1, text: Messages('assets.spaces.office') },
        { value: 2, text: Messages('assets.spaces.garageUnderground') },
        { value: 3, text: Messages('assets.spaces.roofTerrace') },
        { value: 4, text: Messages('assets.spaces.outerSurface') },
        { value: 5, text: Messages('assets.spaces.warehouse') },
        { value: 6, text: Messages('assets.spaces.handlesSurface') },
        { value: 7, text: Messages('assets.spaces.gastronomy') },
        { value: 8, text: Messages('assets.spaces.archive') },
        { value: 9, text: Messages('assets.spaces.technicalRoom') },
        { value: 10, text: Messages('assets.spaces.flat') },
        { value: 11, text: Messages('assets.spaces.retail') }
    ],
    spaceTypeStore: function() {
        return this.devExtremeStoreGenerator(this.spaceTypes);
    },
    spaceTypeStorePlain: function() {
        return this.spaceTypes.slice(0);
    },

    spaceStateStore: function() {
        return new ArrayStore({
            data: [
                { value: 1, display: Messages('state.active') },
                { value: 2, display: Messages('state.archived') }
            ],
            key: "value"
        })
    },

    phonePattern: function() {
        return /^\(\d{3}\)\d{3}-\d{4}$/i;
    }
};

export default CONSTANTS;



