// import template and controller from individual component directory
import Controller from './attachements.controller';
import Template from './attachements.template.html';

let AttachementsComponent = {
    bindings: {
        documents: '=',
        maxAttachementSize: '=?',
        maxAttachementCount: '=?',
        readonly: '=?',
        documentCreateCallback: '=?',
        documentAddCallback: '=?',
        documentDeleteCallback: '=?'
    },
    controller: Controller,
    template: Template
}

export default AttachementsComponent;




