import ValidationEngine from 'devextreme/ui/validation_engine';
import Messages from '../../messages';
import Constants from '../../constants';
import ArrayStore from 'devextreme/data/array_store';
import DataSource from 'devextreme/data/data_source';

export default class SpaceController {
    /*@ngInject*/
    constructor($scope, asset, assetObjectId, floorId, spaceId, AssetObject, Floor, Space, NotificationService, UtilsService, $uibModalInstance) {
        // from component
        this.$scope = $scope;
        this.asset = asset;
        this.assetObjectId = assetObjectId;
        this.floorId = floorId;
        this.spaceId = spaceId;

        this.utils = UtilsService;

        this.Messages = Messages;

        // injected
        this.AssetObject = AssetObject;
        this.Floor = Floor;
        this.Space = Space;
        this.NotificationService = NotificationService;
        this.$uibModalInstance = $uibModalInstance;

        this.validationGroup = 'spaces-add';

        // initialization
        this.init();
        this.initDataSource(t => { this.initForm(); });
        self.objects = [];
        self.floors = [];
    }

    init() {
        let self = this;

        self.$scope.asset = self.asset;
        self.$scope.Messages = Messages;
        self.$scope.getDataInProgress = false;
        self.$scope.editMode = self.spaceId > 0;

        self.errorHandler = {
            requestFailed: function (response) {
                self.NotificationService.error(response.data);
            }
        };

        if (self.spaceId > 0) {
            this.$scope.getDataInProgress = true;
            self.Space.getFull({assetId: self.asset.id, assetObjectId: self.assetObjectId, floorId: self.floorId, id: self.spaceId},
                result => {
                    self.space = result;
                    self.$scope.space = self.space;
                    this.$scope.getDataInProgress = false;
                },
                error => {
                    this.NotificationService.error(error.data.message ? error.data.message : error.data);
                });
        } 
        else {
            self.space = {
                id: 0,
                uniqueId: self.utils.generateGuid(),
                number: '',
                type: 1,
                rentableArea: null,
                assetId: self.asset.id,
                assetObjectId: null,
                floorId: null,
                brokerId: self.asset.brokerId,
                marketingDescription: '',
                notes: ''
            };
            self.$scope.space = self.space;
            
        }

        self.spaceTypeStore = Constants.spaceTypeStore();
        self.spaceStateStore = Constants.spaceStateStore();
    }

    loadAssetObjects(callback){
        let self = this;
        self.AssetObject.query({ assetId: self.asset.id },
            (result) => {
                self.objects = result;
                if(callback) {
                    callback();
                }
            },
            (error) => { });
    }

    loadFloors(assetObjectId, callback){
        let self = this;
        self.Floor.query({ assetId: self.asset.id, assetObjectId: assetObjectId },
            (result) => {
                self.floors = result;
                if(callback) {
                    callback();
                }
            },
            (error) => { });
    }

    initDataSource(callback) {
        let self = this;

        self.loadAssetObjects(t => {
            self.assetObjectStore = new ArrayStore({
                data: self.objects,
                key: "id",
                errorHandler: self.errorHandler.requestFailed
            });

            self.floorStore = new DataSource({
                load: function(loadOptions) {
                    return self.floors;
                },
                byKey: function(key) {
                    return { id: key };
                },
                errorHandler: self.errorHandler.requestFailed
            });

            if(callback){
                callback();
            }
        })
    }

    initForm() {
        let self = this;  

        self.$scope.numberSettings = {
            placeholder: Messages('assets.spaces.placeholder.number'),
            bindingOptions: {
                value: 'space.number'
            }
        };
        self.$scope.numberValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'stringLength', max: 20, message: Messages('validation.maxLength') },
            ],
            validationGroup: self.validationGroup
        };

        self.$scope.typeSelectSettings = {
            dataSource: self.spaceTypeStore,
            displayExpr: 'display',
            valueExpr: 'value',
            bindingOptions: {
                value: 'space.type'
            }
        };

        self.$scope.typeValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') }
            ],
            validationGroup: self.validationGroup
        };

        self.$scope.rentableAreaSettings = {
            placeholder: Messages('assets.spaces.placeholder.rentableArea'),
            bindingOptions: { value: 'space.rentableArea' },
            value: 1,
            showSpinButtons: true,
            showClearButton: false,
            max: 1000,
            min: 1
        };

        self.$scope.rentableAreaValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'numeric' },
                { type: 'range', min: 0 }
            ],
            validationGroup: self.validationGroup
        };

        self.$scope.floorSelectSettings = {
            dataSource: self.floorStore,
            displayExpr: 'number',
            valueExpr: 'id',
            bindingOptions: {
                value: 'space.floorId',
                disabled: 'editMode'
            }
        };

        self.$scope.floorValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') }
            ],
            validationGroup: this.validationGroup
        };

        self.$scope.assetObjectSelectSettings = {
            dataSource: self.assetObjectStore,
            displayExpr: 'name',
            valueExpr: 'id',
            bindingOptions: {
                value: 'space.assetObjectId',
                disabled: 'editMode'
            },
            onSelectionChanged: function(e){
                self.loadFloors(e.selectedItem.id, (t) => {
                    self.floorStore.load().done(() => {
                        if(self.assetObjectId != self.space.assetObjectId) {
                            self.space.floorId = null;
                            self.assetObjectId = self.space.assetObjectId;
                        }

                        $("#field-floor").dxSelectBox("instance").repaint();
                    });
                });
            }
        };

        this.$scope.assetObjectValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') }
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.marketingDescriptionSettings = {
            placeholder: Messages('assets.spaces.placeholder.marketingDescription'),
            height: 180,
            maxLength: 500,
            bindingOptions: {
                value: 'space.marketingDescription'
            }
        };
        this.$scope.marketingDescriptionValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 500 },
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.notesSettings = {
            placeholder: Messages('assets.spaces.placeholder.notes'),
            height: 180,
            maxLength: 500,
            bindingOptions: { value: 'space.notes' }
        };
        this.$scope.notesValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 500 },
            ],
            validationGroup: this.validationGroup
        };

        self.$scope.minAskingPriceSettings = {
            placeholder: Messages('assets.spaces.placeholder.minAskingPrice'),
            bindingOptions: {
                value: 'space.minAskingPrice'
            }
        };
        self.$scope.minAskingPriceValidationRules = {
            validationRules: [
                { type: 'numeric' },
                { type: 'range', min: 0 },
                {
                    type: 'custom',
                    validationCallback: function(options) {
                        if (!options.value)
                            return true;
                        if (!self.space.maxAskingPrice)
                            return true;

                        options.rule.message = Messages('assets.spaces.validation.maxAskingPrice');

                        return (options.value < self.space.maxAskingPrice);
                    }
                }
            ],
            validationGroup: self.validationGroup
        };

        self.$scope.maxAskingPriceSettings = {
            placeholder: Messages('assets.spaces.placeholder.maxAskingPrice'),
            bindingOptions: {
                value: 'space.maxAskingPrice'
            }
        };
        self.$scope.maxAskingPriceValidationRules = {
            validationRules: [
                { type: 'numeric' },
                { type: 'range', min: 0 },
                {
                    type: 'custom',
                    validationCallback: function(options) {
                        if (!options.value)
                            return true;
                        if (!self.space.minAskingPrice)
                            return true;

                        options.rule.message = Messages('assets.spaces.validation.maxAskingPrice');

                        return (options.value > self.space.minAskingPrice);
                    }
                }
            ],
            validationGroup: self.validationGroup
        };

        this.$scope.validateAndSave = function() {
            let result = ValidationEngine.getGroupConfig(self.validationGroup).validate();
            if (result.isValid) {
                self.$uibModalInstance.close({
                    space: self.$scope.space
                });
            }
        };

        this.$scope.cancel = function () {
            ValidationEngine.removeGroup(self.validationGroup);
            self.$uibModalInstance.dismiss('cancel');
        };
    }
}


