import Messages from '../../messages';
import Constants from '../../constants';
import ArrayStore from 'devextreme/data/array_store';
import CustomStore from 'devextreme/data/custom_store';
import DataSource from 'devextreme/data/data_source';
import SpaceController from './space.controller';
import SpaceTemplate from './space.template.html';

export default class SpacesController {
    /*@ngInject*/
    constructor($scope, AssetObject, Floor, Space, NotificationService, UtilsService, sweetAlert, $uibModal) {
        // from component
        this.asset = this.asset;

        // injected
        this.$scope = $scope;
        this.NotificationService = NotificationService;
        this.utils = UtilsService;
        this.sweetAlert = sweetAlert;
        this.$uibModal = $uibModal;
        this.messages = Messages;

        this.AssetObject = AssetObject;
        this.Floor = Floor;
        this.Space = Space;

        this.loadData((t) => {
            this.init(); 
            this.initGrid();
        });
    }

    init() {
        let self = this;
        self.$scope.$on('updateAssetObjectDataSource', function(e,data) {
            self.updateDataSources();
        });

        self.$scope.$on('updateFloorDataSource', function(e,data) {
            self.updateDataSources();
        });

        self.updateDataSources = function(){
            self.loadData((t) => {
                var dataGrid = $("#spacesGridContainer").dxDataGrid('instance');
                dataGrid.beginUpdate();
                dataGrid.columnOption('floorId', 'lookup.dataSource', {store: self.floorStore });
                dataGrid.columnOption('assetObjectId', 'lookup.dataSource', {store: self.assetObjectStore });
                dataGrid.option('dataSource', []);
                dataGrid.option('dataSource', {store: self.createStore()});
                dataGrid.endUpdate(); 

                self.refreshGrid();
            });
        }
    }
    loadData(afterExecuteCallback) {
        let self = this;

        self.loadAssetObjects(() => {
            self.loadFloors(() => {
                self.loadSpaces(() => {
                    if(afterExecuteCallback)
                        afterExecuteCallback();         
                });
            })
        });
    }

    loadAssetObjects(afterExecuteCallback){
        let self = this;

        self.AssetObject.query({ assetId: self.asset.id },
            (result) => {
                self.objects = result;

                if(afterExecuteCallback){
                    afterExecuteCallback();
                }
                
            },
            (error) => { });
    }

    loadFloors(afterExecuteCallback){
        let self = this;
        
        self.Floor.getByAssetId({ assetId: self.asset.id },
            (result) => {
                self.floors = result;

                if(afterExecuteCallback){
                    afterExecuteCallback();
                }
            },
            (error) => { }
        );
    }

    loadSpaces(afterExecuteCallback){
        let self = this;
        
        self.Space.getByAssetId({ assetId: self.asset.id },
            (result) => {
                self.spaces = result;
                
                if(afterExecuteCallback){
                    afterExecuteCallback();
                }
            },
            (error) => { });
    }

    createStore(){
        let self = this;

        self.store = new ArrayStore({
            data: self.spaces,
            key: "id",
            modified: function () {
                // 'modified' event handler
            },
            errorHandler: self.errorHandler.requestFailed
        });

        return self.store;
    }

    initGrid() {
        let self = this;

        self.errorHandler = {
            requestFailed: function (response) {
                self.NotificationService.error(response.data);
            }
        };

        self.spaceTypeStore = Constants.spaceTypeStore();

        self.createStore();

        self.floorStore = new CustomStore({
            load: function (loadOptions) {
                return self.floors;
            },
            byKey: function(key) {
                return { id: key };
            }
        })

        self.assetObjectStore = new CustomStore({
            load: function (loadOptions) {
                return self.objects;
            },
            byKey: function(key) {
                return { id: key };
            }
        })

        self.gridDataSource = self.store;

        self.gridSettings = $.extend(Constants.defaultGridSettings(), {
            dataSource: {store: self.store},
            filterRow: { visible: false },
            groupPanel: { visible: false },
            columns: [
                {
                    dataField: 'number',
                    caption: Messages('assets.spaces.number'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    sortOrder: 'asc',
                    alignment: 'right'
                },
                {
                    dataField: 'type',
                    caption: Messages('assets.spaces.type'),
                    // width: 100,
                    lookup: {
                        dataSource: self.spaceTypeStore,
                        displayExpr: 'display',
                        valueExpr: 'value'
                    }
                },
                {
                    dataField: 'rentableArea',
                    dataType: 'number',
                    caption: Messages('assets.spaces.rentableArea'),
                    filterOperations: ['>', '<', '='],
                    selectedFilterOperation: '>',
                    alignment: 'right'
                },
                {
                    dataField: 'floorId',
                    caption: Messages('assets.spaces.floor'),
                    // width: 100,
                    lookup: {
                        dataSource: {store: self.floorStore},
                        displayExpr: 'number',
                        valueExpr: 'id'
                    },
                    alignment: 'right',
                    allowEditing: false
                },
                {
                    dataField: 'assetObjectId',
                    caption: Messages('assets.spaces.assetobject'),
                    width: 100,
                    lookup: {
                        dataSource: {store: self.assetObjectStore},
                        displayExpr: 'name',
                        valueExpr: 'id'
                    },
                    alignment: 'right',
                    allowEditing: false
                },
                {
                    dataField: "id",
                    caption: Messages('actions'),
                    width: 100,
                    alignment: 'right',
                    cellTemplate: function (container, options) {
                        container.addClass("actions-cell");

                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-pencil"></div>',
                            onClick: function(e) {
                                self.updateSpace(options.data);
                            }
                        }).appendTo(container);

                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-trash-o"></div>',
                            onClick: function(e) {
                                self.deleteSpace(options.data);
                            }
                        }).appendTo(container);
                    }}
                ]
        });
    }

    createSpace() {
        var modal = this.$uibModal.open({
            controller: SpaceController,
            template: SpaceTemplate,
            resolve: {
                asset: this.asset,
                assetObjectId: null,
                floorId: null,
                spaceId: 0
            },
            size: 'lg'
        });
        modal.result.then(result => {
            
            this.Space.create({ assetId: this.asset.id, assetObjectId: result.space.assetObjectId, floorId: result.space.floorId }, result.space,
                (result) => {
                    this.spaces.push(result);
                    this.NotificationService.success(Messages('assets.spaces.created'));
                    this.saveInProgress = false;

                    this.refreshGrid();
                },
                (error) => {
                    console.log(error);
                    this.NotificationService.error(error.data);
                    this.saveInProgress = false;
                }
            );
        });

        modal.closed.then(() => {
            //this.refreshGrid();
        });

        this.space = null;
    }

    updateSpace(space) {
        let self = this;

        var modal = self.$uibModal.open({
            controller: SpaceController,
            template: SpaceTemplate,
            resolve: {
                asset: self.asset,
                assetObjectId: space.assetObjectId,
                floorId: space.floorId,
                spaceId: space.id
            },
            size: 'lg'
        });
        modal.result.then(function(result) {
            jQuery.extend(space, result.space);

            self.Space.update({ assetId: self.asset.id, assetObjectId: result.space.assetObjectId, floorId: result.space.floorId, id: result.space.id }, result.space,
                (result) => {
                    self.NotificationService.success(Messages('assets.spaces.updated'));
                    self.saveInProgress = false;
                },
                (error) => {
                    console.log(error);
                    self.NotificationService.error(error.data);
                    self.saveInProgress = false;
                }
            );
        });
        modal.closed.then(function() {

        });
    }

    deleteSpace(space) {
        let self = this;
        self.sweetAlert.swal(
            {
                title: Messages('assets.spaces.delete.title'),
                type: 'warning',
                showCancelButton: true,
                cancelButtonText: Messages('assets.cancel'),
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('assets.spaces.delete.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    self.Space.delete({ assetId: self.asset.id, assetObjectId: space.assetObjectId, floorId: space.floorId, id: space.id },
                        (result) => {
                            var spaceIdx = self.spaces.findIndex(t => t.id == space.id);
                            self.spaces.splice(spaceIdx, 1);

                            self.refreshGrid();
                            self.NotificationService.success(Messages('assets.spaces.deleted'));
                            self.saveInProgress = false;
                        },
                        (error) => {
                            console.log(error);
                            self.NotificationService.error(error.data.message ? error.data.message : error.data);
                            self.saveInProgress = false;
                        }
                    );
                }
            }
        );
    }

    refreshGrid() {
        $("#spacesGridContainer").dxDataGrid('instance').refresh();
    }
}


