import ArrayStore from 'devextreme/data/array_store';
import AssetEditModalController from './asset-edit-modal.controller';
import AssetEditModalTemplate from './asset-edit-modal.template.html';
import Messages from '../messages';
import Constants from '../constants';

export default class AssetsController {
    /*@ngInject*/
    constructor($scope, $state, Asset, Broker, StoreAdapter, currentUser, NotificationService, $uibModal, sweetAlert, UtilsService) {
        this.$scope = $scope;
        this.$state = $state;
        this.Asset = Asset;
        this.Broker = Broker;
        this.storeAdapter = StoreAdapter;
        this.currentUser = currentUser;
        this.notificationService = NotificationService;
        this.$uibModal = $uibModal;
        this.sweetAlert = sweetAlert;
        this.utils = UtilsService;

        this.initGrid();
    }

    initGrid() {
        let self = this;

        self.errorHandler = {
            requestFailed: function (response) {
                this.NotificationService.error(response.data);
            }
        };

        self.stateStore = Constants.assetStateStore();
        self.countryStore = Constants.countryStore();

        self.assets = [];

        self.Asset.query(
            {},
            (result) => {
                self.assets = result;

                self.assets.forEach(item => {
                    item.address = self.utils.generateAddress(item);
                });

                self.store = new ArrayStore({
                    data: self.assets,
                    key: "id",
                    modified: function () {
                        // 'modified' event handler
                    },
                    errorHandler: self.errorHandler.requestFailed
                });

                self.gridDataSource = { store: self.store };

                self.gridSettings = $.extend(Constants.defaultGridSettings(), {
                    dataSource: self.gridDataSource,
                    editing: {
                        allowAdding: false,
                        allowUpdate: false,
                        allowDelete: false,
                        texts: {
                            confirmDeleteMessage: ''
                        }
                    },
                    columns: [{
                        dataField: "id",
                        visible: false
                    }, {
                        dataField: "name",
                        caption: Messages('assets.name'),
                        sortOrder: "asc",
                        filterOperations: ['contains', 'startswith', '='],
                        selectedFilterOperation: 'contains',
                        cellTemplate: function(cellElement, cellInfo) {
                            let element = $('<a>', {
                                'href': '/assets/' + cellInfo.data.id,
                                'ui-sref': 'asset'
                            });

                            element.text(cellInfo.displayValue)
                                   .appendTo(cellElement);
                        }
                    }, {
                        dataField: "address",
                        caption: Messages('assets.address'),
                        filterOperations: ['contains', 'startswith', '='],
                        selectedFilterOperation: 'contains',
                    }, {
                        dataField: "countryId",
                        caption: Messages('assets.country'),
                        lookup: {
                            dataSource: self.countryStore,
                            displayExpr: 'display',
                            valueExpr: 'value'
                        },
                    }, {
                        dataField: "state",
                        caption: Messages('users.state'),
                        lookup: {
                            dataSource: self.stateStore,
                            displayExpr: 'display',
                            valueExpr: 'value'
                        },
                        width: 100
                    },
                    {
                        dataField: "brokerDatabaseRecordId",
                        caption: Messages('assets.brokerDatabaseRecordId'),
                        filterOperations: ['contains', 'startswith', '='],
                        selectedFilterOperation: 'contains',
                        alignment: 'right'
                    }, {
                        dataField: "brokerId",
                        caption: Messages('assets.broker'),
                        lookup: {
                            dataSource: this.storeAdapter.createStore(this.Broker, {}, self.errorHandler),
                            displayExpr: 'name',
                            valueExpr: 'id'
                        }
                    }, {
                        caption: Messages('actions'),
                        width: 90,
                        alignment: 'right',
                        cellTemplate: function (container, options) {
                            container.addClass("actions-cell");

                            $("<div />").dxButton({
                                type: 'custom',
                                template: `<div class="fa fa-pencil" title="${Messages('assets.edit')}"></div>`,
                                onClick: function(data) {
                                    data.jQueryEvent.stopPropagation();
                                    let modal = self.$uibModal.open({
                                        controller: AssetEditModalController,
                                        template: AssetEditModalTemplate,
                                        resolve: {
                                            assetObject: Object.assign({}, options.data)
                                        }
                                    });
                                    modal.result.then((result) => {
                                        result.address = self.utils.generateAddress(result);
                                        jQuery.extend(options.data, result);
                                    });
                                }
                            }).appendTo(container);

                            let isArchived = options.data.state === 3;
                            $("<div />").dxButton({
                                type: 'custom',
                                template: `<div class="fa ${isArchived ? 'fa-undo' : 'fa-trash-o'}" title="${isArchived ? Messages('assets.activate') : Messages('assets.archive')}"></div>`,
                                onClick: function(data) {
                                    if (isArchived) {
                                        self.activateAsset(options.data);
                                    } else {
                                        self.archiveAsset(options.data);
                                    }
                                }
                            }).appendTo(container);
                        }
                    }]
                });
            }
        );
    }

    archiveAsset(asset) {
        let self = this;
        this.sweetAlert.swal(
            {
                title: Messages('assets.archive.title'),
                type: 'warning',
                showCancelButton: true,
                cancelButtonText: Messages('assets.cancel'),
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('assets.archive.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    self.Asset.archive({ id: asset.id },
                        function (result) {
                            self.store.remove(asset.id);
                            result.address = self.utils.generateAddress(result);
                            self.store.insert(result);

                            $('#gridContainer').dxDataGrid('instance').refresh();
                            self.notificationService.success(Messages('assets.message.archived'));
                        },
                        function (error) {
                            self.notificationService.error(error.data.message ? error.data.message : error.data);
                        });
                }
            }
        );
    }

    activateAsset(asset) {
        asset = Object.assign({}, asset);

        let self = this;
        this.sweetAlert.swal(
            {
                title: Messages('assets.activate.title'),
                type: 'warning',
                showCancelButton: true,
                cancelButtonText: Messages('assets.cancel'),
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('assets.activate.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    asset.state = 2;
                    self.Asset.update({ id: asset.id }, asset,
                        function(result) {
                            self.store.remove(asset.id);
                            result.address = self.utils.generateAddress(result);
                            self.store.insert(result);
                            $('#gridContainer').dxDataGrid('instance').refresh();
                            self.notificationService.success(Messages('assets.message.activated'));
                        },
                        function(error) {
                            self.notificationService.error(error.data.message ? error.data.message : error.data);
                        });
                }
            }
        );
    }
}


