export default class AssetStats {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
    return $resource(ConfigurationService.getApiBaseUrl() + 'api/assets/:id', { id: '@id' }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/assets',
            isArray: true
        },
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    AssetStats.instance = new AssetStats(ConfigurationService, $resource);
    return AssetStats.instance;
  }
}


