import Messages from '../messages';

export default class AssetStatsController {
    /*@ngInject*/
    constructor($scope, $state, StoreAdapter, AssetStats, NotificationService) {
        this.$scope = $scope;
        this.$state = $state;
        this.storeAdapter = StoreAdapter;
        //this.currentUser = currentUser;
        this.notificationService = NotificationService;

        this.Messages = Messages;

        this.assetStats = AssetStats;

        this.init();

        this.isMore = false;
        this.page = 0;
        this.pageSize = 8;
        this.loadedImages = [];
        this.loadedImagesCount = 0;
        this.totalRowsCount = 0;
    }

    init() {
        this.errorHandler = {
            requestFailed: function (response) {
                this.NotificationService.error(response.data);
            }
        };

        this.assetStats.query({
            pageNumber: this.page
        }, (result) => {
            let assets = [],
                rowIndex = 0;

            result.forEach((item, i) => {
                if (i % 4 === 0 && i !== 0) {
                    rowIndex++;
                }
                if (!assets[rowIndex]) {
                    assets[rowIndex] = [];
                }
                item.color = this.getPanelColor();
                assets[rowIndex].push(item);
            });

            this.assets = assets;
            this.assetsLength = result.length;
            this.totalRowsCount = result.length;
        }, (error) => {

        });
    }


    loadMore() {
        this.page++;

        this.assetStats.query({
            pageNumber: this.page
        }, (result) => {
            this.assets = this.assets.concat(result.items);
        }, (error) => {

        });
    }

    imageLoad() {
        this.loadedImagesCount++;
        if (this.loadedImagesCount === this.assetsLength) {
            this.loadedImages[this.page] = true;
            this.isMore = this.assetsLength < this.totalRowsCount;
            this.$scope.$apply();
        }
    }

    isShow(index) {
        return this.loadedImages[Math.floor(index / this.pageSize)];
    }

    getPanelColor() {
        let panelColors = ['hgreen', 'hblue', 'hyellow', 'hviolet', 'horange', 'hred', 'hreddeep', 'hnavyblue'];
        return panelColors[Math.floor(Math.random() * panelColors.length)];
    }
}



