import ArrayStore from 'devextreme/data/array_store';
import Messages from '../../messages';
import ImageCropperController from './imagecropper.controller';
import ImageCropperTemplate from './imagecropper.template.html';

export default class ImageGalleryController {
    /*@ngInject*/
    constructor($scope, $uibModal, FileService, ImageService, UtilsService) {
        // from component
        this.images = this.images;
        this.maxImageWidth = this.maxImageWidth || 500;
        this.maxImageHeight = this.maxImageHeight || 500;
        this.maxImageCount = this.maxImageCount || 10;
        this.readonly = this.readonly || false;
        this.imageAddCallback = this.imageAddCallback;
        this.imageDeleteCallback = this.imageDeleteCallback;

        // injected
        this.$scope = $scope;
        this.$uibModal = $uibModal;
        this.fileService = FileService;
        this.imageService = ImageService;
        this.utils = UtilsService;

        this.init();
        this.initComponent();
    }

    init(){
        let self = this;

        for (var i = 0; i < self.images.length; i++){
            if(!self.images[i].uniqueId) {
                self.images[i].uniqueId = self.utils.generateGuid();
            }
        }

        self.enableUpload = true;
        self.imageCount = self.images.length;

        self.$scope.images = self.images;
        self.$scope.readonly = self.readonly;
    }
    initComponent() {
        let self = this;

        self.addImageBtnConfig = {
            icon: 'add',
            type: 'default',
            text: Messages('users.images.add'),
            bindingOptions: {
                disabled: '!$ctrl.enableUpload'
            }
        };

        self.uploadImage = function() {
            let self = this;

            if(self.readonly) {
                return;
            }

            var modal = this.$uibModal.open({
                controller: ImageCropperController,
                template: ImageCropperTemplate,
                resolve: {
                    maxImageHeight: self.maxImageHeight,
                    maxImageWidth: self.maxImageWidth,
                    viewPortType: function() { return 'square'; }
                }
                //size: 'lg'
            });
            modal.result.then(result => {
                if(self.imageCount >= self.maxImageCount){
                    self.checkImageLimit();
                    return;
                }

                self.imageCount++;
                
                var image = self.createImage(result.file);
                image.imageFile.imageData = result.raw;
                
                self.addImage(image);
            });

            modal.closed.then(() => {

            });

            /*$("#imageUploader").val(null);

            setTimeout(function () {
                $("#imageUploader").click();
            }, 200);*/

        };

        self.addImage = function(image) {
            if(self.readonly)
                return;

            self.images.push(image);

            if (!self.$scope.$$phase && (!self.$scope.$root || !self.$scope.$root.$$phase)){
                self.$scope.$apply();
            }

            if(self.imageAddCallback){
                self.imageAddCallback(image);
            }

            self.updateImageOrder();
            self.checkImageLimit();
        };

        self.deleteImage = function (image) {
            if(self.readonly)
                return;

            for (var i = 0; i < self.images.length; i++) {
                if (self.images[i].uniqueId == image.uniqueId) {
                    self.images.splice(i, 1);
                    break;
                }
            }

            if(self.imageDeleteCallback){
                self.imageDeleteCallback(image);
            }

            self.imageCount--;

            self.updateImageOrder();
            self.checkImageLimit();
        }

        self.onImageSort = function(){
            self.updateImageOrder();
        }

        self.createImage = function (file) {
            var image = {
                id: 0,
                uniqueId: self.utils.generateGuid(),
                filePath: '',
                order: 0,
                originalFileName: file.filename,
                isMain: 0,
                contentType: file.extension,
                imageFile: {
                    data: [],
                    imageData: file.data,
                    contentType: file.extension,
                    uniqueFileName: '',
                    originalFileName: file.filename
                }
            };

            return image;
        }

        self.sortImage = function() {
            self.images.sort(function (a, b) {
                if (a.order > b.order) {
                    return 1;
                }
                if (a.order < b.order) {
                    return -1;
                }
                return 0;
            });
        };

        self.updateImageOrder = function () {
            for (var i = 0; i < self.images.length; i++) {
                self.images[i].order = i;
                self.images[i].isMain = false;
            }

            if(self.images.length > 0){
                self.images[0].isMain = true;
            }

            if (self.sortingChangeCallback) {
                self.sortingChangeCallback();
            }
        }

        self.checkImageLimit = function(){
            if(self.images.length < self.maxImageCount && !self.readonly){
                self.enableUpload = true;
            }
            else {
                self.enableUpload = false;
            }

            if (!self.$scope.$$phase && (!self.$scope.$root || !self.$scope.$root.$$phase)){
                self.$scope.$apply();
            }
        }

        self.loadFiles = function (files) {
            if(self.readonly)
                return;

            self.fileService.uploadFiles(files,
                function (singleFile) {
                    if(self.imageCount >= self.maxImageCount){
                        self.checkImageLimit();
                        return;
                    }

                    self.imageCount++;
                    
                    var image = self.createImage(singleFile);
                    self.resizeImage(image, function(imageRaw){
                        image.imageFile.imageData = imageRaw;
                        self.addImage(image);
                    })
                },
                function () {
                    self.checkImageLimit();
                })
        }

        self.resizeImage = function(image, imageResizedCallback) {
            var sourceImage = new Image();
            sourceImage.src = image.imageFile.imageData;

            var imgWidth = sourceImage.width;
            var imgHeight = sourceImage.height;

            if(sourceImage.width > self.maxImageWidth || sourceImage.height > self.maxImageHeight) {
                if (sourceImage.width > sourceImage.height) {
                    imgWidth = self.maxImageWidth;
                    imgHeight = sourceImage.height * (self.maxImageWidth / sourceImage.width);
                }
                else {
                    imgWidth = sourceImage.width * (self.maxImageHeight / sourceImage.height);
                    imgHeight = self.maxImageHeight;
                }
            }

            self.imageService.resizeImage(sourceImage, image.contentType, imgWidth, imgHeight, imageResizedCallback);
        }

        self.sortImage();

        self.$scope.addImageBtnClick = self.addImageBtnClick;
        self.$scope.deleteImage = self.deleteImage;
        self.$scope.onImageSort = self.onImageSort;
        self.$scope.Messages = Messages;

        angular.element(document).ready(function() {

            $("#imageUploader").change(function () {
                self.loadFiles($("#imageUploader")[0].files);
            });

            self.checkImageLimit();
        });
    }
}


