// import template and controller from individual component directory
import Controller from './object.controller'
import Template from './object.template.html'

let AssetObjectComponent = {
    bindings: {
        asset: '=',
        saveUpdates: '=?',
        needUpdateDataSources: '&'
    },
    controller: Controller,
    template: Template
};

export default AssetObjectComponent;


