export default class AnimatePanelDirective {
    /*@ngInject*/
    constructor($timeout, $state) {
        this.restrict = 'A';
        this.$state = $state;
        this.$timeout = $timeout;
    }

    link(scope, element, attrs) {
        let self = this;

        //Set default values for start animation and delay
        let startAnimation = 0,
            delay = 0.06,   // seconds
            start = Math.abs(delay) + startAnimation;

        // Set default values for attrs
        if (!attrs.effect) {
            attrs.effect = 'zoomIn'
        }

        if (attrs.delay) {
            delay = attrs.delay / 10
        } else {
            delay = 0.06
        }

        if (!attrs.child) {
            attrs.child = '.row > div'
        } else {
            attrs.child = "." + attrs.child
        }


        // Get all visible element and set opactiy to 0
        let panel = element.find(attrs.child);
        panel.addClass('opacity-0');

        // Wrap to $timeout to execute after ng-repeat
        self.$timeout(function () {

            // Get all elements and add effect class
            panel.addClass('animated-panel').addClass(attrs.effect);

            let panelsCount = panel.length + 10,
                animateTime = (panelsCount * delay * 10000) / 10;

            // Add delay for each child elements
            panel.each(function (i, elm) {
                start += delay;
                let rounded = Math.round(start * 10) / 10;
                $(elm).css('animation-delay', rounded + 's');
                // Remove opacity 0 after finish
                $(elm).removeClass('opacity-0');
            });

            // Clear animation after finish
            self.$timeout(function () {
                $('.stagger').css('animation', '');
                $('.stagger').removeClass(attrs.effect).removeClass('animated-panel').removeClass('stagger');
                panel.resize();
            }, animateTime)

        });
    }

    /*@ngInject*/
    static activate($timeout, $state) {
        AnimatePanelDirective.instance = new AnimatePanelDirective($timeout, $state);
        return AnimatePanelDirective.instance;
    }
};


