import ValidationEngine from 'devextreme/ui/validation_engine';
import Messages from '../messages';
import Constants from '../constants';

export default class UserChangePasswordController {
    /*@ngInject*/
    constructor($scope, $state, $uibModalInstance, User, userId, NotificationService, sweetAlert) {
        this.$scope = $scope;
        this.$state = $state;
        this.User = User;
        this.userId = userId;
        this.notificationService = NotificationService;
        this.$uibModalInstance = $uibModalInstance;
        this.validationGroup = 'user-change-password';
        this.sweetAlert = sweetAlert;

        this.initForm();
    }

    initForm() {
        let self = this;

        this.errorHandler = {
            requestFailed: function (response) {
                self.NotificationService.error(response.data);
            }
        };

        this.$scope.isSaveOperationDisable = false;

        this.$scope.changePasswordModel = {
                newPassword: null,
                confirmPassword: null
            }

        self.$scope.isModalChanged = function(){
            return self.$scope.changePasswordModel.newPassword != null && self.$scope.changePasswordModel.confirmPassword != null;
        }

        self.comprasionTarget = function() {
            if (self.$scope.changePasswordModel.newPassword) {
                return self.$scope.changePasswordModel.newPassword;
            }
        }

        self.$scope.isFormValid = function(){
            let result = ValidationEngine.getGroupConfig(self.validationGroup).validate();
            return result.isValid;
        }

        this.$scope.newPasswordSettings = {
            placeholder: Messages('users.placeholder.newPassword'),
            bindingOptions: {
                value: 'changePasswordModel.newPassword '
            },
            mode: 'password' 
        };
        this.$scope.newPasswordValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'stringLength', min: 6, message :Messages('validation.shortPassword') }
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.confirmPasswordSettings = {
            placeholder: Messages('users.placeholder.confirmPassword'),
            bindingOptions: {
                value: 'changePasswordModel.confirmPassword '
            },
            mode: 'password' 
        };
        this.$scope.confirmPasswordValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'compare',  comparisonTarget: self.comprasionTarget, message: Messages('validation.passwordsNotMatch') }
            ],
            validationGroup: this.validationGroup
        };

        this.$scope.validateAndSave = () => {
            let result = ValidationEngine.getGroupConfig(this.validationGroup).validate();
            let self = this;
            if (result.isValid) {
                self.$scope.saveInProgress = true;
                self.$scope.isSaveOperationDisable = true;

                this.User.changePassword({ id: this.userId }, this.$scope.changePasswordModel,
                        (result) => {
                            this.notificationService.success(Messages('users.message.updated'));
                            self.$scope.saveInProgress = false;
                            this.$uibModalInstance.close(this.$scope.user);
                            this.sweetAlert.swal(
                                {
                                    title: Messages('users.changePassord.title'),
                                    text: Messages('users.changePassword.success'),
                                    type: 'success',
                                });
                        },
                        (error) => {
                            this.notificationService.error(error.data.message ? error.data.message : error.data);
                            self.$scope.saveInProgress = false;
                            self.$scope.isSaveOperationDisable = false;
                            this.sweetAlert.swal(
                                {
                                    title: Messages('users.changePassord.title'),
                                    text: Messages('users.changePassword.failed'),
                                    type: 'warning',
                                });
                        }
                    );
            }
        };

        this.$scope.cancel = () => {
            ValidationEngine.removeGroup(this.validationGroup);
            this.$uibModalInstance.dismiss('cancel');
        };
    }

    
}


