import ArrayStore from 'devextreme/data/array_store';
import Messages from '../../messages';
import Croppie from 'croppie';
import 'croppie/croppie.css';

export default class ImageCropperController {
    /*@ngInject*/
    constructor($scope, $rootScope, $uibModalInstance, Upload, maxImageHeight, maxImageWidth, viewPortType) {
        // from component

        // injected
        this.$scope = $scope;
        this.$rootScope = $rootScope;
        this.$uibModalInstance = $uibModalInstance;

        this.maxImageHeight = maxImageHeight;
        this.maxImageWidth = maxImageWidth;
        this.viewPortType = viewPortType || 'square';

        this.$croppie = null;

        this.init();
    }

    init(){
        let self = this;

        this.$scope.fileSelected = function(files, events, b) {
            if(files && files.length > 0){
                var reader = new FileReader();
                reader.onload = function (evt) {
                    self.cropImage(evt.target.result);
                };
                reader.readAsDataURL(files[0]);
            }
        }

        this.$scope.uploadImage = function() {
            $("#upload").val(null);

            setTimeout(function () {
                $("#upload").click();
            }, 200);

        }

        this.$scope.save = function() {
            self.$croppie.result({
				type: 'canvas',
				size: { 
                    width: self.maxImageWidth,
                    height: self.maxImageHeight 
                },
                format: self.getImageType(self.$scope.picFile)
			}).then(function (resp) {
                self.$uibModalInstance.close({
                    file: self.$scope.picFile,
                    raw: resp
                });
			});
        };

        this.$scope.cancel = function () {
            self.$uibModalInstance.dismiss('cancel');
        };
        
        this.cropImage = function(file) {
            let self = this;

            setTimeout(function () {
                self.$croppie.bind({ url: file, zoom: 0 });
            }, 50);
        }

        this.getImageType = function(file){
            if(file.name.split('.').pop().toLowerCase() == 'png') {
                return 'png'
            }
            else {
                return 'jpeg';
            }
        }

        angular.element(document).ready(function(){
            var el = document.getElementById('cropPanel');

            self.$croppie = new Croppie(el, {
                enableExif: true,
                viewport: { width: 200, height: 200, type: self.viewPortType },
                boundary: { width: 300, height: 300 },
                mouseWheelZoom: true,
                showZoomer: true,
                enableOrientation: true,
                enforceBoundary: true
            });

            $("#upload").change(function () {
                if($("#upload")[0].files && $("#upload")[0].files.length > 0) {
                    self.$scope.picFile = $("#upload")[0].files[0];
                    self.$scope.fileSelected($("#upload")[0].files);

                    if(!self.$rootScope.$$phase)
                        self.$scope.$apply();  
                }
            });
        });
    }
}


