import BrokerEditModalController from './broker-edit-modal.controller';
import BrokerEditModalTemplate from './broker-edit-modal.template.html';
import Messages from '../messages';
import Constants from '../constants';

export default class BrokerController {
    /*@ngInject*/
    constructor($scope, $state, Broker, brokerObject, NotificationService, sweetAlert, $filter, $uibModal, NgMap, UtilsService) {
        this.$scope = $scope;
        this.$state = $state;
        this.Broker = Broker;
        this.brokerObject = brokerObject;
        this.notificationService = NotificationService;
        this.sweetAlert = sweetAlert;
        this.$uibModal = $uibModal;
        this.NgMap = NgMap;
        this.utils = UtilsService;
        this.$filter = $filter;

        this.init();
    }

    init() {
        let self = this;

        this.$scope.isArchived = this.brokerObject.state === 2;

        this.brokerObject.address = this.utils.generateAddress(this.brokerObject);

        this.NgMap.getMap().then(map => {
            this.map = map;
            if (!this.map.marker) 
                this.map.marker = new google.maps.Marker();

            this.map.marker.setMap(null);

            let stylez = [{
                featureType: 'all',
                elementType: 'all',
                stylers: [{
                    saturation: -100  // made the map as grayscale
                }]
            }];
            let mapType = new google.maps.StyledMapType(stylez, {
                name: 'Grayscale'
            });

            map.mapTypes.set('tehgrayz', mapType);
            map.setMapTypeId('tehgrayz');

            this.setMapCoordinates();
        });
    }

    editBroker() {
        let modal = this.$uibModal.open({
            controller: BrokerEditModalController,
            template: BrokerEditModalTemplate,
            resolve: {
                brokerObject: Object.assign({}, this.brokerObject)
            }
        });
        modal.result.then((result) => {
            let newAddress = this.utils.generateAddress(result),
                newCountry = result.countryId,
                needToUpdateMap;

            if (newAddress !== this.brokerObject.address || newCountry !== this.brokerObject.countryId) {
                needToUpdateMap = true;
            }
            
            jQuery.extend(this.brokerObject, result);
            this.$scope.isArchived = result.state === 2;

            this.brokerObject.address = newAddress;

            if (needToUpdateMap) {
                this.setMapCoordinates();
            }
        });
    }

    archiveTrigger() {
        if (this.$scope.isArchived) {
            this.activate();
        } else {
            this.archive();
        }
    }

    archive() {
        let self = this;
        this.sweetAlert.swal(
            {
                title: Messages('brokers.archive.title'),
                type: 'warning',
                showCancelButton: true,
                cancelButtonText: Messages('brokers.cancel'),
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('brokers.archive.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    let broker = Object.assign({}, self.brokerObject);
                    broker.state = 2;
                    self.Broker.update({ id: broker.id },broker,
                        function() {
                            self.brokerObject.state = 2;
                            self.$scope.isArchived = true;
                            self.notificationService.success(Messages('brokers.message.deleted'));
                        },
                        function(result) {
                            self.notificationService.error(result.data.message ? result.data.message : result.data);
                        });
                }
            }
        );
    }

    activate() {
        let self = this;
        this.sweetAlert.swal(
            {
                title: Messages('brokers.activate.title'),
                type: 'warning',
                showCancelButton: true,
                cancelButtonText: Messages('brokers.cancel'),
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('brokers.activate.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    let broker = Object.assign({}, self.brokerObject);
                    broker.state = 1;
                    self.Broker.update({ id: broker.id }, broker,
                        function() {
                            self.brokerObject.state = 1;
                            self.$scope.isArchived = false;
                            self.notificationService.success(Messages('brokers.message.activated'));
                        },
                        function(result) {
                            self.notificationService.error(result.data.message ? result.data.message : result.data);
                        });
                }
            }
        );
    }

    setMapCoordinates() {
        this.map.marker.setMap(null);

        if (this.brokerObject.address) {
            let geocoder = new google.maps.Geocoder(),
                countries = Constants.countryStorePlain(),
                country = this.$filter('filter')(countries, {
                    value: this.brokerObject.countryId
                });

            if (country && country[0]) {
                country = country[0].text + ', ';
            } else {
                country = '';
            }

            geocoder.geocode({
                address: country + this.brokerObject.address
            }, (result, status) => {
                if (status === 'OK') {
                    this.map.setCenter(result[0].geometry.location);

                    this.map.marker.setPosition(result[0].geometry.location);
                    this.map.marker.setMap(this.map)
                }
            });
        }
    }
}


