'use strict';
import Constants from '../constants';
import Messages from '../messages';
import moment from 'moment';

export default class AssetStatStackingPlanController {
    /*@ngInject*/
    constructor($scope, $timeout, asset, assetId, AssetStatStackingPlan) {
        this.$scope = $scope;
        this.viewHeight = 0;
        this.assetId = assetId;
        this.asset = asset;
        this.AssetStatStackingPlan = AssetStatStackingPlan;
        this.$timeout = $timeout;

        this.Messages = Messages;

        this.onMouseDownOnScrollDriverEventHandler = this.onMouseDownOnScrollDriverEventHandler.bind(this);
        this.onContentScrollEventHandler = this.onContentScrollEventHandler.bind(this);
        this.onMouseMoveOnDocumentEventHandler = this.onMouseMoveOnDocumentEventHandler.bind(this);
        this.onMouseUpOnDocumentEventHandler = this.onMouseUpOnDocumentEventHandler.bind(this);
        this.breadcrumbStates = [{ path: 'asset-stats', title: this.Messages('assets.title') }, { title: this.Messages('stacking-plan.title') }];

        this.init();
    }

    init(){
        this.$scope.isTabsLoadingState = true;
        this.$scope.noStackingPlan = false;
        this.AssetStatStackingPlan.getObjects({
            asset: this.assetId
        }, (result) => {
            this.$scope.tabs = result;
            this.$scope.isTabsLoadingState = false;
            if (result.length) {
                this.$scope.isLoadingState = true;
                this.getFloorData(result[0]);
            } else {
                this.$scope.noStackingPlan = true;
            }
        }, (err) => {
            this.$scope.isTabsLoadingState = false;
            this.$scope.noStackingPlan = true;
            this.$scope.noStackingPlanMessage = err.data.message;
        });

        this.$scope.$on("$destroy", () => {
            this.clearScrollHandlers();
        });

        this.legendYears = this.generateYearsList();
    }

    setSizes() {
        this.scrollDriverBlockerEl = $('#plan-scroll-blocker');
        this.scrollDriverWrap = $('#plan-scroll-wrap');
        this.contentWrap = $('#plan-wrap');

        this.scrollDriverBlockerHeight = this.scrollDriverBlockerEl.height();
        this.scrollDriverHeight = $('#plan-scroll-cont').outerHeight();
        this.contentHeight = $('#plan-cont').height();

        let wrapperOffset = $('#plan-page-wrap').offset(),
            height = $(window).height() - wrapperOffset.top - 260,
            scrollDriverTargetHeight;

        this.viewHeight = height;

        this.contentWrap.height(height);

        if (this.scrollDriverHeight > height) {
            scrollDriverTargetHeight = height;
        } else if (this.scrollDriverHeight < this.scrollDriverBlockerHeight) {
            scrollDriverTargetHeight = this.scrollDriverBlockerHeight;
        } else {
            scrollDriverTargetHeight = this.scrollDriverHeight;
        }
        this.scrollDriverWrap.height(scrollDriverTargetHeight);
        this.scrollDriverWrapHeight = scrollDriverTargetHeight;

        this.initScrollHandlers();
    }

    initScrollHandlers() {
        this.scrollDriverWrap.on('mousewheel', this.onMouseWheelOnScrollDriverEventHandler);

        this.contentWrap.hover(() => {
            this.contentWrap.on('scroll', this.onContentScrollEventHandler);
        }, () => {
            this.contentWrap.off('scroll', this.onContentScrollEventHandler);
        });

        this.blockerInitialTop = null;
        this.blockerCurPosition = null;

        this.scrollDriverBlockerEl.on('mousedown', this.onMouseDownOnScrollDriverEventHandler);
        $(document.body).on('mousemove', this.onMouseMoveOnDocumentEventHandler);
        $(document.body).on('mouseup', this.onMouseUpOnDocumentEventHandler);
    }

    clearScrollHandlers() {
        if (!this.scrollDriverWrap) {
            return;
        }

        this.scrollDriverWrap.off('mousewheel', this.onMouseWheelOnScrollDriverEventHandler);
        this.scrollDriverBlockerEl.off('mousedown', this.onMouseDownOnScrollDriverEventHandler);
        $(document.body).off('mousemove', this.onMouseMoveOnDocumentEventHandler);
        $(document.body).off('mouseup', this.onMouseUpOnDocumentEventHandler);
    }

    onMouseWheelOnScrollDriverEventHandler(e) {
        e.originalEvent.preventDefault();
    }

    onMouseDownOnScrollDriverEventHandler(e) {
        e = e.originalEvent;

        this.blockerInitialTop = e.pageY;
        this.blockerCurPosition = parseInt(this.scrollDriverBlockerEl.css('top'), 10);
    }

    onContentScrollEventHandler(e) {
        let curScroll = e.target.scrollTop,
            scrollInPercents = curScroll / ((this.contentHeight - this.viewHeight) / 100),
            targetPositionForBlocker;

        if (scrollInPercents > 100) {
            scrollInPercents = 100;
        }

        if (this.scrollDriverHeight > this.viewHeight) {
            this.scrollDriverWrap[0].scrollTop = (this.scrollDriverHeight - this.viewHeight) / 100 * scrollInPercents;
        }

        targetPositionForBlocker = (this.scrollDriverWrapHeight - this.scrollDriverBlockerHeight) / 100 * scrollInPercents;
        this.scrollDriverBlockerEl.css({
            top: targetPositionForBlocker + 'px'
        });
    }

    onMouseMoveOnDocumentEventHandler(e) {
        if (this.blockerInitialTop === null) {
            return;
        }

        e = e.originalEvent;

        let position = this.blockerCurPosition - (this.blockerInitialTop - e.pageY);

        if (position < 0) {
            position = 0;
        } else if (position + this.scrollDriverBlockerHeight > this.scrollDriverWrapHeight) {
            position = this.scrollDriverWrapHeight - this.scrollDriverBlockerHeight;
        }

        this.scrollDriverBlockerEl.css({
            top: position + 'px'
        });

        let positionInPercents = position / ((this.scrollDriverWrapHeight - this.scrollDriverBlockerHeight) / 100);
        this.contentWrap[0].scrollTop = (this.contentHeight - this.viewHeight) / 100 * positionInPercents;
        this.scrollDriverWrap[0].scrollTop = (this.scrollDriverHeight - this.viewHeight) / 100 * positionInPercents;
    }

    onMouseUpOnDocumentEventHandler() {
        this.blockerInitialTop = null;
    }

    showSpaceDescription(data) {
        let self = this;

        self.$scope.description = data;
        self.$scope.details = null;

        self.$timeout(() => {
            self.$scope.details = data;

            if (data.leaseStartDate && data.leaseEndDate) {
                self.$scope.details.expires = (moment(moment.utc()).diff(moment(data.leaseStartDate))/moment(data.leaseEndDate).diff(moment(data.leaseStartDate))).toFixed(2) * 100;
            }
            else {
                self.$scope.details.expires = null;
            }

            // additional data
            Constants.spaceTypeStore()
                .byKey(this.$scope.details.spaceType)
                .done(function(dataItem) {
                    self.$scope.details.spaceTypeString = dataItem.display;
                });
        }, 51);
    }

    hideSpaceDescription() {
        let self = this;

        self.$scope.description = null;
        self.$scope.details = null;
    }

    tabSelected(e, data) {
        e.preventDefault();
        this.$scope.description = null;
        this.$scope.isLoadingState = true;
        this.clearScrollHandlers();
        this.getFloorData(data);
    }

    getMaxOfFloorArea(floors) {
        let maxFloorArea = 0;

        floors.forEach(function(floor) {
            if (floor.totalFloorArea > maxFloorArea) {
                maxFloorArea = floor.totalFloorArea;
            }
        });

        return maxFloorArea;
    }

    getFloorData(object) {
        this.AssetStatStackingPlan.getObjectData({
            asset: this.assetId,
            object: object.id
        }, (result) => {
            let maxFloorArea = this.getMaxOfFloorArea(result.floors);
            result.floors.forEach((floor) => {
                let floorWidth;

                if (this.$scope.view === 'actual') {
                    floorWidth = floor.totalFloorArea / (maxFloorArea / 100) + '%';
                } else {
                    floorWidth = '100%';
                }
                floor.width = floorWidth;

                for (let i = 0, l = floor.spaces.length; i < l; i++) {
                    let space = floor.spaces[i],
                        spaceInPercents;
                    //filling industry name
                    if (space.industryId != null){
                        Constants.clientsIndustryStore().byKey(space.industryId).done(function(dataItem) {
                                space.industry = dataItem.display;
                        })
                    }
                    //end filling industry name
                    space.leaseEndYearIndex = this.getLeaseEndYearIndex(space.leaseEndDate);
                    
                    // let spaceWithTheSameTenantId = floor.spaces.find(function(item) {
                    //     return item.tenantId === space.tenantId;
                    // });

                    // if (spaceWithTheSameTenantId && spaceWithTheSameTenantId.spaceId !== space.spaceId) {
                    //     spaceWithTheSameTenantId.spaceArea += space.spaceArea;

                    //     spaceInPercents = spaceWithTheSameTenantId.spaceArea / (floor.totalFloorArea / 100);

                    //     if (!(spaceWithTheSameTenantId.spaceId instanceof Array)) {
                    //         spaceWithTheSameTenantId.spaceId = [spaceWithTheSameTenantId.spaceId];
                    //     }
                    //     spaceWithTheSameTenantId.spaceId.push(space.spaceId);

                    //     spaceWithTheSameTenantId.weight = spaceInPercents;

                    //     floor.spaces.splice(i, 1);
                    //     i--; l--;
                    // } else {
                        spaceInPercents = space.spaceArea / (floor.totalFloorArea / 100);
                        space.weight = spaceInPercents;
                    //}
                }
                floor.spaces.sort(function(a, b) {
                    if (!a.tenantId && b.tenantId) {
                        return 1;
                    } else if (a.tenantId && !b.tenantId) {
                        return -1;
                    } else {
                        return 0;
                    }
                });
            });
            result.floors.sort(function(a, b) {
                if (a.floorOrder > b.floorOrder) {
                    return -1;
                } else if (a.floorOrder < b.floorOrder) {
                    return 1;
                } else {
                    return 0;
                }
            });

            this.$scope.floors = result.floors;
            this.$scope.isLoadingState = false;
            this.$timeout(() => {
                this.setSizes();
            }, 201);
        });
    }

    viewChanged(e) {
        e.originalEvent.preventDefault();

        let target = e.originalEvent.target,
            activeCl = 'active',
            viewType = target.getAttribute('data-type');

        $('.plan-tabs .view-tab').each(function(key, item) {
            let type = item.getAttribute('data-type');
            if (type !== viewType) {
                $(item).removeClass(activeCl);
            } else {
                $(item).addClass(activeCl);
            }
        });

        let maxFloorArea = this.getMaxOfFloorArea(this.$scope.floors);
        this.$scope.floors.forEach((floor) => {
            this.$scope.view = viewType;
            let floorWidth;
            if (viewType === 'actual') {
                floorWidth = floor.totalFloorArea / (maxFloorArea / 100) + '%';
            } else {
                floorWidth = '100%';
            }
            floor.width = floorWidth;
        });

        this.clearScrollHandlers();
        this.$timeout(() => {
            this.setSizes();
        }, 350);
    }

    generateYearsList(){
        var currentYear = new Date().getFullYear();

        var result =[];

        for (var i=0; i<5; i++){
            result.push({title :  currentYear + i, index: i})
        }

        return result;
    }

    getLeaseEndYearIndex(leaseEndDate){
        if (leaseEndDate == null) return null;

        var date = new Date(leaseEndDate);
        var leaseEndYear = date.getFullYear();

        var currentYear = new Date().getFullYear();

        var result = leaseEndYear - currentYear;

        if (result > 4) result = 4;

        return result;
    }
}


