import ValidationEngine from 'devextreme/ui/validation_engine';
import Messages from '../../messages';
import ArrayStore from 'devextreme/data/array_store';

export default class AttachementsController {
    /*@ngInject*/
    constructor($scope, FileService, NotificationService, UtilsService) {
        // from component
        this.documents = this.documents;
        this.maxAttachementSize = this.maxAttachementSize || 5000000;
        this.maxAttachementCount = this.maxAttachementCount || 10;
        this.readonly = this.readonly || false;
        this.documentCreateCallback = this.documentCreateCallback;
        this.documentAddCallback = this.documentAddCallback;
        this.documentDeleteCallback = this.documentDeleteCallback;

        // injected
        this.$scope = $scope;
        this.fileService = FileService;
        this.notificationService = NotificationService;
        this.utils = UtilsService;

        this.init();
        this.initComponent();
        this.initList();
    }

    init(){
        let self = this;

        for (var i = 0; i < self.documents.length; i++){
            if(!self.documents[i].uniqueId) {
                self.documents[i].uniqueId = self.utils.generateGuid();
            }
        }

        self.enableUpload = true;
        self.documentsCount = self.documents.length;
        self.editNotesValue = ''; //dx-text-box doesn't support one way binding

        self.$scope.Messages = Messages;
    }

    initComponent(){
        let self = this;

        self.editDocumentBtnConfig = {
            icon: 'edit',
            type: 'success',
            bindingOptions: {
                visible: '!file.isEditMode'
            },
            onClick: function(e) {
                if(e.model.file.isEditMode){
                    self.resetEditMode();
                }
                else {
                    self.resetEditMode();

                    self.editNotesValue = e.model.file.notes;
                    e.model.file.isEditMode = true;

                }
            }
        }

        self.deleteDocumentBtnConfig = {
            icon: 'trash',
            type: 'danger',
            bindingOptions: {
                visible: '!file.isEditMode'
            },
            onClick: function(e) {
                self.resetEditMode();
                self.deleteDocument(e.model.file);
            }
        }

        self.saveDocumentBtnConfig = {
            icon: 'save',
            type: 'default',
            bindingOptions: {
                visible: 'file.isEditMode'
            },
            onClick: function(e) {
                e.model.file.notes = self.editNotesValue;
                self.resetEditMode();
            }
        }

        self.cancelDocumentBtnConfig = {
            icon: 'remove',
            type: 'danger',
            bindingOptions: {
                visible: 'file.isEditMode'
            },
            onClick: function(e) {
                self.resetEditMode();
            }
        }

        self.editDocumentTxbConfig = {
            isValid: false,
            bindingOptions: {
                value: '$ctrl.editNotesValue',
                visible: 'file.isEditMode'
            }
        }

        self.editDocumentTxbValidationRules = {
            validationRules: [
                { type: 'stringLength', max: 500 },
            ],
            validationGroup: 'documentNotes'
        }

        self.checkFileLimit = function(){
            if(self.documents.length < self.maxAttachementCount && !self.readonly){
                self.enableUpload = true;
            }
            else {
                self.enableUpload = false;
            }

            if (!self.$scope.$$phase && (!self.$scope.$root || !self.$scope.$root.$$phase)){
                self.$scope.$apply();
            }
        }

        self.loadFiles = function (files) {
            self.fileService.uploadFiles(files,
                function (singleFile) {
                    if(singleFile.size > self.maxAttachementSize){
                        self.notificationService.error("Error loading file " + singleFile.filename + ". Maximum file size is " + self.maxAttachementSize + " byte");
                        return;
                    }

                    if(self.documentsCount >= self.maxAttachementCount){
                        self.checkFileLimit();
                        return;
                    }

                    self.documentsCount++;

                    var document = self.createDocument(singleFile);
                    self.documents.push(document);

                    if(self.documentAddCallback){
                        self.documentAddCallback(document);
                    }
                },
                function () {
                    self.checkFileLimit();
                    self.refreshList();
                })
        }

        self.createDocument = function (file) {
            var document = {
                id: 0,
                uniqueId: self.utils.generateGuid(),
                isEditMode: false,
                filePath: '',
                originalFileName: file.filename,
                notes: '',
                contentType: file.extension,
                documentFile: {
                    data: [],
                    fileData: file.data,
                    contentType: file.extension,
                    uniqueFileName: '',
                    originalFileName: file.filename,
                }
            };

            if(self.documentCreateCallback){
                self.documentCreateCallback(document);
            }

            return document;
        }

        self.deleteDocument = function (file) {
            for (var i = 0; i < self.documents.length; i++) {
                if (self.documents[i].uniqueId == file.uniqueId) {
                    self.documents.splice(i, 1);
                    break;
                }
            }

            self.documentsCount--;

            if(self.documentDeleteCallback){
                self.documentDeleteCallback(file);
            }

            self.checkFileLimit();
            self.refreshList();
        }

        self.resetEditNotesValue = function() {
            self.editNotesValue = '';
        }

        self.resetEditMode = function () {
            self.resetEditNotesValue();

            for (var i = 0; i < self.documents.length; i++) {
                self.documents[i].isEditMode = false;
            }
        }

        angular.element(document).ready(function() {

            $("#attachementsUploader").change(function () {
                self.loadFiles($("#attachementsUploader")[0].files);
            });

            self.checkFileLimit();
        });
    }

    initList() {
        let self = this;

        self.errorHandler = {
            requestFailed: function (response) {
                this.notificationService.error(response.data);
            }
        }


        self.store = new ArrayStore({
            data: self.documents,//self.asset.documents,
            key: "uniqueId",
            modified: function () {
                // 'modified' event handler
            },
            errorHandler: self.errorHandler.requestFailed
        });

        self.listDataSource = { store: self.store };

        self.listOptions = {
            dataSource: self.listDataSource,
        }

        self.refreshList = function() {
            $("#listContainer").dxList('instance').reload();
        }
    };

    addDocument() {
        $("#attachementsUploader").val(null);
        setTimeout(function () {
            $("#attachementsUploader").click();
        }, 200);
    }

}


