let AssetGalleryComponent = {
    bindings: {
        images: '=',
        maxImageWidth: '=?',
        maxImageHeight: '=?',
        maxImageCount: '=?',
        readonly: '=?',
        updateImage: '=?'
    },
    controller: function() {
        let self = this;

        self.maxImageCount = 1;
        self.maxImageHeight = 500;
        self.maxImageWidth = 500;

        self.imageAddCallback = function() {
            self.updateImage();
        };
        self.imageDeleteCallback = function() {
            self.updateImage();
        };
        self.sortingChangeCallback = function() {
        };
    },
    template: "<imagegallery images='$ctrl.images' " +
    "                        readonly='$ctrl.readonly' " +
    "                        max-image-count='$ctrl.maxImageCount' " +
    "                        max-image-height='$ctrl.maxImageHeight' " +
    "                        max-image-width='$ctrl.maxImageWidth' " +
    "                        image-add-callback='$ctrl.imageAddCallback' " +
    "                        image-delete-callback='$ctrl.imageDeleteCallback' " +
    "                        sorting-change-callback='$ctrl.sortingChangeCallback' " +
    "                        class='asset-gallery'></imagegallery>"
}

export default AssetGalleryComponent;


