import Messages from './messages';
import ValidationEngine from 'devextreme/ui/validation_engine';
import {LocaleId, updateLocale} from './app.localization';

/*@ngInject*/
export default function run($rootScope, SecurityService) {
    SecurityService.fillAuthData();
    $rootScope.Messages = Messages;

    // reset DevExtreme validation engine as it may be considered 'singleton'
    $rootScope.$on('$stateChangeStart', function(){ 
        ValidationEngine.initGroups();

        SecurityService.getUserInfo().then(result => { 
            updateLocale(result.localeId);
        });
    });

    var setBodySmall = function() {
        if ($(window).outerWidth() < 769) {
            $('body').addClass('page-small');
        } else {
            $('body').removeClass('page-small');
            $('body').removeClass('show-sidebar');
        }
    }

    $(document).ready(function () {
        setBodySmall();
    });

    $(window).bind("resize click", function () {
        setBodySmall();
    });
}



