import 'angularjs-toaster';

import AuthInterceptorService from './auth-interceptor-service';
import ConfigurationService from './configuration-service';
import DialogService from './dialog.service';
import NotificationService from './notification-service';
import SecurityService from './security-service';
import ImageService from './image.service';
import FileService from './file.service';
import UtilsService from './utils.service';
import CompareObjectService from './compare.service';

var module = angular.module('tower360.services', ['toaster'])
  .factory('AuthInterceptorService', ['$q', '$location', 'localStorageService', ($q, location, localStorageService) => new AuthInterceptorService($q, location, localStorageService)])
  .service('ConfigurationService', ConfigurationService)
  .service('DialogService', DialogService)
  .service('NotificationService', NotificationService)
  .service('SecurityService', SecurityService)
  .service('ImageService', ImageService)
  .service('FileService', FileService)
  .service('UtilsService', UtilsService)
  .service('CompareObjectService', CompareObjectService);

export default module;



