import Dialog from 'devextreme/ui/dialog';

export default class DialogService {
    constructor() {
    }

    confirmation(msg, confirmAction) {
        var result = Dialog.confirm(msg, 'Please confirm');
        result.done(function (dialogResult) {
            if (dialogResult) {
                confirmAction();
            }
        });
    }
}



