'use strict';

import Messages from '../../messages';

export default class RightSidebar {
    /*@ngInject*/
    constructor($scope, $rootScope, $state, $timeout) {
        this.$scope = $scope;
        this.$rootScope = $rootScope;
        this.$state = $state;
        this.$timeout = $timeout;
        this.messages = Messages;
        this.bodyCl = 'right-sidebar-opened';
        this.bodyBlockerCl = 'blocker-show';

        this.updateContent = this.updateContent.bind(this);
        this.close = this.close.bind(this);

        this.init();
    }

    init() {
        this.$scope.$on('right-sidebar:open', this.updateContent);
        this.$scope.$on('right-sidebar:close', this.close);
    }

    updateContent(e, content) {
        this.$scope.contentType = null;
        this.$scope.content = null;

        this.$timeout(() => {
            this.$scope.contentType = content.type;
            this.$scope.content = content.data;
        });
        if (!this.$scope.visible) {
            $(document.body).addClass(this.bodyCl);
            this.$timeout(() => {
                $(document.body).addClass(this.bodyBlockerCl);
            });
            this.$scope.visible = true;
        }
    }

    close() {
        this.$scope.visible = false;
        $(document.body).removeClass(this.bodyBlockerCl);
        this.$timeout(() => {
            this.$scope.contentType = null;
            this.$scope.content = null;
            $(document.body).removeClass(this.bodyCl);
        }, 300);
        this.$rootScope.$broadcast('right-sidebar:closed');
    }
}



