export default class Representative {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
    return $resource(ConfigurationService.getApiBaseUrl() + 'api/clients/:clientId/representatives/:id', { id: '@id', clientId: '@clientId' }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/clients/:clientId/representatives',
            isArray: true
        },
        update: {
            method: 'PUT'
        },
        create: {
            method: 'POST',
            url: ConfigurationService.getApiBaseUrl() + 'api/clients/:clientId/representatives',
        },
        delete: {
            method: 'DELETE',
        },
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    Representative.instance = new Representative(ConfigurationService, $resource);
    return Representative.instance;
  }
}


