'use strict';

import Messages from '../../messages';
import Constants from '../../constants';

export default class TenantDescription {
    /*@ngInject*/
    constructor($scope, $rootScope, uibDateParser, UtilsService) {
        this.$scope = $scope;
        this.$rootScope = $rootScope;
        this.messages = Messages;
        this.uibDateParser = uibDateParser;
        this.utils = UtilsService;

        this.init();
    }

    init() {
        this.$scope.data = this.data;
        this.$scope.context = this.context;
        this.$scope.selectedSpace = this.data.spaces[0];

        this.$scope.vatCosts = 0;
        this.$scope.additionalCosts = 0;
        this.data.spaces.forEach((item) => {
            this.$scope.vatCosts = (Number(this.$scope.vatCosts) + item.vatCost).toFixed(2);
            this.$scope.additionalCosts = (Number(this.$scope.additionalCosts) + item.additionalCost).toFixed(2);
        });
    }

    getTowersCount() {
        let uniqueTowers = [];
        this.$scope.data.spaces.forEach(function(space) {
            if (uniqueTowers.indexOf(space.towerId) === -1) {
                uniqueTowers.push(space.towerId);
            }
        });

        return uniqueTowers.length;
    }

    getIndustryType(typeId) {
        let industry;
        Constants.clientsIndustryPlain().forEach(function(item) {
            if (item.value === typeId) {
                industry = item.text;
            }
        });
        return industry;
    }

    getStatus(status) {
        return status === 0 ? Messages('assets.tenants.filter.in_place') : Messages('assets.tenants.filter.expired');
    }

    selectSpace(spaceId) {
        this.data.spaces.forEach((item) => {
            if (item.id === spaceId) {
                this.$scope.selectedSpace = item;
            }
        });
    }

    getParsedDate(date) {
        return this.uibDateParser.filter(new Date(date), 'dd/MM/yyyy');
    }

    closeSidebar() {
        this.$rootScope.$broadcast('right-sidebar:close');
    }
}



