import ArrayStore from 'devextreme/data/array_store';
import BrokerEditModalController from './broker-edit-modal.controller';
import BrokerEditModalTemplate from './broker-edit-modal.template.html';
import Messages from '../messages';
import Constants from '../constants';

export default class BrokersController {
    /*@ngInject*/
    constructor($scope, $state, SecurityService, NotificationService, Broker, sweetAlert, $uibModal, UtilsService) {
        this.securityService = SecurityService;
        this.notificationService = NotificationService;
        this.Broker = Broker;
        this.$state = $state;

        this.stateStore = Constants.brokerStateStore();
        this.sweetAlert = sweetAlert;
        this.$uibModal = $uibModal;

        this.utils = UtilsService;

        this.init();
    }

    init() {
        let self = this;

        self.errorHandler = {
            requestFailed: function(response) {
                this.NotificationService.error(response.data);
            }
        };


        self.brokers = [];
        self.Broker.query(
            {},
            (result) => {
                self.brokers = result;
                
                 self.brokers.forEach(item => {
                    item.address = self.utils.generateAddress(item);
                });
                
                self.store = new ArrayStore({
                    data: self.brokers,
                    key: "id",
                    modified: function() {
                        // 'modified' event handler
                    },
                    errorHandler: self.errorHandler.requestFailed
                });

                self.gridDataSource = {store: self.store};


                self.gridSettings = $.extend(Constants.defaultGridSettings(), {
                    dataSource: self.gridDataSource,
                    columns: [
                        {
                            dataField: "id",
                            visible: false
                        }, {
                            dataField: "name",
                            caption: Messages('brokers.name'),
                            sortOrder: "asc",
                            filterOperations: ['contains', 'startswith', '='],
                            selectedFilterOperation: 'contains',
                            cellTemplate: function(cellElement, cellInfo) {
                                let element = $('<a>', {
                                    'href': '/brokers/' + cellInfo.data.id,
                                    'ui-sref': 'broker'
                                });

                                element.text(cellInfo.displayValue)
                                       .appendTo(cellElement);
                            }
                        }, {
                            dataField: "address",
                            caption: Messages('brokers.address'),
                            filterOperations: ['contains', 'startswith', '='],
                            selectedFilterOperation: 'contains'
                        }, {
                            dataField: 'state',
                            caption: Messages('brokers.state'),
                            width: 100,
                            lookup: {
                                dataSource: this.stateStore,
                                displayExpr: 'display',
                                valueExpr: 'value'
                            },
                        },
                        {
                            caption: Messages('actions'),
                            width: 90,
                            alignment: 'right',
                            cellTemplate: function(container, options) {
                                container.addClass("actions-cell");

                                $("<div />").dxButton({
                                    type: 'custom',
                                    template: `<div class="fa fa-pencil" title="${Messages('brokers.edit')}"></div>`,
                                    onClick: function(data) {
                                        data.jQueryEvent.stopPropagation();
                                        let modal = self.$uibModal.open({
                                            controller: BrokerEditModalController,
                                            template: BrokerEditModalTemplate,
                                            resolve: {
                                                brokerObject: Object.assign({}, options.data)
                                            }
                                        });
                                        modal.result.then((result) => {
                                            jQuery.extend(options.data, result);
                                        });
                                    }
                                }).appendTo(container);

                                let isArchived = options.data.state === 2;
                                $("<div />").dxButton({
                                    type: 'custom',
                                    template: `<div class="fa ${isArchived ? 'fa-undo' : 'fa-trash-o'}" title="${isArchived ? Messages('brokers.activate') : Messages('brokers.archive')}"></div>`,
                                    onClick: function(data) {
                                        if (isArchived) {
                                            self.activateBroker(options.data);
                                        } else {
                                            self.archiveBroker(options.data);
                                        }
                                    }
                                }).appendTo(container);
                            }
                        }
                    ]
                });
            }
        );
    }

    archiveBroker(broker) {
        broker = Object.assign({}, broker);
        let self = this;
        broker.state = 2;
        this.sweetAlert.swal(
            {
                title: Messages('brokers.archive.title'),
                type: 'warning',
                showCancelButton: true,
                cancelButtonText: Messages('brokers.cancel'),
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('brokers.archive.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    self.Broker.update({id: broker.id},
                        broker,
                        function(result) {
                            self.store.remove(broker.id);
                            self.store.insert(result);
                            $('#gridContainer').dxDataGrid('instance').refresh();
                            self.notificationService.success(Messages('brokers.message.deleted'));
                        },
                        function(result) {
                            self.notificationService.error(result.data);
                        });
                }
            }
        );
    }

    activateBroker(broker) {
        broker = Object.assign({}, broker);

        let self = this;
        this.sweetAlert.swal(
            {
                title: Messages('brokers.activate.title'),
                type: 'warning',
                showCancelButton: true,
                cancelButtonText: Messages('brokers.cancel'),
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('brokers.activate.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    broker.state = 1;
                    self.Broker.update({ id: broker.id }, broker,
                        function(result) {
                            self.store.remove(broker.id);
                            self.store.insert(result);
                            $('#gridContainer').dxDataGrid('instance').refresh();
                            self.notificationService.success(Messages('brokers.message.activated'));
                        },
                        function(result) {
                            self.notificationService.error(result.data);
                        });
                }
            }
        );
    }

}


