export default class Registration {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
    return $resource(ConfigurationService.getApiBaseUrl() + 'api/account/registration/:token', { token: '@token' }, {
        query: {
            method: 'GET',
        },
        confirm: {
            method: 'PUT'
        },
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    Registration.instance = new Registration(ConfigurationService, $resource);
    return Registration.instance;
  }
}


