import ArrayStore from 'devextreme/data/array_store';
import UserEditModalController from './user-edit-modal.controller';
import UserEditModalTemplate from './user-edit-modal.template.html';
import Messages from '../messages';
import Constants from '../constants';

export default class UsersController {
    /*@ngInject*/
    constructor($scope, $state, User, Broker, Role, StoreAdapter, currentUser, NotificationService, sweetAlert, $uibModal) {
        this.$scope = $scope;
        this.$state = $state;
        this.User = User;
        this.Broker = Broker;
        this.Role = Role;
        this.StoreAdapter = StoreAdapter;
        this.currentUser = currentUser;
        this.notificationService = NotificationService;
        this.sweetAlert = sweetAlert;
        this.$uibModal = $uibModal;

        this.initGrid();
    }

    initGrid() {
        let self = this;

        self.errorHandler = {
            requestFailed: function (response) {
                this.NotificationService.error(response.data);
            }
        }

        self.stateStore = Constants.userStateStore();

        self.users = [];

        self.User.query(
            {},
            (result) => {
                self.users = result;

                self.store = new ArrayStore({
                    data: self.users,
                    key: "id",
                    modified: function () {
                        // 'modified' event handler
                    },
                    errorHandler: self.errorHandler.requestFailed
                });
                self.gridDataSource = { store: self.store };

                self.gridSettings = $.extend(Constants.defaultGridSettings(), {
                    dataSource: self.gridDataSource,
                    columns: [{
                        dataField: "id",
                        visible: false
                    }, {
                        dataField: "displayName",
                        caption: Messages('users.displayname'),
                        filterOperations: ['contains', 'startswith', '='],
                        selectedFilterOperation: 'contains',
                        cellTemplate: function(cellElement, cellInfo) {
                            let element = $('<a>', {
                                'href': '/users/' + cellInfo.data.id,
                                'ui-sref': 'user'
                            });

                            element.text(cellInfo.displayValue)
                                   .appendTo(cellElement);
                        }
                    }, {
                        dataField: "email",
                        caption: Messages('users.email'),
                        sortOrder: "asc",
                        filterOperations: ['contains', 'startswith', '='],
                        selectedFilterOperation: 'contains'
                    }, {
                        dataField: "state",
                        caption: Messages('users.state'),
                        lookup: {
                            dataSource: self.stateStore,
                            displayExpr: 'display',
                            valueExpr: 'value'
                        }
                    }, {
                        dataField: "roleId",
                        caption: Messages('users.role'),
                        lookup: {
                            dataSource: this.StoreAdapter.createStore(this.Role, {}, self.errorHandler),
                            displayExpr: 'name',
                            valueExpr: 'id'
                        }
                    }, {
                        dataField: "brokerId",
                        caption: Messages('users.broker'),
                        lookup: {
                            dataSource: this.StoreAdapter.createStore(this.Broker, {}, self.errorHandler),
                            displayExpr: 'name',
                            valueExpr: 'id'
                        }
                    }, {
                        caption: Messages('actions'),
                        width: 90,
                        alignment: 'right',
                        cellTemplate: function (container, options) {
                            container.addClass("actions-cell");

                            $("<div />").dxButton({
                                type: 'custom',
                                template: `<div class="fa fa-pencil" title="${Messages('users.edit')}"></div>`,
                                disabled: options.data.id === self.currentUser.id, // this is you
                                onClick: function(data) {
                                    data.jQueryEvent.stopPropagation();

                                    let modal = self.$uibModal.open({
                                        controller: UserEditModalController,
                                        template: UserEditModalTemplate,
                                        resolve: {
                                            userObject: Object.assign({}, options.data)
                                        }
                                    });
                                    modal.result.then((result) => {
                                        jQuery.extend(options.data, result);
                                    });
                                }
                            }).appendTo(container);

                            let isArchived = options.data.state === 3;
                            $("<div />").dxButton({
                                type: 'custom',
                                template: `<div class="fa ${isArchived ? 'fa-undo' : 'fa-trash-o'}" title="${isArchived ? Messages('users.activate') : Messages('users.archive')}"></div>`,
                                disabled: options.data.id === self.currentUser.id,
                                onClick: function(data) {
                                    if (isArchived) {
                                        self.activateUser(options.data);
                                    } else {
                                        self.archiveUser(options.data);
                                    }
                                }
                            }).appendTo(container);
                        }
                    }]
                });
            }
        );
    }

    archiveUser(user) {
        let self = this;
        user = Object.assign({}, user);
        
        this.sweetAlert.swal(
            {
                title: Messages('users.archive.title'),
                type: 'warning',
                showCancelButton: true,
                cancelButtonText: Messages('users.cancel'),
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('users.archive.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    user.state = 3;
                    self.User.update({ id: user.id }, user,
                        function (result) {
                            self.store.remove(user.id);
                            self.store.insert(result);

                            $('#gridContainer').dxDataGrid('instance').refresh();
                            self.notificationService.success(Messages('users.message.archived'));
                        },
                        function (result) {
                            self.notificationService.error(result.data.message ? result.data.message : result.data);
                        });
                }
            }
        );
    }

    activateUser(user) {
        user = Object.assign({}, user);

        let self = this;
        this.sweetAlert.swal(
            {
                title: Messages('users.activate.title'),
                type: 'warning',
                showCancelButton: true,
                cancelButtonText: Messages('users.cancel'),
                confirmButtonColor: '#DD6B55',
                confirmButtonText: Messages('users.activate.confirmButton'),
            },
            function (isConfirm) {
                if (isConfirm) {
                    user.state = 2;
                    self.User.update({ id: user.id }, user,
                        function (result) {
                            self.store.remove(user.id);
                            self.store.insert(result);

                            $('#gridContainer').dxDataGrid('instance').refresh();
                            self.notificationService.success(Messages('users.message.activated'));
                        },
                        function (result) {
                            self.notificationService.error(result.data.message ? result.data.message : result.data);
                        });
                }
            }
        );
    }
}


