import Controller from './breadcrumbpanel.controller'
import Template from './breadcrumbpanel.template.html'

let BreadcrumbPanelComponent = {
  bindings: {
    states: '=',
    title: '@'
  },
  transclude: true,
  controller: Controller,
  template: Template
};

export default BreadcrumbPanelComponent;


