export default class Role {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
     return $resource(ConfigurationService.getApiBaseUrl() + 'api/roles/:id', { id: '@id' }, {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/roles',
            isArray: true
        }
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
    Role.instance = new Role(ConfigurationService, $resource);
    return Role.instance;
  }
}


