import $ from 'jquery';
import jQuery from 'jquery';
// export for others scripts to use
window.$ = $;
window.jQuery = jQuery;

import CustomStore from 'devextreme/data/custom_store';

(function () {
    'use strict';

    angular.module('tower360.utils', []);

    angular.module('tower360.utils')
        .factory('jqPromise', [
            function () {
                return function (promise) {
                    var jqDeferred = $.Deferred();
                    promise.then(function (val) {
                        jqDeferred.resolve.apply(jqDeferred, val);
                    }, function (err) {
                        jqDeferred.reject.apply(jqDeferred, err);
                    });

                    return jqDeferred.promise();
                }
            }
        ]);

    angular.module('tower360.utils')
    .factory('StoreAdapter', ['jqPromise',
        function (jqPromise) {
            return {
                createStore: function (resource, queryParams, errorHandler) {
                    queryParams = typeof queryParams !== 'undefined' ? queryParams : {};

                    errorHandler = typeof errorHandler !== 'undefined' ? errorHandler : {
                        requestFailed: function (response) {
                        }
                    };

                    var handleError = function (response) {
                        errorHandler.requestFailed(response);
                    };

                    return new CustomStore({
                        key: "id",
                        load: function (loadOptions) {
                            var params = loadOptions.sort ? $.extend({}, queryParams, loadOptions.sort[0]) : queryParams;

                            return jqPromise(
                                resource.query(params).$promise
                                    .then(function (data) {
                                        return [data, { totalCount: data.length }];
                                    }, function (response) {
                                        handleError(response);
                                    }));
                        },

                        byKey: function (key) {
                            return jqPromise(resource.get({ id: key }).$promise
                                .catch(function (response) {
                                    handleError(response);
                                }));
                        },

                        insert: function (data) {
                            return jqPromise(resource.save(data).$promise
                                .catch(function (response) {
                                    handleError(response);
                                }));
                        },

                        update: function (key, data) {
                            data.id = key;
                            return jqPromise(resource.update(data).$promise
                                .catch(function (response) {
                                    handleError(response);
                                }));
                        },

                        remove: function (key) {
                            return jqPromise(resource.delete({ id: key }).$promise
                                .catch(function (response) {
                                    handleError(response);
                                }));
                        },

                        totalCount: function (loadOptions) {
                            return jqPromise(resource.count().$promise
                                .then(function (data) {
                                    return [data.count];
                                }, function (response) {
                                    handleError(response);
                                }));
                        }
                    });
                }
            }
        }]);
})();



