import FilterController from './tenants-filter-modal.controller';
import FilterTemplate from './tenants-filter-modal.template.html';
import Constants from '../../constants';
import Messages from '../../messages';
import moment from 'moment';

export default class AssetStatsTenantsController {
    /*@ngInject*/
    constructor($scope, $rootScope, $state, asset, assetId, AssetStatTenants, AssetStatStackingPlan, sweetAlert, $uibModal, uibDateParser, UtilsService, $timeout) {
        this.$scope = $scope;
        this.$rootScope = $rootScope;
        this.asset = asset;
        this.assetId = assetId;
        this.$state = $state;
        this.AssetStatTenants = AssetStatTenants;
        this.AssetStatStackingPlan = AssetStatStackingPlan;
        this.sweetAlert = sweetAlert;
        this.$uibModal = $uibModal;
        this.uibDateParser = uibDateParser;
        this.utils = UtilsService;
        this.$timeout = $timeout;

        this.init();

        $scope.$on('$destroy', () => {
            $rootScope.$broadcast('right-sidebar:close');
        });
        $scope.$on('right-sidebar:closed', () => {
            this.selectSpaceRow([]);
            this.selectedTenant = null;
        });
    }

    init() {
        this.$scope.greedData = [];

        this.$scope.statuses = [{
            id: 'in_place',
            value: 0,
            name: Messages('assets.tenants.filter.in_place'),
        }, {
            id: 'expired',
            value: 1,
            name: Messages('assets.tenants.filter.expired')
        }, {
            id: '1_month',
            value: 1,
            name: Messages('assets.tenants.filter.within_1_month')
        }, {
            id: '3_month',
            value: 3,
            name: Messages('assets.tenants.filter.within_3_month')
        }, {
            id: '6_month',
            value: 6,
            name: Messages('assets.tenants.filter.within_6_month')
        }, {
            id: '1_year',
            value: 12,
            name: Messages('assets.tenants.filter.within_1_year')
        }, {
            id: 'custom',
            name: Messages('assets.tenants.filter.custom_dates')
        }];
        this.$scope.selectedStatus = this.$scope.statuses[0];

        this.$timeout(() => {
            this.customDateElement = $('#spaces-filter-daterangepicker');
            $(this.customDateElement).daterangepicker({
                minDate: new Date(),
                autoApply: true,
                opens: 'left'
            });
            $(this.customDateElement).on('apply.daterangepicker', (ev, picker) => {
                this.customExpirationFrom = picker.startDate;
                this.customExpirationTo = picker.endDate;
                this.$scope.customExpirationFrom = this.getParsedDate(picker.startDate);
                this.$scope.customExpirationTo = this.getParsedDate(picker.endDate);
                this.gridUpdate(1);
            });
        }, 1);

        this.statsLabelsDisctionary = {
            'in_place': 'totalAreaActiveContract',
            '1_month': 'totalAreaExpiresInOneMonth',
            '3_month': 'totalAreaExpiresInThreeMonth',
            '6_month' : 'totalAreaExpiresInSixMonth',
            '1_year': 'totalAreaExpiresInOneYear'
        };

        this.$scope.extraFilters = [];
        this.$scope.towers = [];

        this.AssetStatStackingPlan.getObjects({
            asset: this.assetId
        }, (result) => {
            this.$scope.towers = result;
        }, (err) => {

        });

        this.gridUpdate(1);
    }

    selectStatus(e, status) {
        e.preventDefault();
        this.$scope.selectedStatus = status;

        if (status.id !== 'custom') {
            this.gridUpdate(1);
            this.$scope.customExpirationFrom = null;
            this.$scope.customExpirationTo = null;
            $(this.customDateElement).data('daterangepicker').setStartDate(new Date());
            $(this.customDateElement).data('daterangepicker').setEndDate(new Date());
        } else {
            $(this.customDateElement).focus();
        }
    }

    gridUpdate(page) {
        this.$scope.update = true;
        this.page = page;

        let extraFilters = this.$scope.extraFilters,
            status,
            expirationFrom,
            expirationTo,
            pricePerMeterFrom = extraFilters.find((item) => {
                return item.id === 'area_from';
            }),
            pricePerMeterTo = extraFilters.find((item) => {
                return item.id === 'area_to';
            }),
            contractStartDate = extraFilters.find((item) => {
                return item.id === 'lease_start';
            }),
            contractEndDate = extraFilters.find((item) => {
                return item.id === 'lease_end';
            }),
            towers = [],
            spaceTypes = [],
            industries = [];

        extraFilters.forEach((item) => {
            if (item.id.indexOf('tower_') === 0) {
                towers.push(item.value.id);
            }
            if (item.id.indexOf('office_type_') === 0) {
                spaceTypes.push(item.value.id);
            }
            if (item.id.indexOf('industry_') === 0) {
                industries.push(item.value.value);
            }
        });

        switch (this.$scope.selectedStatus.id) {
            case 'in_place':
            case 'expired':
                status = this.$scope.selectedStatus.value;
                break;
            case '1_month':
            case '3_month':
            case '6_month':
            case '1_year':
                expirationFrom = moment().format();
                expirationTo = moment().add(this.$scope.selectedStatus.value, 'M').format();
                break;
            case 'custom':
                expirationFrom = moment(this.customExpirationFrom).format();
                expirationTo = moment(this.customExpirationTo).format();
                break;
        }

        this.AssetStatTenants.query({
            id: this.assetId,
            st: status,
            expgte: expirationFrom,
            explte: expirationTo,
            ipgte: pricePerMeterFrom && pricePerMeterFrom.value,
            iplte: pricePerMeterTo && pricePerMeterTo.value,
            stgte: contractStartDate && moment(contractStartDate.value).format(),
            stlte: contractEndDate && moment(contractEndDate.value).format(),
            twr: towers,
            ot: spaceTypes,
            ind: industries,
            srt: this.$scope.sortType,
            ord: this.$scope.sortDirection,
            pg: page || 1,
            ps: 20
        }, (result) => {
            this.$scope.update = false;

            if (page === 1) {
                this.$scope.greedData = result.items;
                this.$scope.disable_infinite_scrolling = false;
                this.updateStatusLabels(result.stats);
            } else {
                if (result.items.length === 0) {
                    this.$scope.disable_infinite_scrolling = true;
                } else {
                    this.$scope.disable_infinite_scrolling = false;
                    this.$scope.greedData = this.$scope.greedData.concat(result.items);
                }
            }

            this.$timeout(() => {
                this.$scope.load_more = false;
            }, 1);
        }, (error) => {
            console.log(error);
            this.$scope.update = false;
            this.$scope.error = error;
        });

        this.$rootScope.$broadcast('right-sidebar:close');
    }

    loadMoreItems() {
        if (!this.$scope.greedData.length) {
            return;
        }

        this.$scope.load_more = true;
        this.gridUpdate(++this.page);
    }
    sortGrid(sortType) {
        this.$scope.sortType = sortType;
        this.$scope.sortDirection = this.$scope.sortDirection === 1 ? 0 : 1;
        this.gridUpdate(1);
    }

    showExtraFilters() {
        let modal = this.$uibModal.open({
            controller: FilterController,
            template: FilterTemplate,
            resolve: {
                filter: {
                    data: this.$scope.extraFilters
                },
                towers: {
                    data: this.$scope.towers
                }
            },
            size: 'lg'
        });
        modal.result.then((result) => {
            this.$scope.extraFilters = result;
            if (result.length) {
                this.gridUpdate(1);
            }
        });
    }

    extraFilterRemoveItem(item) {
        let id = item.id,
            itemIndex = this.$scope.extraFilters.findIndex(function(item) {
                return item.id === id;
            });

        this.$scope.extraFilters.splice(itemIndex, 1);
        this.gridUpdate(1);
    }

    getParsedDate(date) {
        return this.uibDateParser.filter(new Date(date), 'dd/MM/yyyy');
    }

    selectSpaceRow(row) {
        let cl = 'filtered-spaces-row-selected';
        $('#filtered-spaces-grid tr').each(function(i, item) {
            if (item === row[0]) {
                $(item).addClass(cl);
            } else {
                $(item).removeClass(cl);
            }
        });
    }

    getOfficeType(type) {
        let officeType = Constants.spaceTypeStorePlain().find((item) => {
            return item.value === type;
        });
        return officeType.text;
    }

    getUniqueSpacesTowers(spaces) {
        let uniqueSpacesTowers = [];
        spaces.forEach(function(item) {
            if (uniqueSpacesTowers.indexOf(item.towerName) === -1) {
                uniqueSpacesTowers.push(item.towerName);
            }
        });
        return uniqueSpacesTowers;
    }

    showSpaceInfo(space) {
        this.$rootScope.$broadcast('right-sidebar:open', {
            type: 'loading'
        });
        this.AssetStatTenants.getSpaceInfo({
            assetId: this.assetId,
            assetObjectId: space.assetObjectId,
            floorId: space.floorId,
            id: space.id
        }, (result) => {
            let contract = result.contract;

            result.spaceTypeString = this.getOfficeType(result.type);
            result.spaceNumber = result.number;
            result.spaceArea = result.rentableArea;
            if (contract && contract.industryId != null){
                Constants.clientsIndustryStore().byKey(contract.industryId).done(function(dataItem) {
                    result.industryId = contract.industryId;
                    result.industry = dataItem.display;
                });
            }
            result.tenantName = contract && contract.tenantName;
            result.leaseStartDate = contract && contract.startsAt;
            result.leaseEndDate = contract && contract.endsAt;
            if (result.leaseStartDate && result.leaseEndDate) {
                result.expires = (moment(moment.utc()).diff(moment(result.leaseStartDate))/moment(result.leaseEndDate).diff(moment(result.leaseStartDate))).toFixed(2) * 100;
            }
            result.totalCosts = contract && contract.totalCosts;
            result.inPlaceRent = contract && contract.inPlaceRent;
            result.netRent = contract && contract.netRent;
            result.additionalCosts = contract && contract.additionalCosts;
            result.otherCosts = contract && contract.otherCosts;
            result.vatCosts = contract && contract.vatCosts;

            this.$rootScope.$broadcast('right-sidebar:open', {
                type: 'spaceDescription',
                data: result
            });
        });
    }

    showTenantInfo(event, tenant) {
        if (event.target.nodeName === 'A') {
            return;
        }

        let row = $(event.target).parent('tr');
        if (this.selectedTenant === row[0]) {
            this.$rootScope.$broadcast('right-sidebar:close');
            return;
        }
        this.selectedTenant = row[0];

        this.$rootScope.$broadcast('right-sidebar:open', {
            type: 'tenantDescription',
            data: tenant
        });
        this.selectSpaceRow(row);
    }

    updateStatusLabels(stats){
        stats = stats || [];
        this.$scope.statuses.forEach((element) => {
            let value = Number(stats[this.statsLabelsDisctionary[element.id]]);
            element.label = this.utils.formatBadgeValue(value);
        });
    }
}


