import Messages from '../../messages';
import Constants from '../../constants';
import moment from 'moment';

export default class SpacesFilterController {
    /*@ngInject*/
    constructor($scope, $state, filter, towers, $uibModalInstance, uibDateParser, $timeout) {
        this.$scope = $scope;
        this.$state = $state;
        this.filter = filter.data;
        this.towers = towers.data;
        this.$uibModalInstance = $uibModalInstance;
        this.uibDateParser = uibDateParser;
        this.$timeout = $timeout;

        this.init();
        this.initForm();
    }

    init() {
        let spaceTypes = Constants.spaceTypeStorePlain(),
            piecesPerRow = Math.round(spaceTypes.length / 3),
            typesRows = [];

        for (let i = 0, l = 3; i < l; i++) {
            typesRows[i] = [];
            let j = 0;
            for (let jl = piecesPerRow; j < jl; j++) {
                let item = spaceTypes.shift();
                if (item) {
                    typesRows[i].push(item);
                } else {
                    break;
                }
            }
        }

        this.$scope.typesRows = typesRows;
    }

    initForm() {
        this.$scope.fromValueChanged = (from, to) => {
            if (this.$scope[from] && this.$scope[to] && this.$scope[from] > this.$scope[to]) {
                this.$scope[to] = this.$scope[from];
            }
        };
        this.$scope.toValueChanged = (from, to) => {
            if (this.$scope[from] && this.$scope[to] && this.$scope[to] < this.$scope[from]) {
                this.$scope[from] = this.$scope[to];
            }
        };

        // area
        let area_from = this.filter.find((item) => {
            return item.id === 'area_from';
        });
        this.$scope.area_from = area_from && area_from.value;
        let area_to = this.filter.find((item) => {
            return item.id === 'area_to';
        });
        this.$scope.area_to = area_to && area_to.value;

        // towers
        this.$scope.towers = {
            available: this.towers,
            selected: []
        };

        // industry
        this.$scope.industry = {
            available: Constants.clientsIndustryPlain(),
            selected: []
        };

        // office type
        this.$scope.office_types = [];
        this.$scope.office_types_ids = [];
        this.$scope.officeTypeToggleSelection = (type, valueString) => {
            let id = this.$scope.office_types.findIndex((item) => {
                return item.id === type;
            });

            if (id > -1) {
                this.$scope.office_types.splice(id, 1);
                this.$scope.office_types_ids.splice(id, 1);
            } else {
                this.$scope.office_types.push({
                    id: type,
                    name: valueString
                });
                this.$scope.office_types_ids.push(type);
            }
        };

        this.leaseDateStart = null;
        this.leaseDateEnd = null;

        this.filter.forEach((item) => {
            if (item.id.indexOf('tower_') === 0) {
                this.$scope.towers.selected.push(item.value);
            } else if (item.id.indexOf('industry_') === 0) {
                this.$scope.industry.selected.push(item.value);
            } else if (item.id.indexOf('office_type_') === 0) {
                this.$scope.office_types.push(item.value);
                this.$scope.office_types_ids.push(item.value.id);
            } else if (item.id.indexOf('lease_start') === 0) {
                this.leaseDateStart = item.value;
            } else if (item.id.indexOf('lease_end') === 0) {
                this.leaseDateEnd = item.value;
            }
        });

        if (this.leaseDateStart && this.leaseDateEnd) {
            this.$scope.lease_date = this.getParsedDate(this.leaseDateStart) + ' - ' + this.getParsedDate(this.leaseDateEnd);
        }

        this.$timeout(() => {
            let pickerField = $('#tenants-filter-daterangepicker');
            pickerField.daterangepicker({
                startDate: this.leaseDateStart || new Date(),
                endDate: this.leaseDateEnd || new Date(),
                locale: {
                    cancelLabel: Messages('assets.tenants.filter.datepicker.clear')
                },
                autoUpdateInput: false,
                ranges: {
                    [Messages('assets.tenants.filter.this_month')]: [moment().startOf('month'), moment().endOf('month')],
                    [Messages('assets.tenants.filter.last_month')]: [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')],
                    [Messages('assets.tenants.filter.this_year')]: [moment().startOf('year'), moment().endOf('year')],
                    [Messages('assets.tenants.filter.last_year')]: [moment().subtract(1, 'year').startOf('year'), moment().subtract(1, 'year').endOf('year')]
                }
            });
            pickerField.on('apply.daterangepicker', (ev, picker) => {
                this.leaseDateStart = picker.startDate;
                this.leaseDateEnd = picker.endDate;
                pickerField.val(this.getParsedDate(picker.startDate) + ' - ' + this.getParsedDate(picker.endDate));
            });
            pickerField.on('cancel.daterangepicker', (ev, picker) => {
                pickerField.val('');
                let d = new Date();
                this.leaseDateStart = null;
                this.leaseDateEnd = null;
                pickerField.data('daterangepicker').setStartDate(d);
                pickerField.data('daterangepicker').setEndDate(d);
            });
        }, 10);


        this.$scope.cancel = () => {
            this.$uibModalInstance.dismiss('cancel');
        };

        this.$scope.save = () => {
            let result = [];
            if (this.$scope.area_from) {
                result.push({
                    id: 'area_from',
                    name: Messages('assets.tenants.filter.rentable_area_from'),
                    value: this.$scope.area_from,
                    text: `${this.$scope.area_from} ${Messages('square.meter')}`
                });
            }
            if (this.$scope.area_to) {
                result.push({
                    id: 'area_to',
                    name: Messages('assets.tenants.filter.rentable_area_to'),
                    value: this.$scope.area_to,
                    text: `${this.$scope.area_to} ${Messages('square.meter')}`
                });
            }
            if (this.$scope.towers.selected.length) {
                this.$scope.towers.selected.forEach(function(item) {
                    result.push({
                        id: `tower_${item.name}`,
                        value: item,
                        text: item.name
                    });
                });
            }
            if (this.$scope.industry.selected.length) {
                this.$scope.industry.selected.forEach(function(item) {
                    result.push({
                        id: `industry_${item.value}`,
                        value: item,
                        text: item.text
                    });
                });
            }
            if (this.$scope.office_types.length) {
                this.$scope.office_types.forEach((item) => {
                    result.push({
                        id: `office_type_${item.id}`,
                        value: item,
                        text: item.name
                    });
                });
            }
            if (this.leaseDateStart) {
                result.push({
                    id: `lease_start`,
                    name: Messages('assets.tenants.filter.lease_start_date'),
                    value: this.leaseDateStart,
                    text: this.getParsedDate(this.leaseDateStart)
                });
            }
            if (this.leaseDateEnd) {
                result.push({
                    id: `lease_end`,
                    name: Messages('assets.tenants.filter.lease_end_date'),
                    value: this.leaseDateEnd,
                    text: this.getParsedDate(this.leaseDateEnd)
                });
            }
            this.$uibModalInstance.close(result);
        }
    }

    getParsedDate(date) {
        return this.uibDateParser.filter(new Date(date), 'dd/MM/yyyy');
    }
}


