export default class Locale {
  /*@ngInject*/
  constructor (ConfigurationService, $resource) {
    return $resource(ConfigurationService.getApiBaseUrl() + 'api/locales/:id', { id: '@id' },
        {
        query: {
            method: 'GET',
            url: ConfigurationService.getApiBaseUrl() + 'api/locales',
            isArray: true
        }
    });
  }

  /*@ngInject*/
  static activate(ConfigurationService, $resource) {
      Locale.instance = new Locale(ConfigurationService, $resource);
    return Locale.instance;
  }
}


