export default class AssetStatSpaces {
    /*@ngInject*/
    constructor(ConfigurationService, $resource) {
        return $resource(ConfigurationService.getApiBaseUrl() + 'api/assets/:id/spaces', {id: '@id'}, {
            query: {
                method: 'GET',
                //url: ConfigurationService.getApiBaseUrl() + 'api/assets',
                isArray: true
            },
            search: {
                method: 'GET',
                url: ConfigurationService.getApiBaseUrl() + 'api/assets/:id/spaces/search'
            },
        });
    }

    /*@ngInject*/
    static activate(ConfigurationService, $resource) {
        AssetStatSpaces.instance = new AssetStatSpaces(ConfigurationService, $resource);
        return AssetStatSpaces.instance;
    }
}


