import ValidationEngine from 'devextreme/ui/validation_engine';
import Messages from '../../messages';
import ArrayStore from 'devextreme/data/array_store';
import CustomStore from 'devextreme/data/custom_store';
import FloorsAddRangeController from './floors-add-range-modal.controller.js';
import FloorsAddRangeTemplate from './floors-add-range-modal.template.html';
import Constants from '../../constants';
import DataSource from 'devextreme/data/data_source';

export default class FloorController {
    /*@ngInject*/
    constructor($scope, $state, Floor, AssetObject, NotificationService, UtilsService, sweetAlert, $uibModal) {
        this.$scope = $scope;
        this.$state = $state;
        this.Floor = Floor;
        this.AssetObject = AssetObject;
        this.NotificationService = NotificationService;
        this.utils = UtilsService;
        this.sweetAlert = sweetAlert;
        this.$uibModal = $uibModal;
        this.messages = Messages;

        this.loadData((t) => {
            this.init(); 
            this.initGrid();
        });
    }

    loadData(afterExecuteCallback){
        let self = this;

        self.loadAssetObjects(() => {
            self.loadFloors(() => {
                if(afterExecuteCallback)
                    afterExecuteCallback();  
            })
        });
    }

    loadAssetObjects(afterExecuteCallback){
        let self = this;

        self.AssetObject.query({ assetId: self.asset.id },
            (result) => {
                self.objects = result;

                if(afterExecuteCallback){
                    afterExecuteCallback();
                }
                
            },
            (error) => { });
    }

    loadFloors(afterExecuteCallback){
        let self = this;
        
        self.Floor.getByAssetId({ assetId: self.asset.id },
            (result) => {
                self.floors = result;

                if(afterExecuteCallback){
                    afterExecuteCallback();
                }
            },
            (error) => { }
        );
    }

    createStore(){
        let self = this;

        self.store = new ArrayStore({
            data: self.floors,
            key: "id",
            modified: function () {
                // 'modified' event handler
            },
            errorHandler: self.errorHandler.requestFailed
        });

        return self.store;
    }

    init(){
        let self = this;

        self.isFloorFormVisible = false;
        self.isAddFloorRangeDisabled = false;
        self.$scope.Messages = Messages;

        self.$scope.$on('updateAssetObjectDataSource', function(e,data) {
            self.updateDataSources();
        });

        self.updateDataSources = function(){
            self.loadData((t) => {
                var dataGrid = $("#gridContainer").dxDataGrid('instance');
                dataGrid.beginUpdate();
                dataGrid.columnOption('assetObjectId', 'lookup.dataSource', {store: self.assetObjectStore });
                dataGrid.option('dataSource', []);
                dataGrid.option('dataSource', {store: self.createStore()});
                dataGrid.endUpdate(); 

                self.refreshGrid();
            });
        }

        self.validateFloor = function (oldValue, newValue) {
            var floors = self.floors;

            if (!newValue || (newValue.number == '' && newValue.number != 0))
                return false;

            if(newValue.number == undefined)
                return true;

            for (var i = 0; i < floors.length; i++) {
                if (!oldValue || floors[i].number !== oldValue.number) {
                    if(newValue.number == floors[i].number && newValue.assetObjectId == floors[i].assetObjectId){
                        return false;
                    }
                }
            }

            return true;
        };

    }
    initGrid() {
        let self = this;

        self.errorHandler = {
            requestFailed: function (response) {
                self.NotificationService.error(response.data);
            }
        }

        self.createStore();

        self.assetObjectStore = new CustomStore({
            load: function (loadOptions) {
                return self.objects;
            },
            byKey: function(key) {
                return { id: key };
            }
        })

        self.gridSettings = $.extend(Constants.defaultGridSettings(), {
            dataSource: {store: self.store},
            groupPanel: {
                visible: false
            },
            filterRow: {
                visible: false
            },
            editing: {
                texts: {
                    confirmDeleteMessage: ''
                }
            },
            columns: [
                {
                    dataField: "number",
                    caption: Messages('assets.floors.number'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    allowSorting: false,
                    validationRules: [
                        { type: 'required', message: Messages('validation.required') },
                        { type: 'stringLength', max: 10 },
                        ],
                    width: 150,
                    alignment: 'right'
                },
                {
                    dataField: "description",
                    caption: Messages('assets.floors.description'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    allowSorting: false,
                    validationRules: [
                        { type: 'stringLength', max: 500 },
                        ]
                },
                {
                    dataField: 'assetObjectId',
                    caption: Messages('assets.floors.assetobject'),
                    width: 100,
                    allowSorting: false,
                    lookup: {
                        dataSource: {store: self.assetObjectStore},
                        displayExpr: 'name',
                        valueExpr: 'id',
                    },
                    alignment: 'right',
                    allowEditing: false
                },
                {
                    dataField: "order",
                    visible: false,
                    sortOrder: "asc" },

                {
                    dataField: "id",
                    caption: Messages('actions'),
                    width: 100,
                    allowSorting: false,
                    alignment: 'right',
                    cellTemplate: function (container, options) {
                        container.addClass("actions-cell");
                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-pencil"></div>',
                            onClick: function (e) {
                                console.log("editing...");
                                $('#gridContainer').dxDataGrid('instance').editRow(options.rowIndex);
                            }
                        }).appendTo(container);

                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-trash-o"></div>',
                            onClick: function (e) {
                                if (options.data.spaces && options.data.spaces.length > 0) {
                                    self.NotificationService.warning('Please remove spaces from the floor first');
                                }
                                else {
                                    self.sweetAlert.swal(
                                    {
                                        title: Messages('assets.floors.delete.title'),
                                        type: 'warning',
                                        showCancelButton: true,
                                        cancelButtonText: Messages('assets.cancel'),
                                        confirmButtonColor: '#DD6B55',
                                        confirmButtonText: Messages('assets.floors.delete.confirmButton'),
                                    },
                                    function (isConfirm) {
                                        if (isConfirm) {
                                            self.deleteFloor(options.data);
                                        }}
                                    );
                                }
                            }
                        }).appendTo(container);
                    },
                    editCellTemplate: function (container, options) {
                        container.addClass("actions-cell");
                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-floppy-o"></div>',
                            onClick: function(e) {
                                $('#gridContainer').dxDataGrid('instance').saveEditData(options.rowIndex);
                                if(self.updateFloor){
                                    self.updateFloor(options.data);
                                }
                            }
                        }).appendTo(container);
                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-times"></div>',
                            onClick: function(e) {
                                $('#gridContainer').dxDataGrid('instance').cancelEditData(options.rowIndex);
                            }
                        }).appendTo(container);
                    }
            }],
            onRowValidating: function (e) {
                var dataGrid = e.component;
                var rowIndex = dataGrid.getRowIndexByKey(e.key);

                if(!self.validateFloor(e.oldData, e.newData)){
                    dataGrid.getCellElement(rowIndex, 'number').find('input').css('background', 'rgba(255, 0, 0, 0.5)');
                    e.isValid = false;
                }

            },
        });

        self.refreshGrid = function() {
            $("#gridContainer").dxDataGrid('instance').refresh();
        }

        self.updateFloor = function(floor) {
            self.Floor.update({ assetId: self.asset.id, objectId: floor.assetObjectId, id: floor.id }, floor, 
            (result) => {
                self.NotificationService.success(Messages('assets.floors.updated'));
                self.saveInProgress = false;

                self.needUpdateDataSources();
            },
            (error) => {
                console.log(error);
                self.NotificationService.error(error.data);
                self.saveInProgress = false;
            }
            );
        }

        self.deleteFloor = function(floor){
            self.Floor.delete({ assetId: self.asset.id, assetObjectId: floor.assetObjectId, id: floor.id }, 
                (result) => {
                    var floorIdx = self.floors.findIndex(t => t.id == floor.id);
                    self.floors.splice(floorIdx, 1);

                    self.NotificationService.success(Messages('assets.floors.deleted'));
                    self.saveInProgress = false;

                    self.needUpdateDataSources();

                    self.refreshGrid();
                },
                (error) => {
                    console.log(error);
                    self.NotificationService.error(error.data);
                    self.saveInProgress = false;
                }
            );
        }

        self.addFloorRange = function(cnt, autoNumerate, assetObjectId) {
            self.Floor.createRange({ assetId: self.asset.id, assetObjectId: assetObjectId, count: cnt, autonumerate: autoNumerate }, 
                (result) => {
                    result.forEach((t)=>{
                        self.floors.push(t);
                    });

                    self.NotificationService.success(Messages('assets.floors.created'));
                    self.saveInProgress = false;

                    self.needUpdateDataSources();

                    self.refreshGrid();
                },
                (error) => {
                    console.log(error);
                    self.NotificationService.error(error.data);
                    self.saveInProgress = false;
                }
            );
        };

        self.temp = true;
    };

    addFloorsRange() {
        let self = this;

        let modal = this.$uibModal.open({
            controller: FloorsAddRangeController,
            template: FloorsAddRangeTemplate,
            resolve: {
                asset: self.asset
            }
        });
        modal.result.then(result => {
            self.addFloorRange(result.addRangeFloorCount, result.autoNumerateFloors, result.assetObjectId);
        });
    }

}


