import ValidationEngine from 'devextreme/ui/validation_engine';
import Messages from '../../messages';
import ArrayStore from 'devextreme/data/array_store';
import Constants from '../../constants';
import AssetObjectEditModalController from './asset-object-edit-modal.controller';
import AssetObjectEditModalTemplate from './asset-object-edit-modal.template.html';


export default class AssetObjectController {
    /*@ngInject*/
    constructor($scope, $state, AssetObject, NotificationService, UtilsService, sweetAlert, $uibModal) {
        this.$scope = $scope;
        this.$state = $state;
        this.AssetObject = AssetObject;
        this.NotificationService = NotificationService;
        this.utils = UtilsService;
        this.sweetAlert = sweetAlert;
        this.$uibModal = $uibModal;

        this.messages = Messages;

        this.init();
    }

   
    init(){
        let self = this;

        self.AssetObject.query({ assetId: self.asset.id },
            (result) => {
                self.objects = result;

                for (var i = 0; i < self.objects.length; i++){
                    if(!self.objects[i].uniqueId) {
                        self.objects[i].uniqueId = self.utils.generateGuid();
                    }
                    self.objects[i].address = self.utils.generateAddress(self.objects[i]);
                }

                self.initGrid();
            },
            (error) => { });
    }

    initGrid() {
        let self = this;

        self.errorHandler = {
            requestFailed: function (response) {
                this.NotificationService.error(response.data);
            }
        }

        self.store = new ArrayStore({
            data: self.objects,
            key: "uniqueId",
            modified: function () {
                // 'modified' event handler
            },
            errorHandler: self.errorHandler.requestFailed
        });

        self.gridDataSource = { store: self.store };

        self.gridSettings = $.extend(Constants.defaultGridSettings(), {
            dataSource: self.gridDataSource,
            groupPanel: {
                visible: false
            },
            filterRow: {
                visible: false
            },
            columns: [
                {
                    dataField: "name",
                    caption: Messages('assets.objects.name'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    allowSorting: false,
                    width: 150,
                    alignment: 'right'
                },
                {
                    dataField: "address",
                    caption: Messages('assets.objects.address'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    allowSorting: false,
                },
                {
                    dataField: "isDisplayOnStackingPlan",
                    caption: Messages('assets.objects.isDisplayOnStackingPlan'),
                    filterOperations: ['contains', 'startswith', '='],
                    selectedFilterOperation: 'contains',
                    allowSorting: false,
                    dataType: 'boolean'
                },
                {
                    dataField: "id",
                    visible: false,
                },
                {
                    dataField: "uniqueId",
                    caption: Messages('actions'),
                    width: 100,
                    allowSorting: false,
                    alignment: 'right',
                    cellTemplate: function (container, options) {
                        container.addClass("actions-cell");
                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-pencil"></div>',
                            onClick: function (e) {
                                self.editAssetObject(options.data.id);
                            }
                        }).appendTo(container);
                        
                        $("<div />").dxButton({
                            type: 'custom',
                            template: '<div class="fa fa-trash-o"></div>',
                            onClick: function (e) {
                                self.sweetAlert.swal(
                                    {
                                        title: Messages('assets.objects.delete.title'),
                                        type: 'warning',
                                        showCancelButton: true,
                                        cancelButtonText: Messages('assets.cancel'),
                                        confirmButtonColor: '#DD6B55',
                                        confirmButtonText: Messages('assets.objects.delete.confirmButton'),
                                    },
                                    function (isConfirm) {
                                        if (isConfirm) {
                                            self.deleteAssetObject(options.data);
                                        }}
                                    );
                            }
                        }).appendTo(container);
                        
                    },
            }],
        });

        self.refreshGrid = function() {
            $("#objectsGridContainer").dxDataGrid('instance').refresh();
        }

        self.deleteAssetObject = function(assetObject){
            self.AssetObject.archive({ assetId: self.asset.id, id: assetObject.id }, 
            (result) => {
                var assetObjectIdx = self.objects.findIndex(t => t.id == assetObject.id);
                self.objects.splice(assetObjectIdx, 1);

                self.NotificationService.success(Messages('assets.objects.message.deleted'));
                self.saveInProgress = false;

                self.needUpdateDataSources();
                self.refreshData();
            },
            (error) => {
                console.log(error);
                self.NotificationService.error(error.data);
                self.saveInProgress = false;
            }
            );
        }
    };

    addAssetObject() {
        let self = this;


        let modal = this.$uibModal.open({
            controller: AssetObjectEditModalController,
            template: AssetObjectEditModalTemplate,
            resolve: {
                towerObject: null,
                assetId: self.asset.id
            }
        });
        modal.result.then(result => {
            self.needUpdateDataSources();
            self.refreshData();
        });
    }

    editAssetObject(objectId) {
        let self = this;

        this.AssetObject.getById(
                {id: objectId, assetId: self.asset.id},
                result => {
                    let modal = this.$uibModal.open({
                        controller: AssetObjectEditModalController,
                        template: AssetObjectEditModalTemplate,
                        resolve: {
                            towerObject: result,
                            assetId: self.asset.id
                        }
                    });
                    modal.result.then(result => {
                        self.needUpdateDataSources();
                        self.refreshData();
                    });
                },
                error => {
                    this.notificationService.error(error.data.message ? error.data.message : error.data);
                }
            );

    }

    refreshData(){
        let self = this;

        self.AssetObject.query({ assetId: self.asset.id },
            (result) => {
                self.objects = result;

                for (var i = 0; i < self.objects.length; i++){
                    if(!self.objects[i].uniqueId) {
                        self.objects[i].uniqueId = self.utils.generateGuid();
                    }
                    self.objects[i].address = self.utils.generateAddress(self.objects[i]);
                }

                self.store = new ArrayStore({
                    data: self.objects,
                    key: "uniqueId",
                    modified: function () {
                    },
                    errorHandler: self.errorHandler.requestFailed
                });
                let dataGrid = $("#objectsGridContainer").dxDataGrid('instance');
                dataGrid.beginUpdate();
                dataGrid.option('dataSource', []);
                dataGrid.option('dataSource', {store: self.store});
                dataGrid.endUpdate(); 

                self.refreshGrid();
            },
            (error) => { });
    }
}


