// application styles
import './vendor.styles';
import '../styles/style.less';

import 'jquery';
import 'jquery-ui';
import 'bootstrap';

import angular from 'angular';
import angularsanitize from 'angular-sanitize';
import 'angular-animate';
import 'angular-local-storage';
import uirouter from 'angular-ui-router';
import angularresource from 'angular-resource/index.js';
import ActivityMonitor from 'angular-activity-monitor';
import 'angularjs-toaster';
import 'angular-xeditable';
import 'angular-ui-bootstrap';
import 'moment';
import 'angular-moment';
import 'ngmap';
import 'sweetalert';
import 'ladda';
import 'angular-ladda';
import 'ui-select';
import 'bootstrap-daterangepicker';
import ngInfiniteScroll from 'ng-infinite-scroll';

import 'devextreme/integration/angular';
import 'devextreme/ui/data_grid';
import 'devextreme/ui/button';

import routing from './app.config';
import interceptors from './app.interceptors';
import run from './app.run';
import activityMonitor from './app.activitymonitor';

// common
import Services from './services/services';
import Components from './components/components';
import './services/storeadapter.service'

// common directives
import PanelToolsDirective from './components/panels/paneltools.directive';
import AnimatePanelDirective from './components/panels/animate.directive';
import SweetAlert from './components/alerts/sweet-alert.factory';

// business modules
import Home from './home/home';
import Login from './login/login';
import Brokers from './brokers/brokers';
import Users from './users/users';
import Assets from './assets/assets';
import AssetStats from './asset-stats/asset-stats';
import Clients from './clients/clients';
import Registrations from './registration/registration';

import 'ng-onload/release/ng-onload.min';

let app = angular.module('tower360', [
    uirouter,
    'dx',
    'LocalStorageModule',
    angularresource,
    'ActivityMonitor',
    'ngAnimate',
    'ngOnload',
    'xeditable',
    'ui.bootstrap',
    'ngMap',
    'angularMoment',
    'angular-ladda',
    'ui.select',
    'ngSanitize',
    ngInfiniteScroll,
    // common
    Services.name,
    Components.name,
    'tower360.utils', // this should be converted to ES6 style

    // business
    Home.name,
    Login.name,
    Brokers.name,
    Users.name,
    Assets.name,
    AssetStats.name,
    Clients.name,
    Registrations.name
]);

app.config(routing);
app.config(interceptors);
app.run(run);
app.run(activityMonitor);
app.run(['editableOptions', function (editableOptions) {
    editableOptions.theme = 'bs3';
}]);

app.directive('panelTools', PanelToolsDirective.activate)
    .directive('animatePanel', AnimatePanelDirective.activate)
    .factory('sweetAlert', SweetAlert);

app.filter('shortNumber', () => {
    return (number) => {
        if (number) {
            let abs = Math.abs(number);
            if (abs >= Math.pow(10, 12)) {
                // trillion
                number = (number / Math.pow(10, 12)).toFixed(1) + "T";
            } else if (abs < Math.pow(10, 12) && abs >= Math.pow(10, 9)) {
                // billion
                number = (number / Math.pow(10, 9)).toFixed(1) + "B";
            } else if (abs < Math.pow(10, 9) && abs >= Math.pow(10, 6)) {
                // million
                number = (number / Math.pow(10, 6)).toFixed(1) + "M";
            } else if (abs < Math.pow(10, 6) && abs >= Math.pow(10, 3)) {
                // thousand
                number = (number / Math.pow(10, 3)).toFixed(1) + "K";
            }
            return number;
        }
    }
});

app.constant('Activities', {
    HomeView: '390E3425-970F-4DDF-9ED1-CC39B1F30E50',
    UsersView: '7A1781A1-E26A-4993-8BC5-09EFAF9D5DE8',
    BrokersView: '58B4A88A-6728-4DA3-9F3D-0546ABDB4560',
    BrokerManage: '934871FF-A6DE-4B2C-B863-A378A4A414C8',
    AssetsView: 'D8D389D1-F92A-40A8-ABE5-CC3639D3CFD6',
    AssetsManage: 'AD46C0DF-7009-4EA3-91F6-01C4133FD38C',
    ClientsManage: 'D00F82C9-A412-4010-833A-F78F0DFD10DD',
});


