// import template and controller from individual component directory
import Controller from './imagecropper.controller'
import Template from './imagecropper.template.html'

let ImageCropperComponent = {
    bindings: {
        maxImageHeight: '=',
        maxImageWidth: '=',
        viewPortType: '=?'
    },
    controller: Controller,
    template: Template
};

export default ImageCropperComponent;


