import AssetsController from './assets.controller';
import AssetController from './asset.controller';
import AssetGallaryComponent from './assetgallary.component';
import FloorComponent from './floors/floor.component';
import SpaceComponent from './spaces/space.component';
import SpacesComponent from './spaces/spaces.component';
import AssetDocumentsComponent from './assetdocuments.component';
import ObjectComponent from './assetObjects/object.component';
import Asset from './asset.resource';
import AssetObject from './assetObjects/object.resource';
import Floor from './floors/floor.resource';
import Space from './spaces/space.resource';
import 'angular-ui-sortable';
import 'angular-sortable-view';

var module = angular.module('tower360.assets',['ui.sortable','angular-sortable-view'])
    .controller('AssetsController', AssetsController)
    .controller('AssetController', AssetController)
    .component('assetgallery', AssetGallaryComponent)
    .component('floor', FloorComponent)
    .component('space', SpaceComponent)
    .component('spaces', SpacesComponent)
    .component('assetdocuments', AssetDocumentsComponent)
    .factory('Asset', Asset.activate)
    .factory('AssetObject', AssetObject.activate)
    .factory('Floor', Floor.activate)
    .factory('Space', Space.activate)
    .component('object', ObjectComponent)

export default module;


