import en_us_locale from './messages_en-us';
import de_ge_locale from './messages_de-ge';
import { LocaleId, updateLocale } from './app.localization';

/*@ngInject*/
export default function Messages(messageId){
    var locale =  en_us_locale;
    var localeId = LocaleId;
    if(localeId === 2) {
        locale = de_ge_locale;
    }

    return locale(messageId);
}




