export default class AssetStatStackingPlan {
    /*@ngInject*/
    constructor(ConfigurationService, $resource) {
        return $resource(ConfigurationService.getApiBaseUrl() + 'api/assets/:asset/objects', {asset: '@asset', object: '@object'}, {
            getObjects: {
                method: 'GET',
                isArray: true
            },
            getObjectData: {
                method: 'GET',
                url: ConfigurationService.getApiBaseUrl() + 'api/assets/:asset/objects/:object/plan'
            },
        });
    }

    /*@ngInject*/
    static activate(ConfigurationService, $resource) {
        AssetStatStackingPlan.instance = new AssetStatStackingPlan(ConfigurationService, $resource);
        return AssetStatStackingPlan.instance;
    }
}


