import ValidationEngine from 'devextreme/ui/validation_engine';
import 'devextreme/ui/text_area';
import Messages from '../../messages';
import ArrayStore from 'devextreme/data/array_store';

export default class FloorsAddRangeModalController {
    /*@ngInject*/
    constructor($scope, asset, Asset, AssetObject, $uibModalInstance, NotificationService) {
        this.$scope = $scope;
        this.asset = asset;
        this.Asset = Asset;
        this.AssetObject = AssetObject;
        this.$uibModalInstance = $uibModalInstance;
        this.notificationService = NotificationService;
        this.validationGroup = 'floors-add-range';

        this.init();
        this.initDataSource();
    }

    init() {
        var self = this;

        self.$scope.floorRequest = { addRangeFloorCount: 1, autoNumerateFloors: false, assetObjectId: null};

        self.$scope.getDataInProgress = false;

        self.errorHandler = {
            requestFailed: function (response) {
                self.NotificationService.error(response.data);
            }
        };

        self.$scope.cancel = function () {
            ValidationEngine.removeGroup(self.validationGroup);
            self.$uibModalInstance.dismiss('cancel');
        };
    }

    initDataSource(){
        var self = this;
        self.$scope.getDataInProgress = true;
        self.AssetObject.query({ assetId: self.asset.id },
            (result) => {
                self.objects = result;
                self.assetObjectStore = new ArrayStore({
                    data: self.objects,
                    key: "id",
                    errorHandler: self.errorHandler.requestFailed
                });
                self.$scope.getDataInProgress = false;
                self.initForm();
            },
            (error) => { });
    }

    initForm() {
        var self = this;

        self.$scope.floorRangeSettings = {
            showSpinButtons: true,
            showClearButton: false,
            onValueChanged: function(e) {
                let result = ValidationEngine.getGroupConfig(self.validationGroup).validate();
                self.$scope.isAddFloorRangeDisabled = !result.isValid;
            },
            bindingOptions:{
                value: 'floorRequest.addRangeFloorCount'
            }
        };
        self.$scope.floorRangeValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'numeric' },
                { type: 'range', min: 1, max: 100 }
            ],
            validationGroup: self.validationGroup
        };

        self.$scope.autoNumerateFloorSettings = {
            text: '',
            type: 'default',
            bindingOptions: {
                value: 'floorRequest.autoNumerateFloors'
            }
        };

        self.$scope.assetObjectSelectSettings = {
            dataSource: self.assetObjectStore,
            displayExpr: 'name',
            valueExpr: 'id',
            bindingOptions: {
                value: 'floorRequest.assetObjectId',
            }
        };

        self.$scope.assetObjectValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') }
            ],
            validationGroup: self.validationGroup
        };

        self.$scope.validateAndSaveSettings = function() {
            let result = ValidationEngine.getGroupConfig(self.validationGroup).validate();
            if (result.isValid) {
                self.$scope.saveInProgress = true;
                self.$scope.isSaveOperationDisable = true;

                self.$uibModalInstance.close({
                    addRangeFloorCount: self.$scope.floorRequest.addRangeFloorCount,
                    autoNumerateFloors: self.$scope.floorRequest.autoNumerateFloors,
                    assetObjectId: self.$scope.floorRequest.assetObjectId
                });
            }
        };
    }
}



