import ValidationEngine from 'devextreme/ui/validation_engine';
import Messages from '../messages';

export default class RegistrationController {
    /*@ngInject*/
    constructor($scope, $state, $stateParams, Registration, registrationObject, NotificationService, SecurityService) {
        this.$scope = $scope;
        this.$state = $state;
        this.Registration = Registration;
        this.registrationObject = registrationObject;
        this.notificationService = NotificationService;
        this.$stateParams = $stateParams;
        this.securityService = SecurityService;

        this.init();
        this.initForm();

        
    }

    init() {
        let self = this;

        self.validate = function(){
            let validateGroup = ValidationEngine.getGroupConfig('settings');
            let result = validateGroup.validate();
            return result.isValid;
        }

        self.comprasionTarget = function() {
            if (self.registrationObject.password)
                return self.registrationObject.password;
        }
    }

    initForm() {
        let self = this;

        self.errorHandler = {
            requestFailed: function (response) {
                this.NotificationService.error(response.data);
            }
        }

        self.nameSettings = { bindingOptions: { value: '$ctrl.registrationObject.email' }, disabled: true };
        self.nameValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
            ],
            validationGroup: 'settings'
        }

        self.passwordSettings = { bindingOptions: { value: '$ctrl.registrationObject.password' }, mode: 'password' };
        self.passwordValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'stringLength', min:6, message :Messages('validation.shortPassword')}
            ],
            validationGroup: 'settings'
        }

        self.confirmPasswordSettings = { bindingOptions: { value: '$ctrl.registrationObject.confirmPassword' }, mode: 'password'};
        self.confirmPasswordValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
                { type: 'compare',  comparisonTarget: self.comprasionTarget, message: Messages('validation.passwordsNotMatch') }
            ],
            validationGroup: 'settings'
        }
        self.displayNameSettings = { bindingOptions: { value: '$ctrl.registrationObject.displayName' } };
        self.displayNameValidationRules = {
            validationRules: [
                { type: 'required', message: Messages('validation.required') },
            ],
            validationGroup: 'settings'
        }

        self.saveSettingsBtnConfig = {
            text: Messages('registration.submit'),
            type: 'default',
            onClick: self.submit,
            template: 'saveButtonWithLoadIndicator',
            bindingOptions: {
                disabled: '$ctrl.isSaveOperationDisabled || $ctrl.saveInProgress'
            }
        };

        self.cancelSettingsBtnConfig = {
            text: Messages('registration.cancel'),
            type: 'normal',
            onClick: function () {
                self.$state.go('home');
            }
        };

        self.$scope.$watch('$ctrl.registrationObject', function(newValue, oldValue) {
            self.isSaveOperationDisabled = !self.validate();
        }, true);

    }

    submit(){
        let self = this.$ctrl;

        let result = ValidationEngine.getGroupConfig('settings').validate();
        if (result.isValid) {
            self.saveInProgress = true;
            //self.client.brokerId = self.currentUser.brokerId;

            self.Registration.confirm({token: self.$stateParams.token}, self.registrationObject,
                    (result) => {
                        self.notificationService.success(Messages('clients.message.created'));
                        self.saveInProgress = false;
                        self.signIn(self.registrationObject.email, self.registrationObject.password);
                    },
                    (error) => {
                        self.notificationService.error(error.data);
                        self.saveInProgress = false;
                    }
                );
        }
    }

    signIn(login, password) {
        let self = this.$ctrl;
        
        this.securityService.signIn(login, password)
        .then(
            (response) => {
                this.securityService.reset(); 
                this.$state.go('home');
            },
            (err) => { this.error = err.data.error_description; }
        );
    }
}


