export default class FileService {
    constructor() {
        this.init();
    }

    init(){
        let self = this;

        self.readSingleFile = function(file, readCompleted) {
            var reader = new FileReader();
            reader.onload = (function(f) {
                return function(evt) {
                    var uploadedFile = {
                        filename: f.name,
                        extension: f.name.split('.').pop().toLowerCase(), //extension: f.name.match(/\.[0-9a-z]+$/i);
                        size: f.size,
                        data: evt.target.result
                    };

                    if(readCompleted){
                        readCompleted(uploadedFile);
                    }
                }

            })(file);
            reader.readAsDataURL(file);
        }
    }

    uploadFiles(sourceFileArray, singleFileReadCallBack, readCompletedCallback){

        let self = this;

        var filesCount = sourceFileArray.length;
        var readedFilesCount = 0;

        var  destFileArray = [];

        var readFile = function(fileIndex){
            var file = sourceFileArray[fileIndex]
            self.readSingleFile(file,function (e) {
                if(singleFileReadCallBack)
                    singleFileReadCallBack(e);

                destFileArray.push(e);
                readedFilesCount++;

                if(readedFilesCount < filesCount){
                    readFile(readedFilesCount);
                }
                else{
                    if(readCompletedCallback)
                        readCompletedCallback(destFileArray);
                }

            })
        }

        if(filesCount > 0)
            readFile(0);

    }
}



